export const sourceCode = "---\r\ntitle: \"Gerund as Subject: 'Making a good result in the exam needs...'\"\r\ndescription: \"'Making a good result in the exam needs a lot of hard work.' - এই বাক্যে 'making' শব্দটি Gerund কেন? Gerund Phrase as a Subject এর ব্যবহার শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"gerund as subject\",\r\n    \"gerund phrase\",\r\n    \"making a good result\",\r\n    \"gerund vs participle\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n    \"verbal noun\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: Making a good result in the exam needs a lot of hard work. Here ‘making’ is a —\r\n\r\n**Question:** Making a good result in the exam needs a lot of hard work. Here ‘making’ is a —\r\n(a) present participle\r\n(b) pronoun\r\n(c) adjective\r\n(d) gerund\r\n\r\n**Correct Answer:** (d) gerund\r\n\r\n### কেন 'making' একটি Gerund? (Why is 'making' a Gerund?)\r\n\r\nএই প্রশ্নের উত্তরটি বাক্যে **Gerund Phrase**-কে subject হিসেবে সনাক্ত করার উপর নির্ভরশীল।\r\n\r\n**Key Concept: Gerund as the Subject of a Sentence**\r\n\r\n1.  **Gerund কী?** Gerund হলো `verb+ing` যুক্ত এমন একটি শব্দ যা বাক্যে **Noun**-এর কাজ করে।\r\n2.  **Gerund Phrase:** একটি Gerund এবং তার সাথে সম্পর্কিত অন্যান্য শব্দ (যেমন object, modifier) মিলে একটি Gerund Phrase তৈরি করে। এই পুরো phrase-টি একটি Noun-এর মতোই কাজ করে।\r\n3.  **Function as Subject:** Gerund বা Gerund Phrase একটি বাক্যের verb-এর subject (কর্তা) হিসেবে বসতে পারে।\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- প্রথমে বাক্যের main verb-টি খুঁজে বের করুন। Main verb হলো `needs`।\r\n- এবার verb-কে \"What?\" বা 'কী?' দিয়ে প্রশ্ন করুন: \"**What** needs a lot of hard work?\" (কিসের জন্য অনেক কঠোর পরিশ্রম প্রয়োজন?)\r\n- উত্তর আসে: \"**Making a good result in the exam**\" (পরীক্ষায় ভালো ফল করা)।\r\n\r\nযেহেতু \"Making a good result in the exam\" এই সম্পূর্ণ phrase-টি `needs` verb-টির subject হিসেবে কাজ করছে, এটি একটি Noun Phrase। আর এই phrase-টির মূল হলো `making`। যখন কোনো `verb+ing` শব্দ একটি noun phrase-এর প্রধান হিসেবে subject-এর কাজ করে, তখন তাকে **Gerund** বলে।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** যদি কোনো `verb+ing` দিয়ে শুরু হওয়া phrase-কে `It` বা\r\n  `Something` দিয়ে প্রতিস্থাপন করার পরও বাক্যটি অর্থপূর্ণ থাকে, তাহলে ঐ\r\n  `verb+ing` শব্দটি একটি Gerund। - **Making a good result in the exam** needs a\r\n  lot of hard work. - -> **It** needs a lot of hard work. (বাক্যটি সঠিক)।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) present participle:** Present Participle একটি Adjective-এর মতো কাজ করে কোনো Noun বা Pronoun-কে modify করে। এখানে `making` কোনো Noun/Pronoun-কে modify করছে না, বরং নিজেই subject-এর কাজ করছে।\r\n- **(b) pronoun:** Pronoun (e.g., he, it, they) কোনো Noun-এর পরিবর্তে বসে। `making` কোনো কিছুর পরিবর্তে বসেনি।\r\n- **(c) adjective:** Adjective কোনো Noun-কে বিশেষায়িত করে। `making` এখানে তা করছে না।\r\n\r\n### আরও উদাহরণ\r\n\r\n- **Swimming in the ocean** has been my passion. (Subject)\r\n- **Writing a novel** takes a long time. (Subject)\r\n- I enjoy **reading books**. (Object)";
