export const sourceCode = "---\r\ntitle: \"Passive Voice with 'need': 'Phosphates need to be added'\"\r\ndescription: \"'Phosphates ___ to most farm lands in Bangladesh.' - কেন 'need to be added' সঠিক? 'need' যুক্ত বাক্যের Passive Voice গঠনের নিয়ম সহজ বাংলায় শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"passive voice with need\",\r\n    \"need to be added\",\r\n    \"passive infinitive\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n    \"verb need\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: Phosphates \\_\\_\\_ to most farm lands in Bangladesh.\r\n\r\n**Question:** Phosphates \\_\\_\\_ to most farm lands in Bangladesh.\r\n(a) need to be adding\r\n(b) need to be added\r\n(c) need added\r\n(d) need to add\r\n\r\n**Correct Answer:** (b) need to be added\r\n\r\n### কেন 'need to be added' সঠিক উত্তর?\r\n\r\nএই প্রশ্নের উত্তরটি `need` verb-টির **Passive Voice**-এর গঠন বোঝার উপর নির্ভরশীল।\r\n\r\n**Key Concepts:**\r\n\r\n1.  **Active vs. Passive Voice:**\r\n\r\n    - **Active Voice:** Subject নিজে কাজটি করে। (e.g., Farmers **add** phosphates.)\r\n    - **Passive Voice:** Subject-এর উপর কাজটি করা হয়। (e.g., Phosphates **are added** by farmers.)\r\n\r\n2.  **Passive with `need`:**\r\n    `need` verb-টির পরে যখন কোনো কাজ করা প্রয়োজন বোঝায় এবং বাক্যের subject সেই কাজটি নিজে করতে পারে না, তখন passive form ব্যবহৃত হয়। এর দুটি সাধারণ গঠন আছে:\r\n    - **Structure 1:** Subject + `need` + **to be + Past Participle (V3)**\r\n    - **Structure 2:** Subject + `need` + **Gerund (verb+ing)**\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- **Subject:** `Phosphates` (ফসফেট)\r\n- **Action:** `add` (যোগ করা)\r\n\r\nফসফেট কি নিজে নিজেকে যোগ করতে পারে? না। ফসফেটকে জমিতে যোগ করা হয় (সাধারণত কৃষকদের দ্বারা)। যেহেতু subject (`Phosphates`) কাজটি নিজে করছে না, বরং কাজটি তার উপর করা হচ্ছে, তাই বাক্যটি অবশ্যই **Passive Voice**-এ হবে।\r\n\r\nএবার option-গুলো দেখি:\r\n\r\n- **(b) need to be added:**\r\n  - `need` + `to be` + `added` (V3)\r\n  - এই গঠনটি `need`-এর passive voice-এর Structure 1-এর সাথে হুবহু মিলে যায়। তাই এটি সঠিক।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** Subject যদি কোনো জড় বস্তু হয় এবং verb-টি যদি একটি কাজ হয়\r\n  যা সেই বস্তু নিজে করতে পারে না, তাহলে passive form (`need to be + V3` or `need\r\n  + V-ing`) ব্যবহার করতে হবে।\r\n</Callout>\r\n\r\n### বিকল্প গঠন (Alternative Structure)\r\n\r\nএই বাক্যটি `need + gerund` গঠন ব্যবহার করেও লেখা যেত:\r\n\r\n- Phosphates **need adding** to most farm lands in Bangladesh.\r\n  এই গঠনটিও শুদ্ধ এবং এর অর্থ passive। যেহেতু `need adding` option-এ নেই, তাই `need to be added` একমাত্র সঠিক উত্তর।\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) need to be adding:** `to be + verb-ing` (continuous infinitive) এখানে ভুল। Passive বোঝানোর জন্য `to be + V3` প্রয়োজন।\r\n- **(c) need added:** `need`-এর পরে সরাসরি Past Participle (V3) বসে না।\r\n- **(d) need to add:** এটি Active Voice-এর গঠন (`to` + V1)। এর অর্থ হবে \"ফসফেটের যোগ করা প্রয়োজন,\" যেন ফসফেট নিজেই কাজটি করবে। এটি যৌক্তিকভাবে ভুল।\r\n\r\nসঠিক বাক্যটির অর্থ: বাংলাদেশের বেশিরভাগ কৃষি জমিতে ফসফেট যোগ করা প্রয়োজন।";
