export const sourceCode = "---\r\ntitle: \"Relative Pronoun 'whose' এর ব্যবহার: ...whose windows were never opened\"\r\ndescription: \"The children studied in a class room ____ windows were never opened - শূন্যস্থানে that, which, where, whose কোনটি বসবে? Possessive Relative Pronoun 'whose' এর ব্যবহার শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"relative pronoun whose\",\r\n    \"whose for things\",\r\n    \"possessive relative pronoun\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n    \"whose windows\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: The children studied in a class room \\_\\_\\_\\_ windows were never opened.\r\n\r\n**Question:** The children studied in a class room \\_\\_\\_\\_ windows were never opened.\r\n(a) that\r\n(b) which\r\n(c) where\r\n(d) whose\r\n\r\n**Correct Answer:** (d) whose\r\n\r\n### কেন 'whose' সঠিক উত্তর? (Why is 'whose' correct?)\r\n\r\nএই প্রশ্নের উত্তরটি **Possessive Relative Pronoun `whose`**-এর সঠিক ব্যবহারের উপর নির্ভরশীল।\r\n\r\n**Key Concept: `whose` for Possession**\r\n\r\n1.  **Relative Pronoun:** Relative Pronoun দুটি বাক্যকে যুক্ত করে এবং তার পূর্ববর্তী Noun বা Pronoun (Antecedent)-কে নির্দেশ করে। এই বাক্যে Antecedent হলো `a class room`।\r\n\r\n2.  **`whose` এর কাজ:** `whose` একটি Possessive Relative Pronoun। এটি ব্যক্তি (person) এবং বস্তু (thing) উভয়ের ক্ষেত্রে **মালিকানা বা অধিকার (possession)** বোঝাতে ব্যবহৃত হয়। এর অর্থ হলো 'যার' বা 'যাদের'।\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- **Antecedent:** `a class room` (একটি শ্রেণীকক্ষ)\r\n- **Related Noun:** `windows` (জানালা)\r\n\r\nএখানে 'শ্রেণীকক্ষের জানালা' (windows of the class room) বোঝানো হচ্ছে। অর্থাৎ, জানালাগুলোর মালিক বা অংশ হলো শ্রেণীকক্ষ। এই মালিকানার সম্পর্কটি প্রকাশ করার জন্য `whose` ব্যবহার করা হয়।\r\n\r\n**Structure:** Antecedent + `whose` + Noun\r\n\r\n- `a class room` + `whose` + `windows` = একটি শ্রেণীকক্ষ যার জানালা...\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** যদি শূন্যস্থানের পরে একটি Noun থাকে এবং সেই Noun-টি\r\n  শূন্যস্থানের আগের Noun-টির 'অংশ' বা 'সম্পত্তি' বোঝায়, তবে `whose` বসে। যেমন:\r\n  The man **whose car** was stolen... (লোকটি যার গাড়ি...), The house **whose\r\n  roof** is red... (বাড়িটি যার ছাদ...)\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) that / (b) which:** `that` এবং `which` বস্তু বা প্রাণীর পরিবর্তে বসে, কিন্তু মালিকানা বোঝায় না। যদি বাক্যটি হতো \"The class room **which was** large...\" তাহলে `which` সঠিক হতো। কিন্তু এখানে `which windows` বা `that windows` কোনো অর্থ তৈরি করে না।\r\n\r\n- **(c) where:** `where` একটি Relative Adverb যা **স্থান (place)** বোঝাতে ব্যবহৃত হয়। এটি 'in which' বা 'at which' এর সমতুল্য। যদি বাক্যটি হতো \"The class room **where** the children studied...\" (শ্রেণীকক্ষটি যেখানে শিশুরা পড়ত...) তাহলে `where` সঠিক হতো। কিন্তু এখানে 'শ্রেণীকক্ষের ভেতরে' কোনো কাজের কথা বলা হচ্ছে না, বরং 'শ্রেণীকক্ষের জানালা'-র কথা বলা হচ্ছে।\r\n\r\n**Important Note:** অনেকেই মনে করেন `whose` শুধু ব্যক্তির জন্য ব্যবহৃত হয়, কিন্তু এটি বস্তুর মালিকানা বোঝাতেও বহুল ব্যবহৃত এবং সম্পূর্ণ শুদ্ধ।";
