export const sourceCode = "---\r\ntitle: \"Causative Verb 'Make': 'The invigilator made us show our identity card'\"\r\ndescription: \"'The invigilator made us ___ our identity card' - কেন 'show' হলো? Causative Verb (make, let, have) এর পর Bare Infinitive ব্যবহারের নিয়মটি শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"causative verb make\",\r\n    \"bare infinitive\",\r\n    \"made us show\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n    \"causative verbs rules\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: The invigilator made us \\_\\_\\_ our identity card at the test center.\r\n\r\n**Question:** The invigilator made us \\_\\_\\_ our identity card at the test center.\r\n(a) to show\r\n(b) showing\r\n(c) show\r\n(d) showed\r\n\r\n**Correct Answer:** (c) show\r\n\r\n### কেন 'show' সঠিক উত্তর? (Why is 'show' correct?)\r\n\r\nএই প্রশ্নের উত্তরটি **Causative Verbs** (প্রযোজক ক্রিয়া) এর নিয়মের উপর ভিত্তি করে।\r\n\r\n**Key Concept: Causative Verbs (`make`, `let`, `have`) + Bare Infinitive**\r\n\r\n1.  **Causative Verb কী?** যখন কোনো কর্তা নিজে কাজটি না করে অন্যকে দিয়ে কাজটি করায়, তখন যে verb ব্যবহৃত হয় তাকে Causative Verb বলে। `Make`, `let`, `have`, `get` ইত্যাদি হলো Causative Verb।\r\n\r\n2.  **The Rule for `make`, `let`, `have`:** Active voice-এ `make`, `let`, এবং `have` এর পরে যদি কোনো object (যাকে দিয়ে কাজ করানো হচ্ছে) থাকে, তাহলে পরবর্তী verb-টি **Bare Infinitive** হয়। Bare Infinitive হলো verb-এর মূল রূপ (base form) যার আগে `to` বসে না।\r\n\r\n**Structure:** Subject + `make`/`let`/`have` + Object + **Bare Infinitive (V1)**\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- **Subject:** `The invigilator`\r\n- **Causative Verb:** `made`\r\n- **Object:** `us` (আমাদেরকে)\r\n- **Action Verb:** `___` (কাজটি হলো 'দেখানো')\r\n\r\nনিয়ম অনুযায়ী, `made` + `us` (object) এর পরে verb-এর bare infinitive `show` বসবে।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** বাক্যে যদি `make`, `let`, বা `have` কাউকে দিয়ে কিছু\r\n  'করানো' বা 'করতে দেওয়া' অর্থে ব্যবহৃত হয়, তাহলে পরের verb-টি হবে একদম simple\r\n  form (V1) — কোনো `to`, `ing`, বা `ed` যুক্ত হবে না।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) to show:** `make`-এর পরে active voice-এ `to`-infinitive বসে না। (ব্যতিক্রম: passive voice-এ `to` বসে। e.g., We were **made to show** our cards.)\r\n- **(b) showing:** `ing` যুক্ত form (participle) এখানে ব্যবহৃত হবে না।\r\n- **(d) showed:** Past tense form (`V2`) এখানে ব্যবহৃত হবে না। Causative verb-এর পরে সবসময় base form বসে।\r\n\r\n### আরও উদাহরণ (More Examples)\r\n\r\n- My teacher **made** me **rewrite** the essay. (Not `to rewrite`)\r\n- She won't **let** me **go** to the party. (Not `to go`)\r\n- I will **have** my mechanic **check** the car. (Not `to check`)\r\n\r\nএই নিয়মটি প্রতিযোগিতামূলক পরীক্ষার জন্য খুবই গুরুত্বপূর্ণ।";
