export const sourceCode = "---\r\ntitle: \"Relative Pronoun 'all of whom' এর ব্যবহার | Pronoun Agreement\"\r\ndescription: \"My uncle has three sons ____ work in the same office - শূন্যস্থানে all to them, Each, they all, all of whom কোনটি বসবে? Relative Pronoun 'all of whom' এর সঠিক ব্যবহার শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"all of whom\",\r\n    \"relative pronoun whom\",\r\n    \"pronoun agreement\",\r\n    \"complex sentence structure\",\r\n    \"bcs english grammar\",\r\n    \"three sons all of whom\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: My uncle has three sons \\_\\_\\_\\_ work in the same office.\r\n\r\n**Question:** My uncle has three sons \\_\\_\\_\\_ work in the same office. Which of the following is the best form of Pronoun in the above sentence?\r\n(a) all to them\r\n(b) Each\r\n(c) they all\r\n(d) all of whom\r\n\r\n**Correct Answer:** (d) all of whom\r\n\r\n### কেন 'all of whom' সঠিক উত্তর? (Why is 'all of whom' correct?)\r\n\r\nএই বাক্যটি দুটি ভিন্ন clause-কে একত্রিত করে একটি complex sentence তৈরি করছে। এর জন্য একটি সঠিক **Relative Pronoun** প্রয়োজন।\r\n\r\n**Clause 1:** My uncle has three sons.\r\n**Clause 2:** All of them work in the same office.\r\n\r\nএই দুটি বাক্যকে যুক্ত করার জন্য আমরা `them`-কে তার Relative Pronoun form `whom` দিয়ে প্রতিস্থাপন করব।\r\n\r\n**Key Concept: Using 'whom' in a Relative Clause**\r\n\r\n1.  **Relative Pronoun as Object of Preposition:** `whom` একটি Relative Pronoun যা ব্যক্তির ক্ষেত্রে **object** হিসেবে ব্যবহৃত হয়। এখানে `of` preposition-টির object হিসেবে `them`-এর পরিবর্তে `whom` বসবে।\r\n2.  **Structure:** `Quantifier (all, some, many, none) + of + whom/which`\r\n    - এই গঠনটি একটি non-restrictive relative clause তৈরি করতে ব্যবহৃত হয়, যা antecedent সম্পর্কে অতিরিক্ত তথ্য দেয়।\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- **Antecedent:** `three sons` (ব্যক্তি)\r\n- আমরা বলতে চাই \"তাদের সবাই\" (all of them)।\r\n- দুটি বাক্যকে যুক্ত করার জন্য `them` (Object Pronoun) এর পরিবর্তে `whom` (Relative Pronoun, Object form) ব্যবহার করতে হবে।\r\n- সুতরাং, `all of them` পরিবর্তিত হয়ে `all of whom` হবে।\r\n\r\n**Correct Sentence:** My uncle has three sons, **all of whom** work in the same office.\r\n(আমার চাচার তিনজন পুত্র আছে, যাদের সবাই একই অফিসে কাজ করে।)\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** দুটি বাক্যকে যুক্ত করতে যদি `and all of them` বা `and\r\n  some of them` ব্যবহার করা যায়, তাহলে `and` এবং `them` তুলে দিয়ে তার জায়গায়\r\n  `all of whom` বা `some of whom` বসানো যায়।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) all to them:** `to` preposition-টি এখানে অপ্রাসঙ্গিক এবং `them` দিয়ে দুটি বাক্য যুক্ত করা যায় না।\r\n- **(b) Each:** `Each` ব্যবহার করলে বাক্যটি হতো: \"My uncle has three sons. **Each** works in the same office.\"। কিন্তু শূন্যস্থানে `Each` বসালে দুটি প্রধান verb (`has` এবং `works`) কোনো conjunction ছাড়া একই বাক্যে চলে আসবে, যা 'run-on sentence' নামক ভুল তৈরি করবে।\r\n- **(c) they all:** `they all` ব্যবহার করলে দুটি স্বাধীন বাক্য তৈরি হবে: \"My uncle has three sons. **They all** work in the same office.\"। কিন্তু প্রশ্নটিতে একটি মাত্র বাক্যের শূন্যস্থান পূরণ করতে বলা হয়েছে, দুটি আলাদা বাক্য তৈরি করতে নয়। `they` একটি Subject Pronoun, Relative Pronoun নয়।\r\n\r\n`all of whom` একমাত্র বিকল্প যা ব্যাকরণগতভাবে শুদ্ধ উপায়ে দুটি clause-কে একটি বাক্যে সংযুক্ত করে।";
