export const sourceCode = "import { Callout } from \"nextra/components\";\r\n\r\n# Semi-Modal Verb: \"need\"\r\n\r\n**প্রশ্ন:** You \\_\\_\\_\\_ tomorrow if you have any work.\r\n\r\n(a) need not to come\r\n(b) need to come\r\n(c) need to coming\r\n(d) don’t need coming\r\n\r\n**সঠিক উত্তর:** প্রদত্ত অপশনগুলোর মধ্যে কোনোটিই সম্পূর্ণ সঠিক নয়। তবে, সঠিক উত্তর হওয়ার কথা `need not come` অথবা `don't need to come`।\r\n\r\n---\r\n\r\n### **ব্যাখ্যা**\r\n\r\nএই প্রশ্নটি `need` verb-টির ব্যবহার পরীক্ষা করার জন্য ডিজাইন করা হয়েছে, যা semi-modal verb হিসেবে কাজ করে। এর দুটি রূপ রয়েছে: একটি modal verb এবং অন্যটি main verb।\r\n\r\n#### **Key Concepts:**\r\n\r\n1.  **`need` as a Modal Verb:**\r\n\r\n    - এটি মূলত প্রশ্নবোধক এবং না-বোধক বাক্যে ব্যবহৃত হয়।\r\n    - এর পরে verb-এর base form (V1) বসে, `to` বসে না।\r\n    - Negative form হলো `need not` বা `needn't`।\r\n    - **সঠিক বাক্য:** You **need not come** tomorrow. (তোমার আগামীকাল আসার দরকার নেই।)\r\n\r\n2.  **`need` as a Main Verb:**\r\n    - এটি সাধারণ verb-এর মতো কাজ করে এবং এর পরে to-infinitive (`to + V1`) বসে।\r\n    - Negative form-এর জন্য `do/does/did` ব্যবহার করা হয়।\r\n    - **সঠিক বাক্য:** You **don't need to come** tomorrow. (তোমার আগামীকাল আসার দরকার নেই।)\r\n\r\n#### **অপশনগুলোর বিশ্লেষণ:**\r\n\r\n- **(a) need not to come**: এটি ভুল। Modal `need not`-এর পরে `to` বসে না। সঠিক রূপ: `need not come`।\r\n- **(b) need to come**: এটি ব্যাকরণগতভাবে সঠিক, কিন্তু বাক্যের \"if you have any work\" অংশের সাথে অর্থগতভাবে মেলে না। বাক্যটি বলছে, \"তোমার আগামীকাল আসা দরকার,\" যা শর্তের সাথে বিপরীত।\r\n- **(c) need to coming**: এটি ভুল। `need to`-এর পরে verb-এর base form (`come`) বসে, `-ing` form (`coming`) নয়।\r\n- **(d) don’t need coming**: এটি ভুল। `don't need`-এর পরে to-infinitive (`to come`) বসে, `-ing` form (`coming`) নয়।\r\n\r\n#### **উপসংহার:**\r\n\r\nপ্রশ্নটির উদ্দেশ্য হলো \"আসার প্রয়োজন নেই\" অর্থটি প্রকাশ করা। এই অর্থটি প্রকাশ করার দুটি সঠিক উপায় হলো:\r\n\r\n1.  You **need not come**.\r\n2.  You **don't need to come**.\r\n\r\nযেহেতু এই দুটি সঠিক উত্তরের কোনোটিই অপশনে নেই, তাই প্রশ্নটি ত্রুটিপূর্ণ। তবে, অপশন (a) `need not to come`-কে প্রায়শই ভুল করে সঠিক উত্তর হিসেবে ধরা হয়, কারণ এতে `need not` অংশটি রয়েছে। পরীক্ষার হলে এমন প্রশ্ন এলে, সবচেয়ে কাছাকাছি এবং সম্ভাব্য সঠিক উত্তরটি বেছে নেওয়াই শ্রেয়।\r\n\r\n<Callout type=\"warning\">\r\n  **গুরুত্বপূর্ণ দ্রষ্টব্য:** এই প্রশ্নের অপশনগুলো ত্রুটিপূর্ণ। সঠিক ইংরেজি\r\n  ব্যাকরণ অনুযায়ী, `need not come` এবং `don't need to come` উভয়ই সঠিক উত্তর।\r\n</Callout>";
