export const sourceCode = "---\r\ntitle: \"Linking Verb Explained: Why 'became' is a Linking Verb\"\r\ndescription: \"'He became a politician' - এই বাক্যে 'became' শব্দটি Linking Verb কেন? Linking, Transitive, এবং Intransitive Verb-এর মধ্যে পার্থক্য সহজ বাংলায় শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"linking verb\",\r\n    \"became a politician\",\r\n    \"transitive vs intransitive\",\r\n    \"subject complement\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n    \"types of verbs\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: He became a politician. Here ‘became’ is a/an —\r\n\r\n**Question:** He became a politician. Here ‘became’ is a/an —\r\n(a) linking verb\r\n(b) transitive verb\r\n(c) intransitive verb\r\n(d) action verb\r\n\r\n**Correct Answer:** (a) linking verb\r\n\r\n### কেন 'became' একটি Linking Verb?\r\n\r\nএই প্রশ্নের উত্তরটি বিভিন্ন প্রকার Verb-এর কাজ বোঝার উপর নির্ভরশীল।\r\n\r\n**Key Concept: Linking Verbs**\r\n\r\n1.  **Linking Verb কী?** Linking Verb কোনো কাজ (action) বোঝায় না। এর কাজ হলো বাক্যের **Subject**-কে তার **Subject Complement**-এর সাথে যুক্ত করা। Subject Complement হলো এমন একটি শব্দ বা শব্দগুচ্ছ (noun বা adjective) যা subject-কে বর্ণনা করে বা নতুন পরিচয় দেয়।\r\n2.  **Function:** এরা subject এবং তার বর্ণনার মধ্যে একটি பால (link) বা সমান চিহ্ন (=) তৈরি করে।\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- **Subject:** `He`\r\n- **Verb:** `became`\r\n- **Subject Complement:** `a politician`\r\n\r\nএখানে `became` শব্দটি `He` এবং `a politician`-এর মধ্যে একটি সংযোগ স্থাপন করেছে। `a politician` শব্দটি subject `He`-এর নতুন পরিচয় দিচ্ছে। আমরা বলতে পারি: **He = a politician**। যেহেতু `became` শব্দটি subject এবং তার complement-কে link করছে এবং কোনো action বোঝাচ্ছে না, তাই এটি একটি **Linking Verb**।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** যদি কোনো verb-কে `am`, `is`, `are`, `was`, `were` (to be\r\n  verb) দিয়ে প্রতিস্থাপন করার পরও বাক্যের অর্থ ঠিক থাকে, তবে সেটি সাধারণত একটি\r\n  Linking Verb। - He **became** a politician. -> He **was** a politician. (অর্থ\r\n  ঠিক আছে)। - The soup **tastes** good. -> The soup **is** good. (অর্থ ঠিক আছে)।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(b) transitive verb (সকর্মক ক্রিয়া):** Transitive verb-এর একটি direct object থাকে, যা কাজটি গ্রহণ করে। যেমন: \"He kicked the **ball**.\"। এখানে 'a politician' কোনো object নয়, এটি subject-এরই বর্ণনা।\r\n- **(c) intransitive verb (অকর্মক ক্রিয়া):** Intransitive verb-এর কোনো object থাকে না। যেমন: \"He sleeps.\"। Linking verb-গুলো intransitive verb-এর একটি বিশেষ প্রকার, কিন্তু 'linking verb' এখানে الأكثر সুনির্দিষ্ট উত্তর কারণ এর পরে একটি subject complement আছে।\r\n- **(d) action verb:** Action verb শারীরিক বা মানসিক কাজ বোঝায় (e.g., run, eat, think)। `became` কোনো কাজ না বুঝিয়ে একটি অবস্থা বা পরিবর্তনে পরিণত হওয়া বোঝাচ্ছে।\r\n\r\n### Common Linking Verbs\r\n\r\n- Forms of **be** (am, is, are, was, were, be, being, been)\r\n- **become**, **seem**, **appear**\r\n- Verbs related to senses: **look**, **feel**, **smell**, **sound**, **taste**\r\n  - _Example:_ You **look** happy. She **feels** sad.";
