export const sourceCode = "---\r\ntitle: \"Transitive vs. Intransitive Verb: Why 'burns' is Intransitive in 'Fire burns'\"\r\ndescription: \"'Fire burns' - এই বাক্যে 'burns' শব্দটি Intransitive Verb কেন? Transitive (সকর্মক) এবং Intransitive (অকর্মক) Verb-এর মধ্যে পার্থক্য সহজ বাংলায় শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"intransitive verb\",\r\n    \"transitive verb\",\r\n    \"fire burns\",\r\n    \"types of verbs\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n    \"sokormok okormok kriya\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: Fire burns. What kind of verb ‘burns’ is?\r\n\r\n**Question:** Fire burns. What kind of verb ‘burns’ is?\r\n(a) linking verb\r\n(b) intransitive verb\r\n(c) auxiliary verb\r\n(d) transitive verb\r\n\r\n**Correct Answer:** (b) intransitive verb\r\n\r\n### কেন 'burns' একটি Intransitive Verb?\r\n\r\nএই প্রশ্নের উত্তরটি **Transitive (সকর্মক)** এবং **Intransitive (অকর্মক)** Verb-এর মধ্যে পার্থক্য বোঝার উপর নির্ভরশীল।\r\n\r\n**Key Concept: Transitive vs. Intransitive Verbs**\r\n\r\n1.  **Transitive Verb (সকর্মক ক্রিয়া):** যে Verb-এর অর্থ সম্পূর্ণ করার জন্য একটি **Object (কর্ম)** প্রয়োজন হয়, তাকে Transitive Verb বলে। এই verb-কে 'কী' (what) বা 'কাকে' (whom) দিয়ে প্রশ্ন করলে উত্তর পাওয়া যায়।\r\n\r\n    - _Example:_ He reads a **book**. (He reads what? -> a book). এখানে `reads` হলো Transitive।\r\n\r\n2.  **Intransitive Verb (অকর্মক ক্রিয়া):** যে Verb-এর অর্থ সম্পূর্ণ করার জন্য কোনো **Object (কর্ম)** প্রয়োজন হয় না, তাকে Intransitive Verb বলে। Subject এবং Verb মিলেই একটি পূর্ণাঙ্গ অর্থ প্রকাশ করে।\r\n    - _Example:_ The baby **sleeps**. (বাক্যটি এখানেই সম্পূর্ণ)।\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- **Sentence:** \"Fire burns.\" (আগুন জ্বলে।)\r\n- **Subject:** `Fire`\r\n- **Verb:** `burns`\r\n\r\nএখানে `burns` verb-টির পরে কোনো object নেই। \"Fire burns what?\" বা \"Fire burns whom?\" - এই প্রশ্নের কোনো উত্তর বাক্যে নেই। Subject (`Fire`) এবং Verb (`burns`) মিলেই একটি সম্পূর্ণ অর্থ প্রকাশ করছে। তাই, `burns` এখানে একটি **Intransitive Verb**।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** Verb-কে 'কী' বা 'কাকে' দিয়ে প্রশ্ন করুন। যদি বাক্যের\r\n  মধ্যেই উত্তর খুঁজে পান, তবে verb-টি Transitive। যদি উত্তর না পান, তবে\r\n  Intransitive।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) linking verb:** Linking verb কোনো কাজ বোঝায় না, এটি subject-কে তার complement-এর সাথে যুক্ত করে (e.g., Fire **is** hot)। `burns` একটি কাজ (action) বোঝাচ্ছে।\r\n- **(c) auxiliary verb:** Auxiliary (helping) verb মূল verb-কে সাহায্য করে (e.g., Fire **is burning**)। এখানে `burns` নিজেই মূল verb।\r\n- **(d) transitive verb:** Transitive হতে হলে একটি object থাকা আবশ্যক ছিল। যেমন: \"The fire burns the **wood**.\" (এই বাক্যে `burns` transitive)।\r\n\r\n### একই Verb, ভিন্ন ব্যবহার\r\n\r\nঅনেক verb আছে যারা Transitive এবং Intransitive উভয়ভাবেই ব্যবহৃত হতে পারে। `burn` তাদের মধ্যে একটি।\r\n\r\n- **Intransitive:** The fire **burns** brightly.\r\n- **Transitive:** He **burns** the old letters.";
