export const sourceCode = "---\r\ntitle: \"Parts of Speech: 'water' as a Verb in 'The girls water the plants'\"\r\ndescription: \"'The girls water the plants' - এই বাক্যে 'water' শব্দটি Noun না হয়ে Verb কেন? Parts of Speech সনাক্ত করার সহজ কৌশল এবং Conversion এর নিয়ম শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"water as a verb\",\r\n    \"parts of speech\",\r\n    \"conversion in grammar\",\r\n    \"zero derivation\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: The girls water the plants. Here ‘water’ is —\r\n\r\n**Question:** The girls water the plants. Here ‘water’ is —\r\n(a) noun\r\n(b) verb\r\n(c) adjective\r\n(d) adverb\r\n\r\n**Correct Answer:** (b) verb\r\n\r\n### কেন 'water' একটি Verb? (Why is 'water' a Verb?)\r\n\r\nসাধারণত `water` (পানি) শব্দটি Noun হিসেবে পরিচিত হলেও, এই বাক্যে এটি একটি **Verb** হিসেবে ব্যবহৃত হয়েছে। এর কারণ হলো **Conversion** বা **Zero Derivation** নামক একটি ব্যাকরণিক প্রক্রিয়া।\r\n\r\n**Key Concept: Conversion (or Zero Derivation)**\r\n\r\nConversion হলো এমন একটি প্রক্রিয়া যেখানে একটি শব্দকে তার রূপ পরিবর্তন না করেই ভিন্ন Parts of Speech হিসেবে ব্যবহার করা হয়। বাক্যে শব্দের অবস্থান এবং কাজই নির্ধারণ করে দেয় সেটি কোন Parts of Speech।\r\n\r\n**বাক্য বিশ্লেষণ:**\r\nএকটি সাধারণ ইংরেজি বাক্যের গঠন হলো: **Subject + Verb + Object**।\r\n\r\n- **Subject (কর্তা):** `The girls` (কারা কাজ করছে?)\r\n- **Verb (ক্রিয়া):** `water` (কী কাজ করছে?)\r\n- **Object (কর্ম):** `the plants` (কিসের উপর কাজ করছে?)\r\n\r\nএখানে `water` শব্দটি মেয়েদের দ্বারা সম্পাদিত একটি **কাজ** বা **ক্রিয়া** (action) বোঝাচ্ছে। 'পানি দেওয়া' (to give water) অর্থে এটি ব্যবহৃত হয়েছে। যেহেতু এটি একটি কাজ বোঝাচ্ছে এবং বাক্যের Verb-এর স্থানে বসেছে, তাই এটি একটি Verb।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** কোনো শব্দের Parts of Speech মুখস্থ না করে, বাক্যে তার\r\n  **অবস্থান (position)** এবং **কাজ (function)** দেখুন। Subject-এর পরে যদি কোনো\r\n  শব্দ কাজ করা বোঝায়, তবে সেটিই Verb।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) noun:** শব্দটি যদি Noun হতো, তাহলে বাক্যটি হতো \"The girls drink **water**.\" (এখানে water কর্ম বা object)। কিন্তু আমাদের প্রশ্নে `water` নিজেই একটি কাজ।\r\n- **(c) adjective:** Adjective কোনো Noun বা Pronoun-কে বিশেষায়িত করে (e.g., **water** bottle)। এখানে `water` কাউকে বিশেষায়িত করছে না।\r\n- **(d) adverb:** Adverb কোনো Verb, Adjective বা অন্য Adverb-কে বিশেষায়িত করে। এখানে `water` অন্য কাউকে modify করছে না।\r\n\r\n### Conversion-এর আরও উদাহরণ\r\n\r\n- **Book:**\r\n  - Noun: I am reading a **book**.\r\n  - Verb: Please **book** a ticket for me.\r\n- **Man:**\r\n  - Noun: He is a good **man**.\r\n  - Verb: We need to **man** the counter.\r\n- **Google:**\r\n  - Noun: **Google** is a search engine.\r\n  - Verb: Just **google** it.";
