export const sourceCode = "import { Callout } from \"nextra/components\";\r\n\r\n# Cognate Object: \"dreamt a dream\"\r\n\r\n**প্রশ্ন:** I dreamt a wonderful dream. (এখানে 'a wonderful dream' কোন ধরনের object?)\r\n\r\n(a) Retained object\r\n(b) Cognate object\r\n(c) Reflexive object\r\n(d) Factive object\r\n\r\n**সঠিক উত্তর:** (b) Cognate object\r\n\r\n---\r\n\r\n### **ব্যাখ্যা**\r\n\r\nএই বাক্যে `a wonderful dream` হলো একটি Cognate Object। Cognate Object হলো এমন একটি object যা তার verb-এর সাথে একই মূল (root) বা ধাতু থেকে উদ্ভূত এবং অর্থের দিক থেকে সম্পর্কিত।\r\n\r\n#### **Key Concepts:**\r\n\r\n1.  **Cognate Object (সমধাতুজ কর্ম):**\r\n\r\n    - সাধারণত, Intransitive Verb (যে verb-এর object লাগে না) object গ্রহণ করে না। যেমন: `dream`, `live`, `die`, `sing`, `run`।\r\n    - কিন্তু যখন এই ধরনের verb-এর পরে এমন একটি object বসে যা verb-টির সাথে একই ধাতু বা উৎস থেকে তৈরি, তখন সেই object-কে Cognate Object বলে।\r\n    - এই object-এর আগে সাধারণত একটি adjective থাকে।\r\n\r\n    **Structure:** `Subject + Intransitive Verb + (Adjective) + Cognate Object`\r\n\r\n#### **বিশ্লেষণ:**\r\n\r\n- **Verb:** `dreamt` (from `dream`)\r\n- **Object:** `dream`\r\n\r\nএখানে verb এবং object উভয়ই `dream` মূল থেকে এসেছে। তাই `a wonderful dream` একটি Cognate Object।\r\n\r\n#### **অন্যান্য উদাহরণ:**\r\n\r\n- He **lived** a happy **life**. (Verb: `live`, Object: `life`)\r\n- She **sang** a sweet **song**. (Verb: `sing`, Object: `song`)\r\n- They **fought** a great **fight**. (Verb: `fight`, Object: `fight`)\r\n- He **ran** a **race**. (Verb: `run`, Object: `race`)\r\n\r\n#### **কেন অন্য অপশনগুলো ভুল?**\r\n\r\n- **(a) Retained object**: Passive voice-এ ব্যবহৃত হয়। যখন ditransitive verb-কে passive করা হয়, তখন যে object-টি subject হয় না এবং verb-এর পরে থেকে যায়, তাকে Retained Object বলে। (e.g., I was given **a book**. এখানে `a book` হলো retained object)।\r\n- **(c) Reflexive object**: যখন object এবং subject একই ব্যক্তি বা বস্তুকে বোঝায় এবং object হিসেবে reflexive pronoun (`myself`, `himself`, `themselves` ইত্যাদি) বসে। (e.g., He hurt **himself**.)\r\n- **(d) Factive object**: `make`, `elect`, `select`, `nominate`, `call` ইত্যাদি verb-এর object-কে Factive Object বলে, যার অর্থ সম্পূর্ণ করার জন্য একটি object complement লাগে। (e.g., We made him **captain**. এখানে `him` হলো factive object এবং `captain` হলো complement)।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Memorize:** যদি দেখেন verb এবং object একই শব্দ বা একই মূল থেকে এসেছে\r\n  (যেমন `dream` ও `dream`, `sing` ও `song`), তাহলে সেটি Cognate Object।\r\n</Callout>";
