export const sourceCode = "---\r\ntitle: \"Relative Clause Placement: 'The man who stole my bag was tall'\"\r\ndescription: \"কোন বাক্যটি শুদ্ধ? The man who stole my bag was tall. Relative Clause (who stole my bag) এর সঠিক ব্যবহার শিখুন। Adjective Clause কে Noun এর পর বসানোর নিয়ম জানুন।\"\r\nkeywords:\r\n  [\r\n    \"relative clause placement\",\r\n    \"correct sentence structure\",\r\n    \"adjective clause\",\r\n    \"english grammar rules\",\r\n    \"bcs english preparation\",\r\n    \"who stole my bag was tall\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: Which one is correct?\r\n\r\n**Question:** Which one is correct?\r\n(a) The man was tall who stole my bag.\r\n(b) The man stole my bag who is tall.\r\n(c) The man stole my bag who was tall.\r\n(d) The man who stole my bag was tall.\r\n\r\n**Correct Answer:** (d) The man who stole my bag was tall.\r\n\r\n### কেন (d) সঠিক উত্তর? (Why is (d) correct?)\r\n\r\nএই প্রশ্নের সঠিক উত্তরটি **Relative Clause** বা **Adjective Clause**-এর সঠিক Placement-এর উপর নির্ভরশীল।\r\n\r\n**Key Concept: Relative Clause তার Antecedent-এর ঠিক পরেই বসে।**\r\n\r\n1.  **Relative Clause কী?** এটি একটি অধীনস্থ বাক্য (subordinate clause) যা `who`, `which`, `that` ইত্যাদি Relative Pronoun দিয়ে শুরু হয়। এর কাজ হলো তার পূর্ববর্তী Noun বা Pronoun (যাকে **Antecedent** বলে) সম্পর্কে অতিরিক্ত তথ্য দেওয়া।\r\n\r\n2.  **Antecedent কী?** Antecedent হলো সেই Noun বা Pronoun যাকে Relative Clause-টি modify করে। এই বাক্যে, Antecedent হলো `The man`।\r\n\r\n3.  **Placement Rule:** Relative Clause-কে অবশ্যই তার Antecedent-এর **যতটা সম্ভব কাছে** বসাতে হবে, সাধারণত ঠিক পরেই। এতে বাক্যের অর্থ স্পষ্ট থাকে।\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- **Main Clause:** \"The man was tall.\" (লোকটি লম্বা ছিল।)\r\n- **Relative Clause:** \"who stole my bag\" (যে আমার ব্যাগ চুরি করেছিল।)\r\n\r\nএই Relative Clause-টি `The man`-কে বর্ণনা করছে। তাই নিয়ম অনুযায়ী, এটি `The man`-এর ঠিক পরেই বসবে।\r\n\r\n**Correct Structure:**\r\n`The man` (Antecedent) + `who stole my bag` (Relative Clause) + `was tall` ( বাকি Main Clause)।\r\n\r\nResult: **The man who stole my bag was tall.**\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** যে Noun-টি সম্পর্কে `who/which/that` দিয়ে অতিরিক্ত তথ্য\r\n  দিচ্ছেন, সেই তথ্যটুকু Noun-টির পাশেই বসিয়ে দিন। এতে বাক্যটি যৌক্তিক এবং শুদ্ধ\r\n  হবে।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) The man was tall who stole my bag:** এখানে Relative Clause `who stole my bag` তার Antecedent `The man` থেকে অনেক দূরে `tall` Adjective-এর পরে বসেছে। এটি একটি 'Misplaced Modifier' এর উদাহরণ এবং অশুদ্ধ।\r\n- **(b) The man stole my bag who is tall:** এখানে `who is tall` clause-টি `my bag`-এর পরে বসেছে, যা শুনে মনে হচ্ছে যেন 'ব্যাগটি' লম্বা ছিল। এটি অর্থ পরিবর্তন করে দিচ্ছে এবং ভুল।\r\n- **(c) The man stole my bag who was tall:** এটিও (b)-এর মতো একই ভুল। `who was tall` clause-টি ভুল জায়গায় বসেছে।\r\n\r\nএই নিয়মটি ইংরেজি বাক্য গঠনের অন্যতম মৌলিক ভিত্তি এবং সকল প্রকার পরীক্ষার জন্য গুরুত্বপূর্ণ।";
