export const sourceCode = "---\r\ntitle: What is the noun of 'Deny'? | Denial Meaning & Easy Bangla Grammar Tips\r\ndescription: Learn the correct noun form of the verb 'Deny' with clear Bangla explanation, examples, tricks, and memory hacks for BCS and Primary School Teacher exam preparation.\r\ntags: Deny noun form, Denial meaning Bangla, Verb to Noun conversion, BCS English Grammar, Primary Teacher English, English Grammar Tricks, Nominalization in English\r\n---\r\n\r\n# ❓ What is the noun of 'Deny'?\r\n\r\n(ক) Refuse  \r\n(খ) **Denial** ✅  \r\n(গ) Deny  \r\n(ঘ) Deniable\r\n\r\n---\r\n\r\n## ✅ সঠিক উত্তর: **Denial**\r\n\r\n---\r\n\r\n## 💡 কেন 'Denial' সঠিক Noun Form?\r\n\r\n**Deny** একটি Verb, যার অর্থ — **অস্বীকার করা**।  \r\nএই Verb এর noun form হলো → **Denial**  \r\nযার অর্থ — **অস্বীকৃতি / অস্বীকার**।\r\n\r\n| Verb (ক্রিয়া) | Noun (নাম) |\r\n| ------------- | ---------- |\r\n| Deny          | Denial     |\r\n\r\n---\r\n\r\n## 🔎 প্রতিটি Options বিশ্লেষণ\r\n\r\n### ক) Refuse ❌\r\n\r\nএটি আরেকটি Verb (অস্বীকার করা) → Deny এর noun নয়।\r\n\r\n### খ) Denial ✅\r\n\r\nএটাই সঠিক Noun form → Deny → Denial\r\n\r\n### গ) Deny ❌\r\n\r\nএটি Verb নিজেই।\r\n\r\n### ঘ) Deniable ❌\r\n\r\nএটি Adjective → যার অর্থ “অস্বীকারযোগ্য/deny করা সম্ভব”।\r\n\r\n---\r\n\r\n## ⭐ কনসেপ্ট: Verb থেকে Noun তৈরি (Nominalization)\r\n\r\nইংরেজিতে অনেক Verb এর Noun form হয় **-al, -tion, -ment** ইত্যাদি suffix যোগ করে।  \r\nDeny + al → **Denial**\r\n\r\nআরো উদাহরণ:\r\n\r\n- Approve → Approval\r\n- Arrive → Arrival\r\n- Propose → Proposal\r\n- Deny → Denial\r\n\r\n---\r\n\r\n## 🧠 Memory Hacks (Exam Tricks)\r\n\r\n### 🔹 Hack 1: Verb + “al” → অনেক সময় Noun\r\n\r\nDeny → Denial  \r\nRefuse → Refusal  \r\nPropose → Proposal  \r\nArrive → Arrival\r\n\r\nএভাবে মুখস্থ করলে BCS-এর ২০+ similar প্রশ্ন solve করা সহজ।\r\n\r\n---\r\n\r\n### 🔹 Hack 2: “able / ible” = প্রায় সবসময় Adjective\r\n\r\nযেমন—\r\n\r\n- Deniable\r\n- Comfortable\r\n- Visible\r\n\r\nতাই **Deniable কখনোই Noun হতে পারে না** → Exam Trick!\r\n\r\n---\r\n\r\n### 🔹 Hack 3: Noun চিনতে “The + Noun” test\r\n\r\nSentence বানিয়ে দেখো—\r\n\r\n- The denial was unexpected. ✔️ (মানে ঠিক আছে → এটি Noun)\r\n- The deny was unexpected. ❌ (মানে ঠিক না → Verb)\r\n\r\nএটি exam-এ খুব কাজে লাগে।\r\n\r\n---\r\n\r\n## 🌿 Easy Bangla Explanation\r\n\r\n- **Deny (অস্বীকার করা)** → কাজ বোঝায় → Verb\r\n- **Denial (অস্বীকার)** → নাম বোঝায় → Noun\r\n- **Refuse** → Deny এর synonym, কিন্তু noun নয়\r\n- **Deniable** → adjective\r\n\r\nঅতএব, Deny এর noun হলো **Denial**।\r\n\r\n---\r\n\r\n## 📌 Final Takeaway\r\n\r\n- Deny → Verb\r\n- Denial → Noun (সঠিক উত্তর)\r\n- Refuse → Verb\r\n- Deniable → Adjective\r\n\r\n**Exam Formula:**\r\n\r\n> Verb + “al” → সাধারণত Noun  \r\n> “able/ible” → Adjective  \r\n> Deny এর একমাত্র standard noun form = Denial\r\n\r\n---";
