export const sourceCode = "import { Callout } from \"nextra/components\";\r\n\r\n# Reported Speech: \"will\" থেকে \"would\"\r\n\r\n**প্রশ্ন:** She told that she (visit) London next month. Here the right form verb will be—\r\n\r\n(a) visited\r\n(b) will visit\r\n(c) had visited\r\n(d) would visit\r\n\r\n**সঠিক উত্তর:** (d) would visit\r\n\r\n---\r\n\r\n### **ব্যাখ্যা**\r\n\r\nএই বাক্যটি Indirect Speech বা Reported Speech-এর একটি উদাহরণ। যখন Direct Speech-কে Indirect Speech-এ পরিবর্তন করা হয়, তখন Reporting Verb (এখানে 'told') যদি Past Tense-এ থাকে, তাহলে Reported Speech-এর Tense এবং কিছু শব্দের পরিবর্তন হয়।\r\n\r\n#### **Key Concepts:**\r\n\r\n1.  **Reporting Verb in Past Tense:** বাক্যের Reporting Verb হলো `told`, যা Past Tense-এ আছে।\r\n2.  **Tense Change in Reported Speech:** Reporting Verb Past Tense-এ থাকলে Direct Speech-এর `will` পরিবর্তন হয়ে `would` হয়ে যায়।\r\n\r\n#### **Direct vs. Indirect Speech:**\r\n\r\n- **Direct Speech:** She said, \"I **will** visit London next month.\"\r\n  - এখানে বক্তার কথা হুবহু উদ্ধৃত করা হয়েছে।\r\n- **Indirect Speech:** She told that she **would** visit London the following month.\r\n  - এখানে বক্তার কথাকে অন্য কেউ নিজের ভাষায় প্রকাশ করছে।\r\n\r\n#### **নিয়ম:**\r\n\r\n| Direct Speech | Indirect Speech       |\r\n| :------------ | :-------------------- |\r\n| `will`        | `would`               |\r\n| `shall`       | `should`/`would`      |\r\n| `can`         | `could`               |\r\n| `may`         | `might`               |\r\n| `next month`  | `the following month` |\r\n\r\nএই বাক্যে `told` (Past Tense) থাকায় `will visit` পরিবর্তন হয়ে `would visit` হবে।\r\n\r\n<Callout type=\"info\">\r\n  ** মনে রাখবেন:** Reporting Verb যদি Present Tense (e.g., `says`, `tells`) বা\r\n  Future Tense (e.g., `will say`) হয়, তাহলে Reported Speech-এর Tense-এর কোনো\r\n  পরিবর্তন হয় না। <br />\r\n  **উদাহরণ:** He **says** that he **will** go.\r\n</Callout>\r\n\r\nএই প্রশ্নটি Reported Speech-এর Tense পরিবর্তনের নিয়মটি যাচাই করার জন্য ডিজাইন করা হয়েছে, যা BCS এবং অন্যান্য প্রতিযোগিতামূলক পরীক্ষার জন্য একটি গুরুত্বপূর্ণ বিষয়।";
