export const sourceCode = "---\r\ntitle: \"Participle Phrase: 'Finishing the letter tonight, Rahim will post it'\"\r\ndescription: \"'___ the letter tonight, Rahim will post it tomorrow.' - কেন 'Finishing' দিয়ে বাক্য শুরু হলো? Present Participle Phrase এর ব্যবহার সহজ বাংলায় শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"participle phrase\",\r\n    \"present participle\",\r\n    \"finishing the letter\",\r\n    \"dangling modifier\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n    \"sequence of actions\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: \\_\\_\\_ the letter tonight, Rahim will post it tomorrow.\r\n\r\n**Question:** \\_\\_\\_ the letter tonight, Rahim will post it tomorrow.\r\n(a) Finishing\r\n(b) Finished\r\n(c) To finish\r\n(d) Finish\r\n\r\n**Correct Answer:** (a) Finishing\r\n\r\n### কেন 'Finishing' সঠিক উত্তর? (Why is 'Finishing' correct?)\r\n\r\nএই বাক্যটি একটি **Participle Phrase** ব্যবহার করে দুটি কাজকে একটি বাক্যে সংযুক্ত করেছে।\r\n\r\n**Key Concept: Participle Phrase to Show Sequence or Cause**\r\n\r\n1.  **Participle Phrase:** একটি Present Participle (`verb+ing`) দিয়ে শুরু হওয়া phrase যা main clause-এর subject-কে modify করে। এটি subject-এর দ্বারা সম্পাদিত একটি কাজ বর্ণনা করে যা main clause-এর কাজের সাথে সম্পর্কিত।\r\n2.  **Subject Agreement:** Participle phrase-এর কাজটি অবশ্যই main clause-এর subject দ্বারা সম্পাদিত হতে হবে।\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- **Main Clause:** \"Rahim will post it tomorrow.\" (রহিম আগামীকাল এটি পোস্ট করবে।)\r\n- **Main Subject:** `Rahim`।\r\n- **Action in the Phrase:** চিঠিটি শেষ করার কাজ (finishing the letter)।\r\n- **Who performs the action?** `Rahim`।\r\n\r\nযেহেতু `Rahim` নিজেই চিঠিটি শেষ করার কাজ করছে, তাই `Rahim`-কে modify করার জন্য একটি Present Participle Phrase (`Finishing...`) ব্যবহার করা এখানে সঠিক। এই গঠনটি বোঝাচ্ছে যে, একটি কাজ (চিঠি শেষ করা) করার পর অন্য কাজটি (পোস্ট করা) করা হবে।\r\n\r\n**Correct Structure:** **Finishing the letter tonight** (Participle Phrase), **Rahim** (Subject) will post it tomorrow.\r\n(আজ রাতে চিঠিটি শেষ করে, রহিম আগামীকাল এটি পোস্ট করবে।)\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** যখন একটি বাক্যের শুরুতে `verb+ing` থাকে এবং কমার পরে\r\n  একটি পূর্ণাঙ্গ বাক্য থাকে, তখন দেখুন `verb+ing`-এর কাজটি কি main subject করছে\r\n  কিনা। যদি করে, তাহলে গঠনটি সাধারণত সঠিক।\r\n</Callout>\r\n\r\n### Perfect Participle-এর ব্যবহার\r\n\r\nএই ধরনের বাক্যে, প্রথম কাজটি যে দ্বিতীয় কাজের আগেই সম্পন্ন হয়েছে তা বোঝানোর জন্য **Perfect Participle (`Having` + V3)** ব্যবহার করাও শুদ্ধ।\r\n\r\n- **Having finished** the letter tonight, Rahim will post it tomorrow.\r\n  যেহেতু `Having finished` option-এ নেই, তাই `Finishing` এখানে একমাত্র সঠিক উত্তর।\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(b) Finished:** Past Participle দিয়ে phrase শুরু হলে তা সাধারণত passive অর্থ দেয় (e.g., **Finished** by Rahim, the letter was ready.) অথবা একটি অবস্থা বোঝায়। এখানে active কাজ বোঝানো হচ্ছে।\r\n- **(c) To finish:** `To`-infinitive phrase উদ্দেশ্য বোঝাতে ব্যবহৃত হয়। যেমন: \"**To finish** the letter, he stayed up late.\" (চিঠিটি শেষ করার জন্য, সে দেরি পর্যন্ত জেগে ছিল)।\r\n- **(d) Finish:** Bare infinitive দিয়ে এভাবে একটি modifying phrase শুরু করা যায় না।";
