export const sourceCode = "---\r\ntitle: \"Pronoun-Antecedent Agreement: 'employees who have worked hard'\"\r\ndescription: \"A reward has been announced for the employees who ____ hard - শূন্যস্থানে have worked, has worked, will be work কোনটি বসবে? Pronoun-Antecedent Agreement এর নিয়ম শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"pronoun antecedent agreement\",\r\n    \"employees who have worked\",\r\n    \"relative pronoun agreement\",\r\n    \"subject verb agreement mcq\",\r\n    \"bcs english grammar\",\r\n    \"plural antecedent\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: A reward has been announced for the employees who \\_\\_\\_\\_ hard.\r\n\r\n**Question:** A reward has been announced for the employees who \\_\\_\\_\\_ hard.\r\n(a) have worked\r\n(b) has worked\r\n(c) will be work\r\n(d) have had worked\r\n\r\n**Correct Answer:** (a) have worked\r\n\r\n### কেন 'have worked' সঠিক উত্তর? (Why is 'have worked' correct?)\r\n\r\nএই প্রশ্নের উত্তরটি **Pronoun-Antecedent Agreement** এবং **Subject-Verb Agreement**-এর নিয়মের উপর ভিত্তি করে।\r\n\r\n**Key Concept: Relative Pronoun-এর Verb তার Antecedent-কে অনুসরণ করে।**\r\n\r\n1.  **Relative Pronoun:** `who` একটি Relative Pronoun যা তার পূর্ববর্তী Noun বা Pronoun (যাকে **Antecedent** বলে) কে নির্দেশ করে।\r\n2.  **Antecedent:** এই বাক্যে `who`-এর Antecedent হলো `the employees`।\r\n3.  **Agreement Rule:** Relative Pronoun (`who`, `which`, `that`) যখন একটি clause-এর subject হিসেবে কাজ করে, তখন তার verb-টি singular হবে নাকি plural হবে, তা নির্ভর করে তার Antecedent-এর উপর।\r\n    - যদি Antecedent singular হয়, verb singular হবে।\r\n    - যদি Antecedent plural হয়, verb plural হবে।\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- **Antecedent:** `the employees` (কর্মচারীরা) - এটি একটি **Plural Noun**।\r\n- **Relative Pronoun:** `who` (যে কর্মচারীরা) - `who` এখানে `the employees`-কে নির্দেশ করছে, তাই `who`-ও **plural** হিসেবে বিবেচিত হবে।\r\n- **Verb Agreement:** যেহেতু subject (`who`) plural, তাই verb-টিও plural হতে হবে।\r\n  - `have` হলো plural verb।\r\n  - `has` হলো singular verb।\r\n\r\nঅতএব, `who`-এর পরে plural verb `have` বসবে। `have worked` (Present Perfect Tense) এখানে সঠিক।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** `who/which/that`-এর আগের শব্দটি (Antecedent) দেখুন।\r\n  শব্দটি singular হলে পরের verb-ও singular (`is/was/has/does`) হবে। শব্দটি\r\n  plural হলে পরের verb-ও plural (`are/were/have/do`) হবে।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(b) has worked:** `has` একটি singular verb। কিন্তু Antecedent `employees` plural হওয়ায় এটি ভুল।\r\n- **(c) will be work:** `will be`-এর পরে verb-এর base form (`work`) বসে না, বরং `working` (continuous) বা `worked` (passive) বসতে পারে। এই গঠনটি ভুল।\r\n- **(d) have had worked:** `have had` (Past Perfect) এখানে Tense-এর সঠিক ব্যবহার নয়। \"A reward has been announced\" (Present Perfect) দ্বারা বোঝা যাচ্ছে যে কাজটি সম্প্রতি সম্পন্ন হয়েছে এবং তার ফল বর্তমান। তাই `have worked` (Present Perfect) ই الأكثر ملاءمة।\r\n\r\n### আরও উদাহরণ (More Examples)\r\n\r\n- He is one of the **boys who have** done the work. (Antecedent: boys - plural)\r\n- This is the **book that was** lost yesterday. (Antecedent: book - singular)";
