export const sourceCode = "---\r\ntitle: \"I am used to watching: 'be used to' + Gerund Rule Explained\"\r\ndescription: \"'I am used ___ that television show...' - কেন 'to watching' সঠিক? 'be used to' এবং 'used to' এর মধ্যে পার্থক্য ও Gerund ব্যবহারের নিয়মটি সহজ বাংলায় শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"be used to watching\",\r\n    \"used to vs be used to\",\r\n    \"gerund after to\",\r\n    \"accustomed to\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: I am used \\_\\_\\_ that television show all the time as it always helps me to know about the contemporary issues of the world.\r\n\r\n**Question:** I am used \\_\\_\\_ that television show all the time...\r\n(a) to watch\r\n(b) to watching\r\n(c) to watching _(duplicate)_\r\n(d) to be watched\r\n\r\n**Correct Answer:** (b) / (c) to watching\r\n\r\n### কেন 'to watching' সঠিক উত্তর?\r\n\r\nএই প্রশ্নের উত্তরটি `used to` এবং `be used to`-এর মধ্যে থাকা গুরুত্বপূর্ণ পার্থক্য বোঝার উপর নির্ভরশীল।\r\n\r\n**Key Concepts:**\r\n\r\n1.  **`used to` + Base Form (V1):**\r\n\r\n    - **Meaning:** অতীতের কোনো অভ্যাস বা অবস্থা যা এখন আর নেই (past habit or state)।\r\n    - **Structure:** Subject + `used to` + **Verb (base form)**\r\n    - _Example:_ I **used to play** football when I was young. (আমি ছোটবেলায় ফুটবল খেলতাম, এখন খেলি না)।\r\n\r\n2.  **`be used to` + Gerund (V-ing):**\r\n    - **Meaning:** কোনো কিছুতে অভ্যস্ত থাকা (accustomed to)।\r\n    - **Structure:** Subject + `be` verb (am/is/are/was/were) + `used to` + **Gerund (verb+ing)**\r\n    - _Example:_ I **am used to living** in a big city. (আমি বড় শহরে থাকতে অভ্যস্ত)।\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- \"I **am used** \\_\\_\\_ that television show...\"\r\n- এখানে `used to`-এর আগে `am` (`be` verb) আছে। সুতরাং, এটি \"অভ্যস্ত থাকা\" অর্থ প্রকাশ করছে।\r\n- নিয়ম অনুযায়ী, `be used to`-এর পরে verb-এর সাথে `ing` যোগ করে Gerund (`watching`) বসাতে হবে।\r\n\r\n**সঠিক গঠন:** I am used **to watching**...\r\n(আমি ঐ টেলিভিশন শো-টি দেখতে অভ্যস্ত...)\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** - `used to`-এর আগে যদি `am/is/are/was/were` থাকে, তাহলে\r\n  পরের verb-এর সাথে `ing` যোগ হবে। - `used to`-এর আগে যদি `am/is/are` না থাকে,\r\n  তাহলে পরের verb-টি simple form (V1) হবে।\r\n</Callout>\r\n\r\n### 'to'-এর পর 'ing' কেন?\r\n\r\nসাধারণত `to`-এর পরে verb-এর base form বসে (infinitive)। কিন্তু `be used to`, `look forward to`, `addicted to`, `with a view to`, `object to` - এই phrase-গুলোতে `to` একটি **preposition** হিসেবে কাজ করে, infinitive marker হিসেবে নয়। আর preposition-এর পরে verb আসলে তার সাথে `ing` যোগ হয় (Gerund)।\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) to watch:** এটি base form। `am used to`-এর পরে base form বসে না। যদি বাক্যটি হতো \"I **used to watch**...\", তাহলে এটি সঠিক হতো।\r\n- **(d) to be watched:** এটি passive infinitive। এর অর্থ হবে \"দেখার শিকার হতে অভ্যস্ত,\" যা এখানে কোনো অর্থ তৈরি করে না।\r\n\r\nএই নিয়মটি প্রতিযোগিতামূলক পরীক্ষার জন্য খুবই জনপ্রিয় একটি টপিক।";
