export const sourceCode = "import { Callout } from \"nextra/components\";\r\n\r\n# Modal for Past Criticism: \"shouldn't have lied\"\r\n\r\n**প্রশ্ন:** You \\_\\_\\_\\_ to me. Now I’ll never believe you again.\r\n\r\n(a) wouldn’t have lied\r\n(b) can’t have lied\r\n(c) shouldn’t have lied\r\n(d) mustn’t have lied\r\n\r\n**সঠিক উত্তর:** (c) shouldn’t have lied\r\n\r\n---\r\n\r\n### **ব্যাখ্যা**\r\n\r\nএই বাক্যটিতে বক্তা অতীতের একটি কাজের জন্য অনুশোচনা বা সমালোচনা (regret or criticism about a past action) প্রকাশ করছেন। যে কাজটি করা উচিত হয়নি কিন্তু করা হয়ে গেছে, তা বোঝানোর জন্য `shouldn't have + Past Participle (V3)` ব্যবহৃত হয়।\r\n\r\n#### **Key Concepts:**\r\n\r\n1.  **Modal Perfect for Past Regret/Criticism:**\r\n    - **`should have + V3`**: অতীতের কোনো কাজ করা উচিত ছিল, কিন্তু করা হয়নি।\r\n      - **উদাহরণ:** I **should have studied** harder. (আমার আরও কঠোর পড়াশোনা করা উচিত ছিল।)\r\n    - **`shouldn't have + V3`**: অতীতের কোনো কাজ করা উচিত হয়নি, কিন্তু করা হয়েছে।\r\n      - **উদাহরণ:** You **shouldn't have said** that. (তোমার ওটা বলা উচিত হয়নি।)\r\n\r\n#### **বিশ্লেষণ:**\r\n\r\n- **Context (প্রেক্ষাপট):** \"Now I’ll never believe you again.\" (এখন আমি তোমাকে আর কখনো বিশ্বাস করব না।)\r\n- **Past Action (অতীতের কাজ):** The person lied. (ব্যক্তিটি মিথ্যা বলেছিল।)\r\n- **Criticism (সমালোচনা):** বক্তা বলছেন যে মিথ্যা বলাটা উচিত হয়নি।\r\n\r\n\"You **shouldn't have lied** to me\" বাক্যটির অর্থ হলো, \"তোমার আমাকে মিথ্যা বলা উচিত হয়নি।\" এটি বক্তার অসন্তুষ্টি এবং কাজটি যে ভুল ছিল, তা স্পষ্টভাবে প্রকাশ করছে।\r\n\r\n#### **কেন অন্য অপশনগুলো ভুল?**\r\n\r\n- **(a) wouldn’t have lied**: এটি সাধারণত কাল্পনিক পরিস্থিতি বা Conditional Sentence-এ ব্যবহৃত হয়। (e.g., If I were you, I **wouldn't have lied**.)\r\n- **(b) can’t have lied**: এটি দিয়ে বোঝায় অতীতে কিছু ঘটা অসম্ভব ছিল। (e.g., He was with me, so he **can't have lied**.) এটা বর্তমান পরিস্থিতির ঠিক বিপরীত।\r\n- **(d) mustn’t have lied**: এটি একটি ভুল গঠন। অতীতের অনুমান বোঝাতে `must have lied` (নিশ্চয়ই মিথ্যা বলেছে) বা `can't have lied` (মিথ্যা বলতে পারে না) ব্যবহৃত হয়।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Memorize:** * **উচিত ছিল (কিন্তু করিনি)** -> `should have + V3` *\r\n  **উচিত ছিল না (কিন্তু করেছি)** -> `shouldn't have + V3`\r\n</Callout>\r\n\r\nএই ধরনের গঠন ইংরেজিতে অনুশোচনা, সমালোচনা এবং অতীতের ভুল নিয়ে কথা বলার জন্য খুব প্রচলিত।";
