export const sourceCode = "---\r\ntitle: \"Possessive Pronoun Before Gerund | English Grammar MCQ\"\r\ndescription: \"She insisted on his leaving the house - এই বাক্যে Gerund এর আগে কেন Possessive Pronoun (his) ব্যবহৃত হলো? BCS, NTRCA, Primary সহ সকল পরীক্ষার জন্য গুরুত্বপূর্ণ নিয়মটি শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"possessive pronoun before gerund\",\r\n    \"gerund rules\",\r\n    \"english grammar mcq\",\r\n    \"bcs english preparation\",\r\n    \"primary teacher exam english\",\r\n    \"possessive adjective\",\r\n    \"gerund and possessive\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: She insisted on \\_\\_\\_\\_ leaving the house.\r\n\r\n**Question:** She insisted on \\_\\_\\_\\_ leaving the house.\r\n(a) he\r\n(b) him\r\n(c) himself\r\n(d) his\r\n\r\n**Correct Answer:** (d) his\r\n\r\n### কেন 'his' সঠিক উত্তর? (Why is 'his' correct?)\r\n\r\nএই প্রশ্নের সঠিক উত্তরটি বোঝার জন্য আমাদের **Gerund** এবং তার আগে ব্যবহৃত **Pronoun**-এর নিয়মটি জানতে হবে।\r\n\r\n**Key Concept: Gerund-এর আগে Possessive Pronoun বসে।**\r\n\r\n1.  **Gerund কী?** যখন কোনো Verb-এর সাথে `ing` যুক্ত হয়ে শব্দটি Noun-এর মতো কাজ করে, তখন তাকে Gerund বলে। এখানে, `leaving` (ত্যাগ করা) শব্দটি একটি কাজকে বোঝাচ্ছে এবং `on` preposition-টির object হিসেবে ব্যবহৃত হয়েছে, তাই এটি একটি Gerund।\r\n\r\n2.  **Possessive Pronoun/Adjective কী?** `my`, `our`, `your`, `his`, `her`, `their`, `its` - এই শব্দগুলোকে Possessive Adjective বা Determiner বলা হয়। এরা কোনো Noun-এর আগে বসে তার উপর অধিকার বা সম্পর্ক বোঝায়।\r\n\r\n**নিয়ম:** Gerund একটি Noun-এর মতো কাজ করে। তাই, তার আগে যদি কোনো Pronoun বসে, তবে সেই Pronoun-টির Possessive Form (`my`, `your`, `his`, `her`, `their` ইত্যাদি) ব্যবহৃত হয়। Subjective (`he`) বা Objective (`him`) form বসে না।\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- \"She insisted on...\" (সে জোর দিয়েছিল...) - কিসের উপর?\r\n- \"...his leaving the house.\" (...তার বাড়ি ত্যাগ করার উপর।)\r\n\r\nএখানে 'leaving' Gerund-টির কাজটি কে করেছে? উত্তর হলো 'সে' (he)। কিন্তু Gerund-এর আগে আমরা 'he' বা 'him' ব্যবহার করতে পারবো না। নিয়ম অনুযায়ী তার Possessive Form `his` ব্যবহার করতে হবে।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** Gerund-কে একটি Noun মনে করুন। যেমন: \"She insisted on\r\n  **his decision**.\" এখানে decision (noun)-এর আগে যেমন **his** বসেছে, ঠিক তেমনি\r\n  leaving (gerund)-এর আগেও **his** বসবে।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) he:** এটি Subjective Pronoun। Preposition-এর পরে বা Gerund-এর আগে বসে না।\r\n- **(b) him:** এটি Objective Pronoun। যদিও Preposition-এর পরে Objective Pronoun বসে (e.g., \"insisted on him\"), কিন্তু এখানে 'him'-এর পরে আবার একটি Gerund 'leaving' থাকায় নিয়মটি बदलে গেছে।\r\n- **(c) himself:** এটি Reflexive Pronoun। এখানে আত্মবাচক কোনো অর্থ প্রকাশ পাচ্ছে না।\r\n\r\n### আরও উদাহরণ (More Examples)\r\n\r\n- I appreciate **your helping** me. (Not 'you helping')\r\n- We were surprised at **their coming** so late. (Not 'them coming')\r\n- Would you mind **my opening** the window? (Not 'me opening')\r\n\r\nএই নিয়মটি BCS, Primary Teacher, NTRCA এবং অন্যান্য প্রতিযোগিতামূলক পরীক্ষার জন্য অত্যন্ত গুরুত্বপূর্ণ। Gerund-এর আগে pronoun দেখলেই তার possessive form ব্যবহার করুন।";
