export const sourceCode = "---\r\ntitle: \"Interrogative Pronoun 'who' এর ব্যবহার: Tell me who told you that\"\r\ndescription: \"Tell me ____ that - শূন্যস্থানে whom told you, that told you, who told you, told you কোনটি বসবে? Interrogative Pronoun 'who' এর সঠিক ব্যবহার শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"interrogative pronoun who\",\r\n    \"who vs whom\",\r\n    \"embedded question\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n    \"tell me who told you\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: Tell me \\_\\_\\_\\_ that.\r\n\r\n**Question:** Tell me \\_\\_\\_\\_ that.\r\n(a) whom told you\r\n(b) that told you\r\n(c) who told you\r\n(d) told you\r\n\r\n**Correct Answer:** (c) who told you\r\n\r\n### কেন 'who told you' সঠিক উত্তর? (Why is 'who told you' correct?)\r\n\r\nএই বাক্যটি একটি **Embedded Question** (একটি বড় বাক্যের ভেতরে থাকা প্রশ্ন) এর উদাহরণ। এর সঠিক গঠন বুঝতে হলে আমাদের **Interrogative Pronoun** `who` এবং `whom`-এর ব্যবহার জানতে হবে।\r\n\r\n**Key Concept: Subject vs. Object Pronoun in Questions**\r\n\r\n1.  **Who (Subject Pronoun):** যখন প্রশ্নটি বাক্যের **Subject** (কর্তা) সম্পর্কে করা হয়, তখন `who` ব্যবহৃত হয়। `Who` জানতে চায় 'কে' কাজটি করেছে।\r\n\r\n    - _Example:_ **Who** broke the window? (Subject `Who` + Verb `broke`)\r\n\r\n2.  **Whom (Object Pronoun):** যখন প্রশ্নটি বাক্যের **Object** (কর্ম) সম্পর্কে করা হয়, তখন `whom` ব্যবহৃত হয়। `Whom` জানতে চায় কাজটি 'কাকে' করা হয়েছে।\r\n    - _Example:_ **Whom** did you see? (Object `Whom` + Auxiliary `did` + Subject `you` + Verb `see`)\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- Main Clause: \"Tell me...\" (আমাকে বলো...)\r\n- Embedded Question: \"...\\_\\_\\_\\_ told you that.\" (...কে তোমাকে ওটা বলেছে।)\r\n\r\nএই ভেতরের অংশে, `told` verb-টির কাজ কে করেছে? অর্থাৎ, `told`-এর subject কে? উত্তরটি হলো 'কে' বা `who`।\r\n\r\n- **Subject:** `who`\r\n- **Verb:** `told`\r\n- **Indirect Object:** `you`\r\n- **Direct Object:** `that`\r\n\r\nযেহেতু আমাদের `told` verb-টির subject প্রয়োজন, তাই `who` ব্যবহার করতে হবে।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** উত্তরটি `He` বা `She` দিয়ে দেওয়া গেলে প্রশ্ন `Who` দিয়ে\r\n  হবে। উত্তরটি `Him` বা `Her` দিয়ে দেওয়া গেলে প্রশ্ন `Whom` দিয়ে হবে। -\r\n  **Question:** Who told you that? - **Possible Answer:** **He** told me that.\r\n  (উত্তর `He` (subject), তাই প্রশ্ন `Who` দিয়ে)\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) whom told you:** `whom` একটি object pronoun। এটি verb-এর subject হিসেবে বসতে পারে না। `Whom` ব্যবহার করলে বাক্যটি হতো: \"Tell me **whom** you told.\" (তুমি কাকে বলেছিলে)।\r\n- **(b) that told you:** `that` এখানে Interrogative Pronoun হিসেবে ব্যবহৃত হতে পারে না। `that` সাধারণত Relative Pronoun হিসেবে বসে।\r\n- **(d) told you:** এখানে কোনো subject নেই, যা বাক্যটিকে অসম্পূর্ণ করে দিয়েছে। \"Tell me...\" এর পর একটি সম্পূর্ণ clause প্রয়োজন।\r\n\r\n\"Tell me who told you that\" একটি সম্পূর্ণ এবং ব্যাকরণগতভাবে শুদ্ধ বাক্য, যার অর্থ \"আমাকে বলো কে তোমাকে ওটা বলেছে।\"";
