export const sourceCode = "---\r\ntitle: \"Gerund as Object: Why 'teaching' is a Gerund in 'Do you enjoy teaching?'\"\r\ndescription: \"'Do you enjoy teaching?' - এই বাক্যে 'teaching' শব্দটি Gerund কেন? Verb 'enjoy' এর পর Gerund ব্যবহারের নিয়ম এবং Gerund ও Participle এর পার্থক্য শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"gerund as object\",\r\n    \"verbs followed by gerunds\",\r\n    \"enjoy teaching\",\r\n    \"gerund vs participle\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: ‘Do you enjoy teaching?’ The underlined word is a —\r\n\r\n**Question:** ‘Do you enjoy teaching?’ The underlined word is a —\r\n(a) noun\r\n(b) participle\r\n(c) gerund\r\n(d) pronoun\r\n\r\n**Correct Answer:** (c) gerund\r\n\r\n### কেন 'teaching' একটি Gerund? (Why is 'teaching' a Gerund?)\r\n\r\nএই প্রশ্নের উত্তরটি নির্দিষ্ট কিছু verb-এর পর `verb+ing` ব্যবহারের নিয়মের উপর ভিত্তি করে।\r\n\r\n**Key Concept: Gerund as the Object of a Verb**\r\n\r\n1.  **Gerund কী?** Gerund হলো `verb+ing` যুক্ত এমন একটি শব্দ যা বাক্যে **Noun**-এর কাজ করে। এটি বাক্যের subject, object, বা preposition-এর object হিসেবে বসতে পারে।\r\n\r\n2.  **Verbs Followed by Gerunds:** ইংরেজিতে কিছু নির্দিষ্ট verb আছে (যেমন: `enjoy`, `finish`, `avoid`, `mind`, `practice`, `suggest`, `admit`, `deny` ইত্যাদি), যাদের পরে যদি আরেকটি verb আসে, তবে সেই verb-টির সাথে `ing` যোগ করে Gerund হিসেবে ব্যবহার করতে হয়।\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- **Subject:** `you`\r\n- **Verb:** `enjoy`\r\n- **Object:** `teaching`\r\n\r\nএখানে `teaching` শব্দটি `enjoy` verb-টির **object** (কর্ম) হিসেবে কাজ করছে। যদি প্রশ্ন করা হয়, \"Do you enjoy **what**?\" (তুমি কী উপভোগ কর?), উত্তর আসে \"teaching\" (শিক্ষকতা)। যেহেতু `teaching` শব্দটি এখানে একটি Noun-এর কাজ (object হওয়া) করছে, তাই এটি একটি Gerund।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** Main verb-কে 'কী' (what) দিয়ে প্রশ্ন করলে যদি `verb+ing`\r\n  যুক্ত শব্দটি উত্তর হিসেবে আসে, তবে সেটি Gerund। - I finished **writing**.\r\n  (Finished what? -> writing). So, 'writing' is a gerund. - I enjoy\r\n  **swimming**. (Enjoy what? -> swimming). So, 'swimming' is a gerund.\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) noun:** যদিও Gerund একটি verbal noun (ক্রিয়াবাচক বিশেষ্য), কিন্তু `Gerund` শব্দটি এখানে الأكثر সুনির্দিষ্ট এবং সঠিক ব্যাকরণগত পরিভাষা।\r\n- **(b) participle:** Participle একটি Adjective-এর মতো কাজ করে কোনো Noun/Pronoun-কে modify করে। যেমন: \"a **teaching** professional\"। এখানে `teaching` শব্দটি `you`-কে modify করছে না, বরং এটি একটি কাজ যা উপভোগ করা হচ্ছে।\r\n- **(d) pronoun:** Pronoun কোনো Noun-এর পরিবর্তে বসে (e.g., he, she, it)। `teaching` কোনো কিছুর পরিবর্তে বসেনি।\r\n\r\n### আরও উদাহরণ\r\n\r\n- She **avoids eating** junk food.\r\n- Would you **mind closing** the door?\r\n- He **practiced playing** the piano.";
