export const sourceCode = "---\r\ntitle: \"Verb of Perception Rule: 'He watched the boat floating'\"\r\ndescription: \"He watched the boat ___ down the river - কেন floating হলো? Verb of Perception (watch, see, hear) এর পর Present Participle ব্যবহারের নিয়মটি সহজ বাংলায় শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"verb of perception\",\r\n    \"present participle\",\r\n    \"bare infinitive\",\r\n    \"watched the boat floating\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: He watched the boat \\_\\_\\_ down the river.\r\n\r\n**Question:** He watched the boat \\_\\_\\_ down the river.\r\n(a) to float\r\n(b) floating\r\n(c) was floating\r\n(d) had floated\r\n\r\n**Correct Answer:** (b) floating\r\n\r\n### কেন 'floating' সঠিক উত্তর? (Why is 'floating' correct?)\r\n\r\nএই প্রশ্নের উত্তরটি **Verbs of Perception** (অনুভূতিসূচক ক্রিয়া) এর একটি বিশেষ নিয়মের উপর ভিত্তি করে।\r\n\r\n**Key Concept: Rule for Verbs of Perception**\r\n\r\n`see`, `watch`, `hear`, `feel`, `smell`, `notice` - এই verb-গুলোকে Verbs of Perception বলা হয়। এদের পরে যদি কোনো object থাকে, তাহলে পরবর্তী verb-টি দুটি রূপে বসতে পারে:\r\n\r\n1.  **Bare Infinitive (V1):** Verb-এর মূল রূপ (base form), যার আগে `to` বসে না। এটি সম্পূর্ণ কাজটি দেখা বা শোনা বোঝায়।\r\n2.  **Present Participle (V-ing):** Verb-এর সাথে `ing` যুক্ত রূপ। এটি কাজটি চলমান অবস্থায় দেখা বা শোনা বোঝায়।\r\n\r\n**Structure:** Subject + Verb of Perception + Object + **Bare Infinitive (float)** OR **Present Participle (floating)**\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- \"He watched the boat...\" (সে নৌকাটিকে দেখছিল...)।\r\n- সে কী করতে দেখছিল? নদীতে ভেসে যেতে।\r\n- এই 'ভেসে যাওয়া' কাজটি সে চলমান অবস্থায় দেখেছিল। তাই Present Participle `floating` এখানে একটি সঠিক এবং স্বাভাবিক ব্যবহার।\r\n- যদি option-এ `float` (Bare Infinitive) থাকতো, সেটিও সঠিক হতো। কিন্তু `to float` ভুল।\r\n\r\nযেহেতু option-গুলোর মধ্যে `floating` আছে, তাই এটিই সঠিক উত্তর।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** চোখ, কান, নাক, ত্বক দিয়ে সরাসরি অনুভব করা যায় এমন\r\n  verb-গুলোর (see, watch, hear, feel) পর object থাকলে, পরের verb-টির সাথে হয়\r\n  `ing` যোগ হয় অথবা verb-এর base form বসে। `to` বসে না এবং অন্য কোনো Tense (was,\r\n  had) বসে না।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) to float:** Verbs of Perception-এর পরে `to`-infinitive বসে না, Bare Infinitive বসে।\r\n- **(c) was floating:** `watched` এই বাক্যের main verb। এর পরে আরেকটি finite verb (`was floating`) কোনো conjunction ছাড়া বসতে পারে না। এটি 'Run-on sentence' নামক একটি ভুল তৈরি করে।\r\n- **(d) had floated:** এটিও একটি finite verb এবং (c)-এর মতো একই কারণে ভুল।\r\n\r\n### আরও উদাহরণ (More Examples)\r\n\r\n- I saw him **cross** the road. (আমি তাকে রাস্তাটি পার হতে দেখেছিলাম - সম্পূর্ণ ঘটনা)।\r\n- I saw him **crossing** the road. (আমি তাকে রাস্তা পার হওয়ার সময় চলমান অবস্থায় দেখেছিলাম)।\r\n- We heard her **sing** a beautiful song.\r\n- We heard her **singing** a beautiful song.";
