export const sourceCode = "---\r\ntitle: \"Who, Which, What - কোন প্রকারের Pronoun? Relative vs Interrogative\"\r\ndescription: \"Who, Which, What - এই শব্দগুলো কোন প্রকারের Pronoun? Relative Pronoun এবং Interrogative Pronoun এর মধ্যে পার্থক্য ও ব্যবহার সহজ বাংলায় শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"who which what pronoun type\",\r\n    \"relative pronoun\",\r\n    \"interrogative pronoun\",\r\n    \"pronoun types mcq\",\r\n    \"bcs english grammar\",\r\n    \"demonstrative pronoun\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: Who, Which, What are –\r\n\r\n**Question:** Who, Which, What are –\r\n(a) Demonstrative pronoun\r\n(b) Relative Pronoun\r\n(c) Reflexive pronoun\r\n(d) Indefinite pronoun\r\n\r\n**Correct Answer:** (b) Relative Pronoun\r\n\r\n### ব্যাখ্যা (Explanation)\r\n\r\n`Who`, `Which`, `What` শব্দগুলো একাধিক প্রকারের Pronoun হতে পারে, তবে প্রদত্ত option-গুলোর মধ্যে **Relative Pronoun** সবচেয়ে উপযুক্ত। এই শব্দগুলো **Interrogative Pronoun** হিসেবেও ব্যবহৃত হয়, কিন্তু সেটি option-এ নেই।\r\n\r\n---\r\n\r\n### 1. Relative Pronoun হিসেবে ব্যবহার\r\n\r\n**Key Concept:** Relative Pronoun দুটি বাক্য বা clause-কে যুক্ত করে এবং তার পূর্ববর্তী Noun বা Pronoun (Antecedent)-কে নির্দেশ করে।\r\n\r\n- **Who:** ব্যক্তির পরিবর্তে বসে।\r\n  - _Example:_ The man **who** called you is my brother. (এখানে `who` শব্দটি `The man`-কে নির্দেশ করছে এবং দুটি বাক্যকে যুক্ত করছে।)\r\n- **Which:** বস্তু বা ইতর প্রাণীর পরিবর্তে বসে।\r\n  - _Example:_ This is the car **which** I bought yesterday.\r\n- **What:** যখন কোনো Antecedent থাকে না, তখন `what` ব্যবহৃত হয় (এর অর্থ 'that which')।\r\n  - _Example:_ I know **what** you want.\r\n\r\nযেহেতু এই শব্দগুলো দুটি বাক্যকে relate বা সম্পর্কযুক্ত করতে ব্যবহৃত হয়, তাই এদের Relative Pronoun বলে।\r\n\r\n---\r\n\r\n### 2. Interrogative Pronoun হিসেবে ব্যবহার\r\n\r\n**Key Concept:** Interrogative Pronoun প্রশ্ন জিজ্ঞাসা করতে ব্যবহৃত হয়।\r\n\r\n- **Who:** ব্যক্তি সম্পর্কে প্রশ্ন করতে ব্যবহৃত হয়।\r\n  - _Example:_ **Who** are you?\r\n- **Which:** নির্দিষ্ট সংখ্যক বিকল্পের মধ্যে থেকে একটি পছন্দ করতে ব্যবহৃত হয়।\r\n  - _Example:_ **Which** is your favorite color?\r\n- **What:** általános তথ্য জানতে ব্যবহৃত হয়।\r\n  - _Example:_ **What** is your name?\r\n\r\nযেহেতু প্রশ্নে `Who, Which, What` এর একটি সাধারণ শ্রেণিবিভাগ জানতে চাওয়া হয়েছে এবং option-এ `Relative Pronoun` আছে, তাই এটিই সবচেয়ে সঠিক উত্তর। অনেক সময় এই দুটি (Relative ও Interrogative) -কে একসাথে `Relative Pronoun`-এর broader category-তে আলোচনা করা হয়।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** - যদি `Who/Which/What` দুটি বাক্যকে **যুক্ত করে**, তবে\r\n  এটি **Relative Pronoun**। - যদি `Who/Which/What` দিয়ে **প্রশ্ন শুরু হয়**, তবে\r\n  এটি **Interrogative Pronoun**।\r\n</Callout>\r\n\r\n### অন্যান্য Option-গুলো কেন ভুল?\r\n\r\n- **(a) Demonstrative pronoun:** এরা নির্দিষ্ট কিছু নির্দেশ করে। যেমন: `this`, `that`, `these`, `those`।\r\n- **(c) Reflexive pronoun:** এদের শেষে `self` বা `selves` থাকে এবং কাজটি कर्ताর দিকে ফিরে আসে বোঝায়। যেমন: `myself`, `himself`, `themselves`।\r\n- **(d) Indefinite pronoun:** এরা অনির্দিষ্ট ব্যক্তি বা বস্তুকে বোঝায়। যেমন: `someone`, `anything`, `nobody`, `all`, `some`।";
