export const sourceCode = "import { Callout } from \"nextra/components\";\r\n\r\n# Gerund as Object of a Preposition: \"receiving\"\r\n\r\n**প্রশ্ন:** Giving is better than receiving. Here the ‘receiving’ is a/an—\r\n\r\n(a) common noun\r\n(b) adjective\r\n(c) gerund\r\n(d) present participle\r\n\r\n**সঠিক উত্তর:** (c) gerund\r\n\r\n---\r\n\r\n### **ব্যাখ্যা**\r\n\r\nএই বাক্যে `receiving` শব্দটি একটি Gerund। Gerund হলো verb-এর `-ing` যুক্ত রূপ যা বাক্যে Noun-এর মতো কাজ করে।\r\n\r\n#### **Key Concepts:**\r\n\r\n1.  **Gerund:** Verb + ing = Noun.\r\n    - Gerund বাক্যে Subject, Object, Object of a preposition, বা Complement হিসেবে কাজ করতে পারে।\r\n2.  **Present Participle:** Verb + ing = Adjective or part of a continuous verb.\r\n    - Present Participle কোনো Noun-কে modify করে বা continuous tense গঠনে সাহায্য করে।\r\n\r\n#### **বিশ্লেষণ:**\r\n\r\n- **`Giving`**: এই শব্দটি বাক্যের Subject হিসেবে কাজ করছে। যেহেতু এটি verb (`give`) থেকে তৈরি এবং noun-এর কাজ করছে, তাই এটি একটি Gerund।\r\n- **`receiving`**: এই শব্দটি `than` preposition-এর Object হিসেবে কাজ করছে। Preposition-এর পরে যখন কোনো verb আসে, তখন সেটি সাধারণত Gerund (verb + ing) হয়।\r\n\r\n#### **Gerund vs. Present Participle পার্থক্য:**\r\n\r\n- **Gerund (Noun):**\r\n  - **`Swimming`** is a good exercise. (Subject)\r\n  - I like **`reading`**. (Object)\r\n  - He is fond of **`painting`**. (Object of preposition 'of')\r\n- **Present Participle (Adjective/Verb):**\r\n  - I saw a **`swimming`** snake. (Adjective, 'snake'-কে modify করছে)\r\n  - He is **`reading`** a book. (Continuous tense-এর অংশ)\r\n\r\nআমাদের প্রশ্নে, `Giving is better than receiving`-এ `Giving` এবং `receiving` উভয়ই কাজ বা ක්‍රিয়ার নাম বোঝাচ্ছে, তাই তারা Noun। যেহেতু তারা verb থেকে উদ্ভূত, তাই তারা Gerund।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Memorize:** কোনো `verb+ing` যুক্ত শব্দকে যদি `it` বা `something`\r\n  দিয়ে প্রতিস্থাপন করা যায় এবং বাক্যটি অর্থপূর্ণ থাকে, তবে সেটি Gerund। *\r\n  **Giving** is better than **receiving**. -> **Something** is better than\r\n  **something**. এই প্রতিস্থাপনটি এখানে কাজ করছে, যা নিশ্চিত করে যে `receiving`\r\n  একটি Gerund।\r\n</Callout>\r\n\r\nএই ধরনের প্রশ্ন Verbals (Gerund, Participle, Infinitive) থেকে আসে এবং এদের পার্থক্য বোঝা খুবই জরুরি।";
