export const sourceCode = "---\r\ntitle: \"Perfect Participle Explained: 'I went out having forgotten him'\"\r\ndescription: \"'I went out having forgotten him.' - এই বাক্যে 'having forgotten' অংশটি Perfect Participle কেন? Present, Past, এবং Perfect Participle এর পার্থক্য শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"perfect participle\",\r\n    \"having forgotten\",\r\n    \"types of participles\",\r\n    \"participle phrase\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n    \"having v3\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: I went out having forgotten him. The underlined part is —\r\n\r\n**Question:** I went out having forgotten him. The underlined part is —\r\n(a) gerund\r\n(b) verbal noun\r\n(c) perfect participle\r\n(d) present participle\r\n\r\n**Correct Answer:** (c) perfect participle\r\n\r\n### কেন এটি একটি Perfect Participle?\r\n\r\nএই প্রশ্নের উত্তরটি Participle-এর বিভিন্ন প্রকারভেদ বোঝার উপর নির্ভরশীল।\r\n\r\n**Key Concept: Types of Participles**\r\n\r\nParticiple প্রধানত তিন প্রকার:\r\n\r\n1.  **Present Participle:**\r\n\r\n    - **Structure:** `Verb + ing` (e.g., `going`, `seeing`, `forgetting`)\r\n    - **Function:** চলমান কাজ বোঝায়।\r\n    - _Example:_ **Seeing** the snake, he ran away.\r\n\r\n2.  **Past Participle:**\r\n\r\n    - **Structure:** `Verb` এর 3rd form (V3) (e.g., `gone`, `seen`, `forgotten`)\r\n    - **Function:** সম্পন্ন কাজ বা passive state বোঝায়।\r\n    - _Example:_ **Driven** by hunger, he stole the bread.\r\n\r\n3.  **Perfect Participle:**\r\n    - **Structure:** `Having + V3` (Past Participle)\r\n    - **Function:** একটি কাজ অন্য একটি কাজের **পূর্বে সম্পন্ন হয়েছে** তা স্পষ্টভাবে বোঝানোর জন্য ব্যবহৃত হয়।\r\n    - _Example:_ **Having finished** the work, I went home. ( কাজটি শেষ করে, আমি বাড়ি গিয়েছিলাম।)\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- **Underlined Part:** `having forgotten him`\r\n- **Structure:** `Having` + `forgotten` (forget-এর V3)\r\n- এই গঠনটি সরাসরি **Perfect Participle**-এর সংজ্ঞার সাথে মিলে যায়।\r\n- **Meaning:** বাক্যটি বোঝাচ্ছে যে, তাকে ভুলে যাওয়ার কাজটি সম্পন্ন হওয়ার পর আমি বাইরে গিয়েছিলাম। (First action: forgetting, Second action: going out).\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** যখনই কোনো বাক্যে `Having` + Verb-এর Past Participle (V3)\r\n  দেখবেন, সেটি একটি Perfect Participle। এর সহজ অর্থ হলো \" কাজটি করার পর...\"।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) gerund:** Gerund একটি Noun-এর কাজ করে। `having forgotten him` phrase-টি এখানে `I` pronoun-টিকে modify করছে, এটি কোনো Noun নয়।\r\n- **(b) verbal noun:** Verbal Noun সাধারণত `The + verb-ing + of` এই গঠনে থাকে (e.g., The **reading of** the book)। এটি এখানে প্রাসঙ্গিক নয়।\r\n- **(d) present participle:** Present Participle শুধু `verb+ing` (e.g., `forgetting`) হয়। `Having forgotten` একটি ভিন্ন গঠন।\r\n\r\n### Perfect Participle-এর আরও উদাহরণ\r\n\r\n- **Having completed** the course, he applied for a job.\r\n- **Having been warned** before, she was more careful this time. (Passive Perfect Participle)";
