export const sourceCode = "---\r\ntitle: \"Tense Sequence: 'criminals, who had escaped' - Past Perfect Tense\"\r\ndescription: \"An intensive search was conducted... to locate those criminals, who ____ - শূন্যস্থানে had escaped, have had escaped কোনটি বসবে? Tense Sequence এর নিয়ম শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"sequence of tenses\",\r\n    \"past perfect tense\",\r\n    \"two actions in the past\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n    \"criminals who had escaped\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: An intensive search was conducted by the detective to locate those criminals, who \\_\\_\\_\\_\r\n\r\n**Question:** An intensive search was conducted by the detective to locate those criminals, who \\_\\_\\_\\_\r\n(a) have had escaped\r\n(b) had escaped\r\n(c) are escaping\r\n(d) have been escaping\r\n\r\n**Correct Answer:** (b) had escaped\r\n\r\n### কেন 'had escaped' সঠিক উত্তর? (Why is 'had escaped' correct?)\r\n\r\nএই প্রশ্নের উত্তরটি **Sequence of Tenses** (টেন্সের ধারাবাহিকতা) নিয়মের উপর ভিত্তি করে, বিশেষ করে অতীতে সংঘটিত দুটি কাজের সম্পর্ক বোঝানোর ক্ষেত্রে।\r\n\r\n**Key Concept: Past Perfect for the Earlier of Two Past Actions**\r\n\r\nযখন অতীতে দুটি কাজ সম্পন্ন হয়েছিল, তখন দুটি কাজের মধ্যে যেটি **আগে** ঘটেছিল, সেটি **Past Perfect Tense** (`had` + V3) হয় এবং যেটি **পরে** ঘটেছিল, সেটি **Simple Past Tense** (V2) হয়।\r\n\r\n**বাক্য বিশ্লেষণ:**\r\nএখানে দুটি কাজ অতীতে ঘটেছে:\r\n\r\n1.  **Action 1:** অপরাধীরা পালিয়ে গিয়েছিল (The criminals escaped)।\r\n2.  **Action 2:** ডিটেকটিভ একটি নিবিড় অনুসন্ধান চালিয়েছিল (An intensive search was conducted)।\r\n\r\nকোন কাজটি আগে ঘটেছে? অবশ্যই, অপরাধীরা **আগে পালিয়ে গিয়েছিল**, এবং তারপরে তাদের খুঁজে বের করার জন্য অনুসন্ধান চালানো হয়েছিল।\r\n\r\n- **Earlier Action (আগের কাজ):** The criminals escaped. -> এটি Past Perfect Tense হবে: `had escaped`।\r\n- **Later Action (পরের কাজ):** An intensive search was conducted. -> এটি Simple Past Tense-এ আছে, যা সঠিক।\r\n\r\nঅতএব, শূন্যস্থানে `had escaped` বসবে।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** অতীতে দুটি কাজের কথা বলা হলে, নিজেকে প্রশ্ন করুন কোনটি\r\n  আগে ঘটেছে। যেটি বেশি পুরোনো বা বেশি অতীত, সেটির সাথে `had` বসবে। (The patient\r\n  **had died** before the doctor **came**.)\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) have had escaped:** এই গঠনটি व्याकरणগতভাবে ভুল। `have had` সাধারণত Past Perfect Tense-এর একটি ভিন্ন রূপে ব্যবহৃত হয়, কিন্তু এখানে `have` এবং `had` একসাথে এভাবে বসে না।\r\n- **(c) are escaping:** এটি Present Continuous Tense। মূল বাক্যটি (`was conducted`) Past Tense-এ থাকায় Present Tense এখানে ব্যবহার করা যাবে না।\r\n- **(d) have been escaping:** এটি Present Perfect Continuous Tense। এটিও Present Tense হওয়ায় মূল বাক্যের Past Tense-এর সাথে মিলছে না।\r\n\r\nসঠিক বাক্যটি হলো: \"An intensive search was conducted by the detective to locate those criminals, who **had escaped**.\"\r\n(ডিটেকটিভ সেই অপরাধীদের খুঁজে বের করার জন্য একটি নিবিড় অনুসন্ধান চালিয়েছিল, যারা পালিয়ে গিয়েছিল।)";
