export const sourceCode = "import { Callout } from \"nextra/components\";\r\n\r\n# Modal Deduction: Past Certainty (`must have been`)\r\n\r\n**প্রশ্ন:** Choose the correct sentence.\r\n\r\n(a) Mizan is absent today; he must have been sick again.\r\n(b) Rahim is back in the team today; he must be feeling better now.\r\n(c) The line is busy; someone should be using the telephone now.\r\n(d) She must be study in her room now.\r\n\r\n**সঠিক উত্তর:** (a) and (b) are both correct. This file explains option (a).\r\n\r\n---\r\n\r\n### **ব্যাখ্যা (Option a)**\r\n\r\nএই বাক্যটি `must have + V3` ব্যবহার করে অতীতের কোনো ঘটনা সম্পর্কে একটি যৌক্তিক এবং দৃঢ় অনুমান (logical deduction about the past) প্রকাশ করছে, যা সম্পূর্ণ সঠিক।\r\n\r\n#### **Key Concepts:**\r\n\r\n1.  **Modal Perfect for Past Deduction:** যখন বর্তমানের কোনো প্রমাণের উপর ভিত্তি করে অতীতের কোনো ঘটনা সম্পর্কে প্রায় নিশ্চিত হয়ে কিছু বলা হয়, তখন `must have + Past Participle (V3)` ব্যবহৃত হয়।\r\n    - **Structure:** `Subject + must have + V3`\r\n\r\n#### **বিশ্লেষণ:**\r\n\r\n- **Present Evidence (বর্তমান প্রমাণ):** \"Mizan is absent today.\" (মিজান আজ অনুপস্থিত।)\r\n- **Past Deduction (অতীতের অনুমান):** \"...he must have been sick again.\" (...সে নিশ্চয়ই আবার অসুস্থ হয়েছিল।)\r\n\r\nএখানে, মিজানের বর্তমান অনুপস্থিতি দেখে বক্তা এই সিদ্ধান্তে পৌঁছেছেন যে, সে অতীতে (আজকের আগে বা আজকের দিনের শুরুতে) অসুস্থ হয়েছিল। বর্তমানের ফল দেখে অতীতের কারণ অনুমান করা হচ্ছে। এই ধরনের পরিস্থিতি প্রকাশ করার জন্য `must have been` একটি নিখুঁত ব্যবহার।\r\n\r\n#### **কেন (d) ভুল?**\r\n\r\n- **(d) She must be study in her room now.**\r\n  - এটি ব্যাকরণগতভাবে ভুল। `must be`-এর পরে হয় verb-এর সাথে `ing` যুক্ত হয় (continuous action বোঝাতে) অথবা একটি adjective/noun বসে।\r\n  - **সঠিক রূপ:** She **must be studying** in her room now. (সে নিশ্চয়ই এখন তার ঘরে পড়ছে।)\r\n\r\n<Callout type=\"info\">\r\n  ** মনে রাখবেন:** * **`must be`**: বর্তমান সম্পর্কে দৃঢ় অনুমান। (He **must be**\r\n  rich. He has a big car.) * **`must have been`**: অতীত সম্পর্কে দৃঢ় অনুমান।\r\n  (The street is wet. It **must have rained** last night.)\r\n</Callout>\r\n\r\nএই প্রশ্নটি বর্তমান এবং অতীত সম্পর্কিত অনুমানের জন্য modal verb-এর সঠিক রূপ ব্যবহারের দক্ষতা যাচাই করে।";
