export const sourceCode = "import { Callout } from \"nextra/components\";\r\n\r\n# Modal Deduction: Present Continuous (`must be + V-ing`)\r\n\r\n**প্রশ্ন:** Choose the correct sentence.\r\n\r\n(a) Mizan is absent today; he must have been sick again.\r\n(b) Rahim is back in the team today; he must be feeling better now.\r\n(c) The line is busy; someone should be using the telephone now.\r\n(d) She must be study in her room now.\r\n\r\n**সঠিক উত্তর:** (a) and (b) are both correct. This file explains option (b).\r\n\r\n---\r\n\r\n### **ব্যাখ্যা (Option b)**\r\n\r\nএই বাক্যটি `must be + V-ing` ব্যবহার করে বর্তমান মুহূর্তে চলমান কোনো ক্রিয়া বা অবস্থা সম্পর্কে একটি দৃঢ় অনুমান (logical deduction about a present continuous action) প্রকাশ করছে, যা সম্পূর্ণ সঠিক।\r\n\r\n#### **Key Concepts:**\r\n\r\n1.  **Modal for Present Continuous Deduction:** যখন কোনো প্রমাণের উপর ভিত্তি করে বক্তা প্রায় নিশ্চিত হন যে বর্তমানে কোনো কাজ চলছে বা কোনো অবস্থা বিরাজমান, তখন `must be + Verb-ing` ব্যবহৃত হয়।\r\n    - **Structure:** `Subject + must be + V-ing`\r\n\r\n#### **বিশ্লেষণ:**\r\n\r\n- **Present Evidence (বর্তমান প্রমাণ):** \"Rahim is back in the team today.\" (রহিম আজ দলে ফিরেছে।)\r\n- **Present Deduction (বর্তমান অনুমান):** \"...he must be feeling better now.\" (...সে নিশ্চয়ই এখন ভালো বোধ করছে।)\r\n\r\nএখানে, রহিমের দলে ফেরাটা প্রমাণ করে যে সে বর্তমানে (now) সুস্থ বোধ করছে। এটি একটি চলমান অবস্থা, তাই `feeling` (V-ing) ব্যবহৃত হয়েছে। বক্তা তার বর্তমান অবস্থা সম্পর্কে প্রায় নিশ্চিত, তাই `must be` ব্যবহার করা হয়েছে।\r\n\r\n#### **কেন (c) এবং (d) ভুল?**\r\n\r\n- **(c) The line is busy; someone should be using the telephone now.**\r\n  - এখানে `should be using` ব্যবহার করা হয়েছে, যা একটি প্রত্যাশা (expectation) বোঝায়, দৃঢ় অনুমান নয়। যদিও বাক্যটি ব্যাকরণগতভাবে ভুল নয়, তবে `must be using` (কেউ নিশ্চয়ই ব্যবহার করছে) এই প্রসঙ্গে আরও বেশি যৌক্তিক এবং স্বাভাবিক। `must` এখানে `should`-এর চেয়ে শক্তিশালী অনুমান প্রকাশ করে।\r\n- **(d) She must be study in her room now.**\r\n  - এটি ব্যাকরণগতভাবে ভুল। `must be`-এর পরে verb-এর base form (`study`) বসে না।\r\n  - **সঠিক রূপ:** She **must be studying** in her room now.\r\n\r\n<Callout type=\"info\">\r\n  **Deduction Modals Summary:** * **Present State:** `must be + adjective/noun`\r\n  (He **must be** tired.) * **Present Action:** `must be + V-ing` (He **must be\r\n  sleeping**.) * **Past State/Action:** `must have + V3` (He **must have slept**\r\n  well.)\r\n</Callout>\r\n\r\nএই প্রশ্নটি বর্তমান ও অতীত অনুমান প্রকাশের জন্য modal verb-এর বিভিন্ন রূপের সঠিক ব্যবহার যাচাই করে।";
