export const sourceCode = "---\r\ntitle: Remove শব্দটির Noun Form কোনটি? | BCS English Grammar Tips\r\ndescription: Learn the correct noun form of the word 'Remove' with easy Bangla explanation, tricks, and tips for BCS and Primary School Teacher exam preparation.\r\nkeywords: Remove noun form, Removal meaning, English Grammar in Bangla, BCS English Grammar, Primary Teacher English, Remove to Removal, Parts of Speech tricks\r\n---\r\n\r\n# ✨ Remove শব্দটির Noun Form কোনটি?\r\n\r\nA. Remove  \r\nB. Removement  \r\nC. Removal ✅  \r\nD. Removing\r\n\r\n---\r\n\r\n## ✅ Correct Answer: C. Removal\r\n\r\n---\r\n\r\n## 💡 Concept: Verb থেকে Noun তৈরি করা (Nominalization)\r\n\r\nইংরেজিতে অনেক Verb আছে যেগুলো থেকে **Noun** তৈরি করা যায় **-al, -tion, -ment, -ing** ইত্যাদি suffix যোগ করে।\r\n\r\n| Verb    | Noun Form   | Bangla Meaning        |\r\n| ------- | ----------- | --------------------- |\r\n| Remove  | Removal     | অপসারণ, সরানো         |\r\n| Approve | Approval    | অনুমোদন               |\r\n| Develop | Development | উন্নয়ন                |\r\n| Employ  | Employment  | নিয়োগ                 |\r\n| Read    | Reading     | পড়া (ক্রিয়া থেকে নাম) |\r\n\r\n---\r\n\r\n## 🧠 Trick (Hack) to Memorize Easily\r\n\r\n1. **-al / -tion / -ment endings = Usually Noun**\r\n\r\n   - Remove → Removal\r\n   - Approve → Approval\r\n   - Develop → Development\r\n\r\n2. **-ing = Mostly Verb/Noun (gerund)**\r\n\r\n   - Removing → “ক্রিয়া বোঝাচ্ছে” → Verb/Noun (Gerund)\r\n   - কিন্তু standard noun form হলো **Removal**\r\n\r\n3. **Removement ❌**\r\n   - ইংরেজিতে সাধারণভাবে এই word ব্যবহার হয় না → ভুল উত্তর।\r\n\r\n---\r\n\r\n## 🌿 Grammar Tip (BCS/Primary Teacher Exam)\r\n\r\n- যদি প্রশ্ন হয়: **“Verb এর Noun Form কোনটি?”**, তাহলে suffix দেখে চিনে নিতে হবে।\r\n- সাধারণত, **-al / -tion / -ment** suffix **Noun** বানায়।\r\n- **-ing** ব্যবহার করলে সেটা Gerund, অর্থাৎ ক্রিয়ার নাম, কিন্তু standard noun নয়।\r\n\r\n---\r\n\r\n## ✍️ Shortcut Technique (Exam Hack)\r\n\r\n🔹 যদি Verb এর শেষে “-al / -tion / -ment” যোগ করা যায় → সম্ভাব্য Noun।  \r\n🔹 **-ing** দেখা গেলে → ক্রিয়া (Action) বোঝাচ্ছে, সব সময় Standard Noun নয়।  \r\n🔹 অদ্ভুত word যেমন “Removement” → dictionary এ check করতে হবে, exam-এ প্রায় ব্যবহার হয় না।\r\n\r\n---\r\n\r\n## 📚 Practice Words (Nominalization)\r\n\r\n| Verb     | Correct Noun Form |\r\n| -------- | ----------------- |\r\n| Approve  | Approval          |\r\n| Employ   | Employment        |\r\n| Remove   | Removal           |\r\n| Organize | Organization      |\r\n| Inform   | Information       |\r\n\r\n💡 এই টেকনিক মনে রাখলে BCS এবং Primary Teacher পরীক্ষার Verb→Noun প্রশ্ন খুব সহজে solve করা যাবে।\r\n\r\n---\r\n\r\n## 🎯 Final Takeaway\r\n\r\n- **Remove → Removal**\r\n- **-al / -tion / -ment** suffix দিয়ে Verb থেকে Standard Noun বানানো যায়।\r\n- **-ing** suffix দিয়ে Gerund তৈরি হয়, সব সময় standard noun নয়।\r\n- ভুল option যেমন **Removement** প্রায় কখনো সঠিক নয়।\r\n\r\n---\r\n\r\n**Tags:**  \r\nBCS English Grammar, Primary School Teacher English, Verb to Noun, Nominalization in English, Removal meaning Bangla, Parts of Speech Tips, Easy English Tricks Bangla";
