export const sourceCode = "---\r\ntitle: \"Correct Sentence: 'in hospital' vs 'in a hospital', 'for' vs 'during'\"\r\ndescription: \"My father was in hospital for six weeks during the summer - কেন এই বাক্যটি শুদ্ধ? Article এবং Preposition of Time এর সঠিক ব্যবহার শিখুন। BCS, Primary পরীক্ষার জন্য গুরুত্বপূর্ণ।\"\r\nkeywords:\r\n  [\r\n    \"in hospital vs in a hospital\",\r\n    \"for vs during\",\r\n    \"preposition of time\",\r\n    \"english grammar correction\",\r\n    \"bcs english\",\r\n    \"correct sentence structure\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: Which of the following sentences is correct one?\r\n\r\n**Question:** Which of the following sentences is correct one?\r\n(a) My father was in hospital during six weeks in summer.\r\n(b) In summer during six weeks my father was in hospital.\r\n(c) My father was in a hospital during six weeks in summer.\r\n(d) My father was in hospital for six weeks during the summer.\r\n\r\n**Correct Answer:** (d) My father was in hospital for six weeks during the summer.\r\n\r\n### কেন (d) সঠিক উত্তর? (Why is (d) correct?)\r\n\r\nএই বাক্যটির শুদ্ধতা দুটি প্রধান বিষয়ের উপর নির্ভরশীল:\r\n\r\n1.  **Article-এর ব্যবহার:** `in hospital` বনাম `in a hospital`।\r\n2.  **Preposition of Time-এর ব্যবহার:** `for` বনাম `during`।\r\n\r\n---\r\n\r\n### 1. Article Usage: `in hospital` vs. `in a/the hospital`\r\n\r\n**Key Concept:** যখন `hospital`, `school`, `college`, `church`, `prison` ইত্যাদি স্থান তাদের মূল উদ্দেশ্যে (e.g., হাসপাতালে চিকিৎসার জন্য, স্কুলে পড়ার জন্য) ব্যবহৃত হয়, তখন British English-এ এদের আগে কোনো Article (a, an, the) বসে না। American English-এ `the` ব্যবহৃত হয় (`in the hospital`)।\r\n\r\n- **in hospital (BrE) / in the hospital (AmE):** রোগী হিসেবে হাসপাতালে ভর্তি থাকা।\r\n  - _Example:_ He is **in hospital** with a broken leg.\r\n- **in a/the hospital:** কোনো দর্শনার্থী হিসেবে বা অন্য কোনো কাজে হাসপাতালের বিল্ডিংয়ের ভেতরে থাকা।\r\n  - _Example:_ I went to **the hospital** to visit my friend.\r\n\r\nযেহেতু বাক্যে বাবা অসুস্থ হয়ে হাসপাতালে ছিলেন বোঝাচ্ছে, তাই `in hospital` (BrE style) বা `in the hospital` (AmE style) সঠিক। Option (c)-তে `in a hospital` ব্যবহারটি এখানে ভুল, কারণ এটি নির্দিষ্ট নয় এবং রোগী হিসেবে থাকাকে বোঝাচ্ছে না।\r\n\r\n---\r\n\r\n### 2. Preposition of Time: `for` vs. `during`\r\n\r\n**Key Concept:**\r\n\r\n- **For:** একটি নির্দিষ্ট সময়কাল বা المدة (period of time) বোঝাতে ব্যবহৃত হয়। যেমন: `for three days`, `for two years`, `for six weeks`।\r\n- **During:** একটি নির্দিষ্ট ঘটনার বা সময়কালের মধ্যে 'কখন' কিছু ঘটেছিল তা বোঝাতে ব্যবহৃত হয়। এর পর সাধারণত একটি Noun থাকে। যেমন: `during the meeting`, `during the night`, `during the summer`।\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- \"six weeks\" একটি সময়কাল (period)। তাই এর আগে `for` বসবে। `during six weeks` ব্যবহারটি ভুল।\r\n- \"the summer\" একটি নির্দিষ্ট সময়কাল বা ঘটনা। এর মধ্যে ছয় সপ্তাহ তিনি হাসপাতালে ছিলেন। তাই `during the summer` সঠিক।\r\n\r\n**সঠিক সমন্বয়:**\r\n\r\n- সময়কাল বোঝাতে: `for six weeks`\r\n- কখন ঘটেছিল বোঝাতে: `during the summer`\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** - `for` + a period of time (e.g., for 2 hours, for 5\r\n  days). - `during` + a noun (e.g., during the film, during the holiday). - `in\r\n  hospital` = রোগী হিসেবে থাকা।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) ...during six weeks...:** সময়কালের আগে `during` ভুল। `for` হবে।\r\n- **(b) In summer during six weeks...:** এখানেও `during six weeks` ভুল এবং বাক্যের গঠন দুর্বল।\r\n- **(c) ...in a hospital...:** `a hospital` ব্যবহারটি এখানে ভুল, কারণ রোগী হিসেবে থাকার অর্থ প্রকাশে `in hospital` বা `in the hospital` ব্যবহৃত হয়।\r\n\r\nOption (d) এই সব নিয়ম সঠিকভাবে অনুসরণ করে, তাই এটিই শুদ্ধ বাক্য।";
