export const sourceCode = "---\r\ntitle: \"Correct Sentence: 'The man who said that was a fool'\"\r\ndescription: \"The man who said that was a fool - কেন এই বাক্যটি শুদ্ধ? Relative Pronoun 'who' এবং 'whom' এর সঠিক ব্যবহার শিখুন। BCS, Primary পরীক্ষার জন্য গুরুত্বপূর্ণ।\"\r\nkeywords:\r\n  [\r\n    \"the man who said that\",\r\n    \"who vs whom\",\r\n    \"subject relative pronoun\",\r\n    \"english grammar correction\",\r\n    \"bcs english\",\r\n    \"correct sentence\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: Choose the correct sentence –\r\n\r\n**Question:** Choose the correct sentence –\r\n(a) The man that said was a fool.\r\n(b) The man whom said that was a fool.\r\n(c) The man who said that was a fool.\r\n(d) The man which said was a fool.\r\n\r\n**Correct Answer:** (c) The man who said that was a fool.\r\n\r\n### কেন 'who' সঠিক উত্তর? (Why is 'who' correct?)\r\n\r\nএই প্রশ্নের উত্তরটি **Relative Pronoun** `who` এবং `whom`-এর সঠিক ব্যবহারের উপর নির্ভরশীল।\r\n\r\n**Key Concept: Subject vs. Object Relative Pronoun**\r\n\r\n1.  **Antecedent:** Relative Pronoun-টির পূর্ববর্তী Noun হলো `The man`, যা একজন ব্যক্তি। ব্যক্তির জন্য আমরা `who`, `whom`, বা `that` ব্যবহার করতে পারি।\r\n\r\n2.  **`who` (Subject Pronoun):** যখন Relative Pronoun-টি ভেতরের clause-এর **subject** (কর্তা) হিসেবে কাজ করে, তখন `who` বসে।\r\n3.  **`whom` (Object Pronoun):** যখন Relative Pronoun-টি ভেতরের clause-এর **object** (কর্ম) হিসেবে কাজ করে, তখন `whom` বসে।\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- **Main Clause:** \"The man ... was a fool.\"\r\n- **Relative Clause:** \"\\_\\_\\_\\_ said that\"\r\n\r\nএই Relative Clause-টিতে `said` (বলেছিল) verb-টির কাজ কে করেছে? অর্থাৎ, `said` verb-টির subject কে? উত্তরটি হলো `The man`। যেহেতু Relative Pronoun-টি `The man`-এর পরিবর্তে clause-টির subject হিসেবে বসছে, তাই আমাদের Subject Pronoun `who` ব্যবহার করতে হবে।\r\n\r\n**Structure:** The man (`subject` -> `who`) said that...\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** Relative Pronoun-এর পরে যদি সরাসরি একটি **verb** (যেমন:\r\n  said, is, went, has) থাকে, তাহলে `who` বসে। আর যদি pronoun-এর পরে একটি\r\n  **subject** (যেমন: you, he, they) থাকে, তাহলে `whom` বসে। - `...who\r\n  **said**...` (verb `said` আছে, তাই `who`) - `...whom **you** met...` (subject\r\n  `you` আছে, তাই `whom`)\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) The man that said was a fool:** যদিও `that` ব্যক্তির পরিবর্তে বসতে পারে, কিন্তু `that`-এর পরে `that` (The man that said **that**...) পুনরাবৃত্তিমূলক শোনায় এবং `who` এখানে الأكثر স্বাভাবিক এবং পছন্দের বিকল্প। তবে কিছু ক্ষেত্রে এটি অশুদ্ধ নয়, কিন্তু `who` বেশি সঠিক। _Note: The man that said that..._ is awkward. The question implies \"The man who said [the thing] was a fool\". The first 'that' is the relative pronoun, the second is the object. So, \"The man that said that was a fool\" is technically possible but less common than using 'who'. However, the option given is \"The man that said was a fool\", which is missing an object for 'said', making it incomplete.\r\n\r\n- **(b) The man whom said that was a fool:** `whom` একটি Object Pronoun। এটি verb-এর subject হিসেবে বসতে পারে না। তাই এটি ভুল।\r\n\r\n- **(d) The man which said was a fool:** `which` শুধুমাত্র বস্তু বা ইতর প্রাণীর জন্য ব্যবহৃত হয়। `The man` ব্যক্তি হওয়ায় `which` ব্যবহার করা যাবে না।\r\n\r\nঅতএব, `The man who said that was a fool` বাক্যটিই ব্যাকরণগতভাবে সম্পূর্ণ শুদ্ধ।";
