export const sourceCode = "---\r\ntitle: \"Correct Sentence: 'Let you and him be witnesses' - Objective Pronoun\"\r\ndescription: \"Let you and him be witnesses - কেন এই বাক্যটি শুদ্ধ? 'Let' এর পর Pronoun এর Objective Form ব্যবহারের নিয়মটি সহজ বাংলায় শিখুন। BCS, Primary পরীক্ষার জন্য গুরুত্বপূর্ণ।\"\r\nkeywords:\r\n  [\r\n    \"let you and him\",\r\n    \"objective pronoun after let\",\r\n    \"correct sentence structure\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n    \"pronoun case\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: Choose the correct sentence –\r\n\r\n**Question:** Choose the correct sentence –\r\n(a) Let he and you be witnesses.\r\n(b) Let you and him be witnesses.\r\n(c) Let you and he be witnesses.\r\n(d) Let him and you be witnesses.\r\n\r\n**Correct Answer:** (b) Let you and him be witnesses. and (d) Let him and you be witnesses.\r\n_(Note: Both (b) and (d) are grammatically correct, but (b) follows the rule of pronoun order more commonly. Let's analyze why.)_\r\n\r\n### কেন (b) এবং (d) সঠিক? (Why are (b) and (d) correct?)\r\n\r\nএই প্রশ্নের মূলে রয়েছে দুটি গুরুত্বপূর্ণ নিয়ম:\r\n\r\n1.  `Let`-এর পরে Pronoun-এর কোন Case বসে।\r\n2.  একাধিক Pronoun একসাথে বসলে তাদের ক্রম (Order) কী হয়।\r\n\r\n---\r\n\r\n### 1. Pronoun Case after 'Let'\r\n\r\n**Key Concept:** `Let` একটি Verb। Verb-এর পরে এবং Preposition-এর পরে Pronoun-এর **Objective Case** বসে।\r\n\r\n- **Subjective Pronouns:** I, we, you, he, she, it, they\r\n- **Objective Pronouns:** me, us, you, him, her, it, them\r\n\r\nযেহেতু বাক্যটি `Let` দিয়ে শুরু হয়েছে, তাই এর পরে যে Pronoun-গুলো আসবে, সেগুলো অবশ্যই Objective Form-এ থাকতে হবে।\r\n\r\n- `he` (Subjective) এর Objective Form হলো `him`।\r\n- `you` (Subjective) এর Objective Form হলো `you`।\r\n\r\nএই নিয়ম অনুযায়ী, `he` থাকা বাক্যগুলো (a এবং c) ভুল। `him` এবং `you` থাকা বাক্যগুলো (b এবং d) সঠিক।\r\n\r\n---\r\n\r\n### 2. Order of Pronouns (Pronoun-এর ক্রম)\r\n\r\n**Key Concept:** যখন বিভিন্ন Person-এর Pronoun (1st, 2nd, 3rd) একসাথে একটি বাক্যে বসে, তখন সাধারণত একটি নির্দিষ্ট ক্রম অনুসরণ করা হয়।\r\n\r\n- **General Rule (Good Manners):** 2nd person (`you`), then 3rd person (`he/she/it/they`), and finally 1st person (`I/we`)। এই ক্রমটি হলো **2-3-1**।\r\n\r\n  - _Example:_ **You, he, and I** will go to the party.\r\n\r\n- **For Confessions (দোষ স্বীকার):** যখন কোনো দোষ, ভুল বা নেতিবাচক কাজের কথা বলা হয়, তখন ক্রমটি উল্টে যায়: 1st person, then 2nd person, and finally 3rd person। এই ক্রমটি হলো **1-2-3**।\r\n  - _Example:_ **I, you, and he** are responsible for the mistake.\r\n\r\n**বাক্য বিশ্লেষণ:**\r\nআমাদের বাক্যে আছে 2nd person (`you`) এবং 3rd person (`him`)।\r\n\r\n- সাধারণ **2-3-1** নিয়ম অনুযায়ী, `you` আগে বসবে এবং `him` পরে বসবে।\r\n- Structure: `Let` + `you` (2nd) + `and` + `him` (3rd) + `be witnesses`.\r\n- Result: **Let you and him be witnesses.**\r\n\r\nএই কারণে, option (b) সবচেয়ে প্রচলিত এবং মানসম্মত (standard) উত্তর।\r\n\r\nOption (d) `Let him and you be witnesses` ব্যাকরণগতভাবে ভুল নয়, কারণ `Let`-এর পর Objective Pronoun-এর নিয়মটি মানা হয়েছে। তবে Pronoun-এর ক্রমের সাধারণ নিয়মটি মানা হয়নি। পরীক্ষার উত্তরে (b) এবং (d) উভয়ই থাকলে, (b) বেছে নেওয়াই শ্রেয়।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** `Let`-এর পর `I/he/she/we/they` বসে না,\r\n  `me/him/her/us/them` বসে। আর ভালো কাজ বা সাধারণ কথায়, `you` সবার আগে আসে।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) Let he and you...:** `he` Subjective form, ভুল।\r\n- **(c) Let you and he...:** `he` Subjective form, ভুল।";
