export const sourceCode = "import { Callout } from \"nextra/components\";\r\n\r\n# Modal Perfect: \"might have\" + V3\r\n\r\n**প্রশ্ন:** Fill in the blank: ‘You might \\_\\_\\_\\_ about Milton’.\r\n\r\n(a) hear\r\n(b) have heard\r\n(c) heard\r\n(d) be heard\r\n\r\n**সঠিক উত্তর:** (b) have heard\r\n\r\n---\r\n\r\n### **ব্যাখ্যা**\r\n\r\nএই বাক্যটিতে Modal Auxiliary Verb `might`-এর সঠিক ব্যবহার জানতে চাওয়া হয়েছে। এখানে `might have heard` ব্যবহার করে অতীতে কোনো ঘটনা ঘটার সম্ভাবনা (past possibility) প্রকাশ করা হচ্ছে।\r\n\r\n#### **Key Concepts:**\r\n\r\n1.  **Modal Auxiliary Verbs:** `can`, `could`, `may`, `might`, `shall`, `should`, `will`, `would`, `must` ইত্যাদি হলো Modal Verb।\r\n2.  **Modal Perfect:** যখন Modal Verb-এর পরে `have` + `Past Participle (V3)` বসে, তখন তাকে Modal Perfect বলে। এটি সাধারণত অতীতের কোনো সম্ভাবনা, অনুমান বা অনুশোচনা প্রকাশ করতে ব্যবহৃত হয়।\r\n    - **Structure:** `Modal + have + V3`\r\n\r\n#### **বিশ্লেষণ:**\r\n\r\n- **`might hear`**: এটি ভবিষ্যতের সম্ভাবনা বোঝায়। (e.g., If you listen carefully, you might hear the bird sing.)\r\n- **`might have heard`**: এটি অতীতের সম্ভাবনা বোঝায়। বাক্যটির অর্থ দাঁড়ায়, \"এটা সম্ভব যে তুমি অতীতে মিল্টন সম্পর্কে শুনেছ।\" বক্তা এখানে একটি অতীতের সম্ভাবনা নিয়ে কথা বলছেন।\r\n- **`might heard`**: এটি ভুল, কারণ modal verb-এর পরে verb-এর past form (V2) বসে না।\r\n- **`might be heard`**: এটি passive voice-এ ব্যবহৃত হয়, যার অর্থ \"শোনা যেতে পারে\"। (e.g., His voice might be heard from a distance.)\r\n\r\n#### **কেন `have heard` সঠিক?**\r\n\r\nবাক্যটি একজন ব্যক্তির (Milton) সম্পর্কে শোনার অতীতের সম্ভাবনা নির্দেশ করছে। বক্তা নিশ্চিত নন, তাই তিনি `might` ব্যবহার করে অনুমান করছেন। যেহেতু অনুমানটি অতীত সম্পর্কিত, তাই Modal Perfect Tense (`might have heard`) ব্যবহার করা হয়েছে।\r\n\r\n<Callout type=\"info\">\r\n  ** মনে রাখবেন:** * **`must have + V3`**: অতীতের কোনো ঘটনা সম্পর্কে জোরালো\r\n  অনুমান। (e.g., He is not here. He **must have left**.) * **`should have +\r\n  V3`**: অতীতের কোনো কাজ করা উচিত ছিল কিন্তু করা হয়নি (অনুশোচনা)। (e.g., You\r\n  **should have studied** harder.) * **`could have + V3`**: অতীতে কিছু করার\r\n  সামর্থ্য ছিল কিন্তু করা হয়নি। (e.g., I **could have helped** you.) * **`might\r\n  have + V3`**: অতীতের কোনো ঘটনা সম্পর্কে দুর্বল সম্ভাবনা বা অনুমান।\r\n</Callout>\r\n\r\nএই নিয়মটি Modal Verb-এর advanced ব্যবহারের একটি উদাহরণ, যা বিভিন্ন পরীক্ষায় প্রায়ই আসে।";
