export const sourceCode = "import { Callout } from \"nextra/components\";\r\n\r\n# Modal Expression: \"had better\"\r\n\r\n**প্রশ্ন:** We \\_\\_\\_\\_ hire a rickshaw. It’s already too late.\r\n\r\n(a) had to\r\n(b) would rather\r\n(c) had better\r\n(d) had though\r\n\r\n**সঠিক উত্তর:** (c) had better\r\n\r\n---\r\n\r\n### **ব্যাখ্যা**\r\n\r\nএই বাক্যটিতে একটি নির্দিষ্ট পরিস্থিতিতে কী করা উচিত সে সম্পর্কে একটি জোরালো পরামর্শ বা সতর্কবাণী (strong advice or warning) দেওয়া হচ্ছে। এই ধরনের অর্থ প্রকাশ করার জন্য `had better` expression-টি ব্যবহৃত হয়।\r\n\r\n#### **Key Concepts:**\r\n\r\n1.  **`had better`**: এটি একটি modal expression যা `should`-এর চেয়েও শক্তিশালী অর্থ প্রকাশ করে। এর অর্থ হলো, \"এই কাজটি করা ভালো, কারণ না করলে একটি নেতিবাচক পরিণতি (negative consequence) হতে পারে।\"\r\n2.  **Structure:** `Subject + had better + Verb (base form)`\r\n    - **Negative:** `Subject + had better + not + Verb (base form)`\r\n\r\n#### **বিশ্লেষণ:**\r\n\r\n- **Context (প্রেক্ষাপট):** \"It’s already too late.\" (এর মধ্যেই অনেক দেরি হয়ে গেছে।)\r\n- **Advice/Warning (পরামর্শ/সতর্কবাণী):** এই পরিস্থিতিতে দেরি এড়ানোর জন্য রিকশা ভাড়া করাটাই সবচেয়ে ভালো কাজ। যদি রিকশা ভাড়া না করা হয়, তাহলে আরও বেশি দেরি হয়ে যাবে (নেতিবাচক পরিণতি)।\r\n\r\n\"We **had better** hire a rickshaw\" বাক্যটির অর্থ হলো, \"আমাদের বরং একটি রিকশা ভাড়া করা উচিত/ভালো, কারণ দেরি হয়ে গেছে।\"\r\n\r\n#### **কেন অন্য অপশনগুলো ভুল?**\r\n\r\n- **(a) had to**: এটি অতীতের বাধ্যবাধকতা (past obligation) বোঝায়। (e.g., Yesterday, we **had to** hire a rickshaw.)\r\n- **(b) would rather**: এটি পছন্দ বা অগ্রাধিকার (preference) বোঝায়, কিন্তু কোনো জরুরি অবস্থা বা সতর্কবাণী নয়। (e.g., I **would rather** walk than hire a rickshaw.)\r\n- **(d) had though**: এটি একটি ভুল expression।\r\n\r\n#### **`had better` vs. `should`:**\r\n\r\n- **`should`**: সাধারণ উপদেশ বোঝায়। (You **should** exercise regularly.)\r\n- **`had better`**: নির্দিষ্ট পরিস্থিতিতে জরুরি উপদেশ বা সতর্কবাণী বোঝায়। (You **had better** leave now, or you will miss the train.)\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Memorize:** যখন দেখবেন কোনো কাজ না করলে খারাপ কিছু হওয়ার সম্ভাবনা\r\n  আছে, তখন `had better` ব্যবহার করুন। `had better` দেখতে Past Tense-এর মতো হলেও\r\n  এটি Present বা Future-এর কথা বলে।\r\n</Callout>";
