export const sourceCode = "---\r\ntitle: Honesty এর Parts of Speech এবং সহজভাবে মনে রাখার কৌশল,\r\ndescription: Honesty শব্দটির Parts of Speech কী? BCS এবং প্রাথমিক বিদ্যালয়ের শিক্ষক পরীক্ষার জন্য সহজ ব্যাখ্যা ও স্মরণ কৌশল।\r\ntags: BCS, Parts of Speech, Honesty, Noun Types, প্রাথমিক বিদ্যালয় শিক্ষক, ইংরেজি ব্যাকরণ, Abstract Noun\r\n---\r\n\r\n# Honesty is the Best Policy. বাক্যাংশটির 'Honesty' অংশের Parts of Speech কী?\r\n\r\n(ক) Proper Noun  \r\n(খ) Common Noun  \r\n(গ) Abstract Noun  \r\n(ঘ) Collective Noun\r\n\r\n✅ **Correct Answer: (গ) Abstract Noun**\r\n\r\nBCS বা প্রাথমিক বিদ্যালয়ের শিক্ষক পরীক্ষায় ইংরেজি ব্যাকরণে **Parts of Speech** প্রায়ই প্রশ্ন আসে। আজ আমরা “**Honesty is the Best Policy**” বাক্যাংশটির **‘Honesty’** শব্দের Parts of Speech বিশ্লেষণ করব।\r\n\r\n---\r\n\r\n## ১️⃣ Parts of Speech বোঝার সহজ উপায়\r\n\r\n**Parts of Speech** হল শব্দের কাজ বা রোল বাক্যে। মূলত ৮ ধরনের Parts of Speech আছে:\r\n\r\n1. Noun (ব্যক্তি, বস্তু, স্থান, ধারণা)\r\n2. Pronoun (সর্বনাম)\r\n3. Verb (ক্রিয়া)\r\n4. Adjective (বিশেষণ)\r\n5. Adverb (ক্রিয়াবিশেষণ)\r\n6. Preposition (অব্যয়, সম্পর্কসূচক)\r\n7. Conjunction (যোজক)\r\n8. Interjection (বিস্ময়সূচক)\r\n\r\n---\r\n\r\n## ২️⃣ Noun-এর ধরন\r\n\r\nNoun হল **কেউ বা কিছু বোঝানোর শব্দ**। প্রধান ৪ ধরন:\r\n\r\n| ধরন             | উদাহরণ                  | সহজ বাংলা ব্যাখ্যা                          |\r\n| --------------- | ----------------------- | ------------------------------------------- |\r\n| Proper Noun     | Bangladesh, Mojnu       | নির্দিষ্ট নাম                               |\r\n| Common Noun     | boy, school             | সাধারণ নাম                                  |\r\n| Abstract Noun   | honesty, love, patience | যা চোখে দেখা যায় না, কিন্তু অনুভব করা যায় |\r\n| Collective Noun | team, class             | একসাথে কিছু বোঝায়                          |\r\n\r\n---\r\n\r\n## ৩️⃣ ‘Honesty’ কোন ধরনের Noun?\r\n\r\n**বাক্য:** `Honesty is the best policy.`\r\n\r\n- এখানে **Honesty** বোঝাচ্ছে **সৎ থাকার গুণ**, যা **দেখা যায় না, অনুভব করা যায়**।\r\n- তাই এটি **Abstract Noun (গুণ বা ধারণা বোঝানো Noun)**।\r\n\r\n✅ সঠিক উত্তর: **(গ) Abstract Noun**\r\n\r\n---\r\n\r\n## ৪️⃣ সহজ মেমোরি কৌশল\r\n\r\n১. **Proper vs Common**\r\n\r\n- Proper = নির্দিষ্ট নাম → Capital letter দিয়ে লেখা।\r\n- Common = সাধারণ নাম → ছোট হাতের অক্ষর।\r\n\r\n২. **Abstract vs Collective**\r\n\r\n- Abstract = অনুভূতিগত, ভাবনামূলক → honesty, courage, patience\r\n- Collective = একসাথে কিছু বোঝায় → team, family, class\r\n\r\n৩. **ধাপে ধাপে চিন্তা করুন:**\r\n\r\n1. শব্দ কি চোখে দেখা যায়?\r\n\r\n   - না → Abstract\r\n   - হ্যাঁ → Common / Proper / Collective\r\n\r\n2. যদি একসাথে কিছু বোঝায় → Collective\r\n\r\n3. যদি নির্দিষ্ট নাম → Proper\r\n\r\n4. বাকি সাধারণ বস্তু বা ব্যক্তি → Common\r\n\r\n5. **চরিত্রের সাথে মেমোরি ট্রিক**\r\n\r\n   - “Honesty” → **H** = Heart → গুণ/ভাবনা → Abstract Noun\r\n\r\n---\r\n\r\n## ৫️⃣ টেকনিক হ্যাক BCS / Teacher Exam-এর জন্য\r\n\r\n- **প্রতিদিন ৫টি Abstract Noun লিখুন**: honesty, love, courage, patience, freedom\r\n- **ছোট বাক্যে ব্যবহার করুন** → মনে রাখার জন্য\r\n- **Comparison Chart বানান**: Proper vs Common, Abstract vs Collective → চোখে মনে থাকবে\r\n\r\n---\r\n\r\n### ✨ সংক্ষেপে\r\n\r\n**Honesty** = **Abstract Noun**\r\n\r\n- চোখে দেখা যায় না\r\n- অনুভব করা যায়\r\n- গুণ বা মানসিক ভাব বোঝায়\r\n\r\nএভাবে **BCS এবং প্রাথমিক শিক্ষক পরীক্ষার Part of Speech প্রশ্ন সহজে মনে রাখা যায়।**";
