export const sourceCode = "---\r\ntitle: Ali saw a flock of sheep এর Parts of Speech এবং সহজভাবে মনে রাখার কৌশল\r\ndescription: Ali saw a flock of sheep বাক্যাংশটির 'flock' অংশের Parts of Speech কী? BCS এবং প্রাথমিক বিদ্যালয়ের শিক্ষক পরীক্ষার জন্য সহজ ব্যাখ্যা ও স্মরণ কৌশল।\r\ntags: BCS, Parts of Speech, Flock, Collective Noun, প্রাথমিক বিদ্যালয় শিক্ষক, ইংরেজি ব্যাকরণ\r\n---\r\n\r\n# Ali saw a flock of sheep – ‘flock’ এর Parts of Speech সহজভাবে বোঝা\r\n\r\n(ক) Proper Noun  \r\n(খ) Common Noun  \r\n(গ) Abstract Noun  \r\n(ঘ) Collective Noun ✅\r\n\r\n**সঠিক উত্তর:** **(ঘ) Collective Noun**\r\n\r\n---\r\n\r\n## ১️⃣ Parts of Speech-এর সহজ ধারণা\r\n\r\n**Parts of Speech** বলতে শব্দের বাক্যে কাজ বা রোল বোঝায়। প্রধান ৮টি ধরণ:\r\n\r\n1. **Noun (নাম)** – ব্যক্তি, বস্তু, স্থান, ধারণা\r\n2. **Pronoun (সর্বনাম)** – Noun-এর পরিবর্তে ব্যবহৃত শব্দ\r\n3. **Verb (ক্রিয়া)** – কাজ বা অবস্থা\r\n4. **Adjective (বিশেষণ)** – Noun-এর গুণ বা অবস্থা\r\n5. **Adverb (ক্রিয়াবিশেষণ)** – Verb/Adjective/Adverb এর বিস্তারিত\r\n6. **Preposition (অব্যয়)** – সম্পর্ক সূচক\r\n7. **Conjunction (যোজক)** – বাক্য বা শব্দ যুক্ত করা\r\n8. **Interjection (বিস্ময়সূচক)** – বিস্ময় বা অনুভূতি প্রকাশ\r\n\r\n---\r\n\r\n## Noun-এর ধরন\r\n\r\n**Noun** হল **কেউ বা কিছু বোঝানোর শব্দ**। প্রধান ৪টি ধরন:\r\n\r\n| ধরন             | উদাহরণ             | সহজ বাংলা ব্যাখ্যা                   |\r\n| --------------- | ------------------ | ------------------------------------ |\r\n| Proper Noun     | Ali, Bangladesh    | নির্দিষ্ট নাম                        |\r\n| Common Noun     | boy, school        | সাধারণ নাম                           |\r\n| Abstract Noun   | honesty, love      | যা চোখে দেখা যায় না, অনুভব করা যায় |\r\n| Collective Noun | flock, team, class | একসাথে কিছু বোঝায়                   |\r\n\r\n---\r\n\r\n## ‘flock’ কোন ধরনের Noun?\r\n\r\n**বাক্য:** `Ali saw a flock of sheep.`\r\n\r\n- **flock** বোঝায় একসাথে থাকা **শেডের পশুদের দল**।\r\n- একক নয়, এটি **একসাথে কিছু বোঝায়**।\r\n- তাই এটি **Collective Noun**।\r\n\r\n✅ সঠিক উত্তর: **(ঘ) Collective Noun**\r\n\r\n---\r\n\r\n## সহজ মেমোরি কৌশল\r\n\r\n1. **Proper vs Common**\r\n\r\n   - Proper = নির্দিষ্ট নাম → Capital letter\r\n   - Common = সাধারণ নাম → ছোট হাতের অক্ষর\r\n\r\n2. **Abstract vs Collective**\r\n\r\n   - Abstract = অনুভূতিগত, যা দেখা যায় না → honesty, love, patience, discipline\r\n   - Collective = একসাথে কিছু বোঝায় → flock, team, family, class\r\n\r\n3. **ধাপে ধাপে চিন্তা করুন:**\r\n\r\n   1. শব্দ কি চোখে দেখা যায়?\r\n\r\n      - না → Abstract\r\n      - হ্যাঁ → Common / Proper / Collective\r\n\r\n   2. একসাথে কিছু বোঝায় → Collective\r\n   3. নির্দিষ্ট নাম → Proper\r\n   4. সাধারণ ব্যক্তি/বস্তু → Common\r\n\r\n4. **চোখে মনে রাখার ট্রিক**\r\n\r\n   - flock → **F** = Few animals together → Collective Noun\r\n\r\n---\r\n\r\n## BCS এবং শিক্ষক নিয়োগ পরীক্ষার জন্য হ্যাক\r\n\r\n- **প্রতিদিন ৫টি Collective Noun লিখুন:** flock, team, family, class, audience\r\n- ছোট বাক্যে ব্যবহার করুন → মনে রাখার জন্য\r\n- Proper vs Common vs Abstract vs Collective চারটি ভাগে Comparison Chart বানান → চোখে মনে থাকবে\r\n\r\n---\r\n\r\n### ✨ সংক্ষেপে\r\n\r\n**flock** = **Collective Noun**\r\n\r\n- একসাথে কিছু বোঝায়\r\n- একক নয়\r\n- সাধারণভাবে প্রাণী বা মানুষের দল বোঝায়";
