export const sourceCode = "---\r\ntitle: \"Semi-modal Verb 'dare': 'He dare not touch me'\"\r\ndescription: \"'He dare not touch me' - এই বাক্যে 'dare' শব্দটি Semi-modal Verb কেন? Modal Verb এবং Main Verb হিসেবে 'dare' এর ব্যবহার ও পার্থক্য সহজ বাংলায় শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"semi-modal verb\",\r\n    \"dare as a modal\",\r\n    \"modal auxiliary\",\r\n    \"dare not touch\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n    \"need dare used to\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: He dare not touch me. Here ‘dare’ is a —\r\n\r\n**Question:** He dare not touch me. Here ‘dare’ is a —\r\n(a) Semi-modal verb\r\n(b) Main verb\r\n(c) Modal verb\r\n(d) Pure auxiliary verb\r\n\r\n**Correct Answer:** (a) Semi-modal verb\r\n\r\n### কেন 'dare' একটি Semi-modal verb?\r\n\r\nএই প্রশ্নের উত্তরটি **Modal Verbs** এবং **Semi-modal Verbs**-এর মধ্যে পার্থক্য বোঝার উপর নির্ভরশীল।\r\n\r\n**Key Concepts:**\r\n\r\n1.  **Modal Verbs (Pure Modals):** `can`, `could`, `may`, `might`, `shall`, `should`, `will`, `would`, `must` - এই verb-গুলো শুধুমাত্র auxiliary হিসেবে কাজ করে। এদের পরে `s/es` যোগ হয় না এবং এদের পরে main verb-এর base form বসে।\r\n\r\n2.  **Semi-modal Verbs (or Marginal Modals):** `dare`, `need`, `used to`, `ought to` - এই verb-গুলো বিশেষ কারণ এদের মধ্যে modal verb এবং main verb উভয়ের বৈশিষ্ট্যই বিদ্যমান।\r\n\r\n**'Dare'-এর দ্বৈত চরিত্র:**\r\n\r\n`Dare` (সাহস করা) শব্দটি modal এবং main verb উভয়ভাবেই ব্যবহৃত হতে পারে।\r\n\r\n- **As a Modal Verb:**\r\n\r\n  - যখন `dare` modal হিসেবে ব্যবহৃত হয়, তখন এটি negative (`dare not` বা `daren't`) এবং interrogative (প্রশ্নবোধক) বাক্যে বসে।\r\n  - Modal হিসেবে ব্যবহৃত হলে, third person singular-এ `s` যোগ হয় না (`he dare`, not `he dares`)।\r\n  - এর পরে `not` সরাসরি বসে, `do/does/did`-এর সাহায্য লাগে না।\r\n  - **Example (Our Question):** \"He **dare not** touch me.\" (এখানে `dare` একটি modal-এর মতো আচরণ করছে)।\r\n  - **Example:** \"**Dare** he say that?\"\r\n\r\n- **As a Main Verb:**\r\n  - যখন `dare` main verb হিসেবে ব্যবহৃত হয়, তখন এটি সাধারণ verb-এর সব নিয়ম মানে।\r\n  - Third person singular-এ `s` যোগ হয় (`he dares`)।\r\n  - Negative ও Interrogative করতে `do/does/did`-এর সাহায্য লাগে।\r\n  - এর পরে `to`-infinitive বসে।\r\n  - **Example:** \"He **doesn't dare to** touch me.\"\r\n  - **Example:** \"He **dares to** challenge the authority.\"\r\n\r\nযেহেতু `dare` উভয় প্রকার verb-এর বৈশিষ্ট্য ধারণ করে, তাই একে **Semi-modal verb** বলা হয়। প্রদত্ত option-গুলোর মধ্যে এটিই সবচেয়ে সুনির্দিষ্ট এবং সঠিক উত্তর।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** যদি `dare`-এর সাথে `not` সরাসরি (without do/does) বসে,\r\n  তবে সেটি modal হিসেবে কাজ করছে। আর যদি `don't/doesn't dare` থাকে, তবে সেটি\r\n  main verb।\r\n</Callout>\r\n\r\n### অন্যান্য Option-গুলো কেন কম সঠিক?\r\n\r\n- **(b) Main verb:** এই নির্দিষ্ট বাক্যে (`He dare not...`), `dare` main verb হিসেবে কাজ করছে না, modal হিসেবে করছে।\r\n- **(c) Modal verb:** যদিও এই বাক্যে `dare` modal-এর মতো কাজ করছে, কিন্তু `dare`-এর একটি main verb হিসেবেও ব্যবহার আছে। তাই 'Semi-modal' শব্দটি এর দ্বৈত চরিত্রকে আরও ভালোভাবে বর্ণনা করে।\r\n- **(d) Pure auxiliary verb:** `dare` একটি pure auxiliary নয়, কারণ এটি main verb-ও হতে পারে। Pure auxiliary হলো `be`, `do`, `have`।";
