export const sourceCode = "import { Callout } from \"nextra/components\";\r\n\r\n# Modal for Impossibility: \"can't be\"\r\n\r\n**প্রশ্ন:** ‘There are no lights on and no one answers the doorbell. They \\_\\_\\_\\_ be at home’. Choose the right one of the following.\r\n\r\n(a) mustn’t\r\n(b) can’t\r\n(c) will not\r\n(d) shouldn’t\r\n\r\n**সঠিক উত্তর:** (b) can’t\r\n\r\n---\r\n\r\n### **ব্যাখ্যা**\r\n\r\nএই বাক্যটিতে প্রমাণের উপর ভিত্তি করে একটি যৌক্তিক উপসংহার (logical conclusion) টানা হচ্ছে যে, তাদের বাড়িতে থাকা অসম্ভব। এই ধরনের যৌক্তিক অসম্ভাবনা (logical impossibility) প্রকাশ করার জন্য `can't` ব্যবহার করা হয়।\r\n\r\n#### **Key Concepts:**\r\n\r\nModal verb ব্যবহার করে আমরা বিভিন্ন মাত্রার নিশ্চয়তা প্রকাশ করতে পারি:\r\n\r\n1.  **Certainty (Positive): `must`**\r\n\r\n    - যখন আমরা প্রায় নিশ্চিত যে কিছু একটা সত্য।\r\n    - **উদাহরণ:** He is smiling. He **must be** happy. (সে হাসছে। সে নিশ্চয়ই খুশি।)\r\n\r\n2.  **Impossibility / Certainty (Negative): `can't`**\r\n    - যখন আমরা প্রায় নিশ্চিত যে কিছু একটা সত্য নয় বা অসম্ভব।\r\n    - **উদাহরণ:** He just ate a large meal. He **can't be** hungry. (সে এইমাত্র পেটপুরে খেয়েছে। সে ক্ষুধার্ত হতে পারে না।)\r\n\r\n#### **বিশ্লেষণ:**\r\n\r\n- **Evidence (প্রমাণ):** \"There are no lights on and no one answers the doorbell.\" (বাতি জ্বলছে না এবং কেউ দরজার বেল শুনে جواب দিচ্ছে না।)\r\n- **Logical Conclusion (যৌক্তিক উপসংহার):** এই প্রমাণ থেকে এটাই স্বাভাবিক সিদ্ধান্ত যে, \"তারা বাড়িতে থাকতে পারে না\" বা \"তাদের বাড়িতে থাকাটা অসম্ভব।\"\r\n\r\nএই অসম্ভাবনা প্রকাশ করার জন্য `can't` সবচেয়ে উপযুক্ত।\r\n\r\n#### **কেন অন্য অপশনগুলো ভুল?**\r\n\r\n- **(a) mustn’t**: এটি নিষেধাজ্ঞা (prohibition) বোঝাতে ব্যবহৃত হয়, অনুমান বা সিদ্ধান্তের জন্য নয়।\r\n  - **উদাহরণ:** You **mustn't** make a noise. (তুমি অবশ্যই শব্দ করবে না।)\r\n- **(c) will not (won't)**: এটি ভবিষ্যৎ বা অনিচ্ছা প্রকাশ করে।\r\n- **(d) shouldn’t**: এটি উপদেশ বা পরামর্শ না দেওয়ার ক্ষেত্রে ব্যবহৃত হয় (negative advice)।\r\n  - **উদাহরণ:** You **shouldn't** waste your time. (তোমার সময় নষ্ট করা উচিত নয়।)\r\n\r\n<Callout type=\"info\">\r\n  ** মনে রাখবেন:** * `must be` = আমি ৯৫% নিশ্চিত এটা সত্য। * `can't be` = আমি\r\n  ৯৫% নিশ্চিত এটা মিথ্যা/অসম্ভব। * `mustn't` = এটা করার অনুমতি নেই।\r\n</Callout>\r\n\r\nএই প্রশ্নটি `must` এবং `can't`-এর মধ্যে যৌক্তিক অনুমান সংক্রান্ত ব্যবহারের পার্থক্য তুলে ধরে, যা modal verb-এর একটি গুরুত্বপূর্ণ অংশ।";
