export const sourceCode = "import { Callout } from \"nextra/components\";\r\n\r\n# Semi-Modal Verbs: \"need\" and \"dare\"\r\n\r\n**প্রশ্ন:** Which of the following are semi-modals?\r\n\r\n(a) can, could\r\n(b) need, dare\r\n(c) will, would\r\n(d) must, should\r\n\r\n**সঠিক উত্তর:** (b) need, dare\r\n\r\n---\r\n\r\n### **ব্যাখ্যা**\r\n\r\nSemi-modal verb (বা quasi-modal) হলো এমন কিছু verb যা কখনও modal verb-এর মতো আচরণ করে, আবার কখনও main verb-এর মতো আচরণ করে। `need` এবং `dare` এই শ্রেণীর প্রধান উদাহরণ।\r\n\r\n#### **Key Concepts:**\r\n\r\n1.  **Modal Verbs (Pure Modals):**\r\n\r\n    - এরা হলো: `can`, `could`, `may`, `might`, `shall`, `should`, `will`, `would`, `must`।\r\n    - **বৈশিষ্ট্য:**\r\n      - এদের পরে verb-এর base form (V1) বসে (without `to`)।\r\n      - Third person singular-এ `-s` বা `-es` যুক্ত হয় না।\r\n      - `do`/`does`/`did` ছাড়াই negative বা interrogative বাক্য গঠন করতে পারে।\r\n\r\n2.  **Semi-Modal Verbs:**\r\n    - এরা modal এবং main verb—উভয় ধরনের বৈশিষ্ট্যই প্রদর্শন করে।\r\n    - প্রধান উদাহরণ: `need`, `dare`, `ought to`, `used to`।\r\n\r\n#### **`need` এবং `dare`-এর দ্বৈত রূপ:**\r\n\r\n| Verb     | As a Modal Verb (Negative & Question)                                                   | As a Main Verb (Affirmative, Negative, Question)                                                            |\r\n| :------- | :-------------------------------------------------------------------------------------- | :---------------------------------------------------------------------------------------------------------- |\r\n| **need** | You **need not** worry. (তোমার চিন্তা করার দরকার নেই।) <br/> **Need** I say more?       | You **need to** worry. <br/> You **don't need to** worry. <br/> **Do** you **need to** worry?               |\r\n| **dare** | He **dare not** oppose me. (সে আমার বিরোধিতা করার সাহস করে না।) <br/> How **dare** you! | He **dares to** oppose me. <br/> He **doesn't dare to** oppose me. <br/> **Does** he **dare to** oppose me? |\r\n\r\n#### **অপশনগুলোর বিশ্লেষণ:**\r\n\r\n- **(a) can, could**: এরা Pure Modal Verb।\r\n- **(b) need, dare**: এরা Semi-Modal Verb, কারণ এরা main verb হিসেবেও ব্যবহৃত হতে পারে (`to` infinitive সহ) এবং modal verb হিসেবেও (`to` ছাড়া)।\r\n- **(c) will, would**: এরা Pure Modal Verb।\r\n- **(d) must, should**: এরা Pure Modal Verb।\r\n\r\n<Callout type=\"info\">\r\n  ** মনে রাখবেন:** `need` এবং `dare` যখন modal হিসেবে কাজ করে, তখন তারা মূলত\r\n  negative এবং interrogative বাক্যেই সীমাবদ্ধ থাকে। Main verb হিসেবে তারা যেকোনো\r\n  বাক্যে ব্যবহৃত হতে পারে।\r\n</Callout>";
