export const sourceCode = "---\r\ntitle: \"Each of the sons followed his father's trade - কেন 'his' ব্যবহৃত হলো?\"\r\ndescription: \"Each of the sons followed his father's trade - এই বাক্যে 'their' না হয়ে 'his' কেন হলো? Distributive Pronoun (Each) এর পর Singular Possessive Pronoun ব্যবহারের নিয়মটি শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"each of the sons followed his\",\r\n    \"distributive pronoun each\",\r\n    \"singular possessive pronoun\",\r\n    \"english grammar for bcs\",\r\n    \"pronoun agreement rules\",\r\n    \"subject verb agreement\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: Each of the sons followed \\_\\_\\_\\_ father’s trade.\r\n\r\n**Question:** Each of the sons followed \\_\\_\\_\\_ father’s trade.\r\n(a) their\r\n(b) her\r\n(c) whose\r\n(d) his\r\n\r\n**Correct Answer:** (d) his\r\n\r\n### কেন 'his' সঠিক উত্তর? (Why is 'his' correct?)\r\n\r\nএই প্রশ্নের মূলে রয়েছে **Distributive Pronoun** `Each`-এর একটি গুরুত্বপূর্ণ নিয়ম।\r\n\r\n**Key Concept: `Each` is always singular.**\r\n\r\n1.  **Distributive Pronoun:** `Each`, `Either`, `Neither` - এই pronoun-গুলো একটি group-এর প্রত্যেক সদস্যকে আলাদাভাবে বা পৃথকভাবে বোঝায়।\r\n2.  **Singular Agreement:** যখন `Each` একটি বাক্যের subject বা subject-এর অংশ হিসেবে ব্যবহৃত হয়, তখন সেটি সবসময় **singular** হিসেবে বিবেচিত হয়। এর ফলে, তার সাথে সম্পর্কিত verb এবং pronoun-ও **singular** হতে হয়।\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- \"Each of the sons...\" (পুত্রদের প্রত্যেকে...)। যদিও 'sons' শব্দটি plural, কিন্তু `Each` শব্দটি তাদের প্রত্যেককে আলাদা করে বোঝাচ্ছে। তাই বাক্যের মূল subject `Each` singular।\r\n- যেহেতু subject singular, তাই তার জন্য যে possessive pronoun (যার অর্থ 'তার') ব্যবহার করা হবে, সেটিও singular হতে হবে।\r\n- `his` হলো singular masculine possessive pronoun।\r\n- `their` হলো plural possessive pronoun।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** বাক্যে `Each of...`, `One of...`, `Either of...` দেখলেই\r\n  ধরে নেবেন subject-টি singular। তাই এর সাথে সম্পর্কিত verb এবং pronoun (e.g.,\r\n  is/was/has, his/her) singular হবে। Plural noun (`sons`) দেখে বিভ্রান্ত হবেন\r\n  না।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) their:** এটি একটি Plural Possessive Pronoun। কিন্তু `Each` singular হওয়ায় `their` ব্যবহার করা যাবে না। এটি একটি খুবই সাধারণ ভুল।\r\n- **(b) her:** এটি Singular Possessive Pronoun হলেও feminine (নারীবাচক)। বাক্যে 'sons' (পুত্র) থাকায় masculine pronoun `his` ব্যবহার করতে হবে।\r\n- **(c) whose:** এটি একটি Interrogative বা Relative Pronoun, যা প্রশ্ন করতে বা সম্পর্ক বোঝাতে ব্যবহৃত হয়। এখানে এর ব্যবহার প্রাসঙ্গিক নয়।\r\n\r\n### আরও উদাহরণ (More Examples)\r\n\r\n- **Each** of the students **has** submitted **his** assignment. (Not: have, their)\r\n- **One** of the girls left **her** umbrella at the office. (Not: their)\r\n- **Neither** of the candidates **was** qualified for **his** final interview. (Not: were, their)\r\n\r\nএই নিয়মটি Subject-Verb Agreement এবং Pronoun Agreement-এর একটি মৌলিক অংশ এবং প্রতিযোগিতামূলক পরীক্ষার জন্য অত্যন্ত গুরুত্বপূর্ণ।";
