export const sourceCode = "---\r\ntitle: \"Gerund vs Present Participle: 'Going there he found his friend absent'\"\r\ndescription: \"'Going there he found his friend absent' - এই বাক্যে 'going' কেন Present Participle? Gerund এবং Participle এর মধ্যে পার্থক্য সহজ বাংলায় শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"present participle vs gerund\",\r\n    \"going there he found\",\r\n    \"participle phrase\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n    \"what is a participle\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: Going there he found his friend absent. Here ‘going’ is a —\r\n\r\n**Question:** Going there he found his friend absent. Here ‘going’ is a —\r\n(a) gerund\r\n(b) present participle\r\n(c) noun\r\n(d) past participle\r\n\r\n**Correct Answer:** (b) present participle\r\n\r\n### কেন 'going' একটি Present Participle?\r\n\r\nএই প্রশ্নের উত্তরটি **Gerund** এবং **Present Participle**-এর মধ্যে থাকা সূক্ষ্ম পার্থক্য বোঝার উপর নির্ভর করে। দুটিই verb-এর সাথে `ing` যোগ করে গঠিত হলেও, বাক্যে তাদের কাজ ভিন্ন।\r\n\r\n**Key Concept: Gerund (Noun) vs. Present Participle (Adjective/Adverbial)**\r\n\r\n1.  **Gerund:** যখন কোনো `verb+ing` শব্দটি বাক্যে **Noun**-এর মতো কাজ করে (অর্থাৎ subject, object বা preposition-এর object হিসেবে বসে), তখন তাকে Gerund বলে। Gerund 'কী' (what) প্রশ্নের উত্তর দেয়।\r\n\r\n    - _Example:_ **Swimming** is a good exercise. (Here, 'swimming' is the subject of the verb 'is').\r\n\r\n2.  **Present Participle:** যখন কোনো `verb+ing` শব্দটি বাক্যে **Adjective**-এর মতো কাজ করে (কোনো Noun/Pronoun-কে modify করে) অথবা **Adverb**-এর মতো কাজ করে (বাক্যের verb-কে modify করে, সময় বা কারণ বোঝায়), তখন তাকে Present Participle বলে।\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- \"Going there he found his friend absent.\" (সেখানে গিয়ে সে তার বন্ধুকে অনুপস্থিত পেল।)\r\n- এখানে `Going` শব্দটি `he` (pronoun)-কে modify করছে। 'কে গিয়েছিল?' - উত্তর হলো `he`।\r\n- `Going there` অংশটি একটি **Participle Phrase** যা `he` pronoun-টির অবস্থা বা কাজকে বর্ণনা করছে। এটি main clause \"he found his friend absent\" এর কারণ বা সময় নির্দেশ করছে।\r\n- যেহেতু `going` শব্দটি `he`-কে বর্ণনা করছে (Adjective-এর মতো) এবং বাক্যের verb-কে modify করছে (Adverb-এর মতো), তাই এটি একটি Present Participle।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** - **Gerund Test:** `verb+ing` যুক্ত শব্দটিকে `it` বা\r\n  `something` দিয়ে প্রতিস্থাপন করা গেলে, সেটি Gerund। (e.g., **Swimming** is fun\r\n  -> **It** is fun). - **Participle Test:** `verb+ing` যুক্ত অংশটি যদি 'করার\r\n  সময়', 'করার কারণে', 'করতে করতে' - এই ধরনের অর্থ প্রকাশ করে এবং কোনো\r\n  Noun/Pronoun-এর কাজকে বোঝায়, তবে সেটি Participle। (e.g., **Going there** ->\r\n  **When he was going there**...).\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) gerund:** `going` এখানে বাক্যের subject বা object নয়। বাক্যের subject হলো `he` এবং verb হলো `found`।\r\n- **(c) noun:** Gerund একটি verbal noun, কিন্তু `going` এখানে noun হিসেবে কাজ করছে না।\r\n- **(d) past participle:** Past Participle সাধারণত verb-এর 3rd form (e.g., gone, broken, finished) হয় এবং কাজটি সম্পন্ন বা passive বোঝায়। `going` একটি present participle।\r\n\r\n### আরও উদাহরণ (More Examples)\r\n\r\n- **Walking** along the road, I saw a dead snake. (Participle)\r\n- The boy **playing** in the field is my brother. (Participle modifying 'boy')\r\n- **Reading** is my hobby. (Gerund as subject)";
