export const sourceCode = "---\r\ntitle: \"He killed himself. Here, 'himself' is a...\"\r\ndescription: \"'He killed himself' বাক্যে 'himself' কোন ধরনের pronoun তা সহজ বাংলায় ব্যাখ্যা ও উদাহরণসহ। BCS ও প্রাথমিক শিক্ষক নিয়োগ পরীক্ষার জন্য প্রস্তুতি।\"\r\ntags:\r\n  [\r\n    \"BCS\",\r\n    \"Parts of Speech\",\r\n    \"Reflexive Pronoun\",\r\n    \"English Grammar\",\r\n    \"প্রাথমিক বিদ্যালয় শিক্ষক\",\r\n    \"ইংরেজি ব্যাকরণ\",\r\n  ]\r\n---\r\n\r\n# He killed himself. Here, \"himself\" is a...\r\n\r\nপ্রশ্ন: He killed himself. এখানে \"himself\" কোন ধরনের pronoun?\r\n\r\n(ক) Reflexive Pronoun ✅  \r\n(খ) Personal Pronoun  \r\n(গ) Demonstrative Pronoun  \r\n(ঘ) Interrogative Pronoun\r\n\r\nযে প্রোনাউন বাক্যের সাব্জেক্টের করা কাজ, ঐ সাব্জেক্টের দিকেই নির্দেশ করে এবং যার শেষে \"self\" বা \"selves\" থাকে, তাকে **Reflexive Pronoun** বলে।\r\n\r\n<AudioPlayer\r\n  src=\"/audios/reflexive-pronoun.wav\"\r\n  title=\"Reflexive Pronoun Explanation\"\r\n/>\r\n\r\n---\r\n\r\n## 🔥 **Reflexive Pronoun কী? (Easy Bangla Explanation)**\r\n\r\nযে pronoun–এ শেষে **self / selves** থাকে এবং বাক্যের subject–কে আবার সেই subject-এর দিকেই ফিরিয়ে দেয়, তাকে **Reflexive Pronoun** বলে।\r\n\r\n👉 Reflexive মানে **\"নিজেকে\"** বা **\"নিজেদের\"**\r\n\r\nযেমন:\r\n\r\n- I hurt **myself**. (আমি নিজেকে আঘাত করেছি)\r\n- She blamed **herself**. (সে নিজেকে দোষ দিল)\r\n- They enjoyed **themselves**. (তারা নিজেদের উপভোগ করল)\r\n\r\nএগুলো subject-এর কাজ আবার subject-এর দিকে ফিরে আসে।\r\n\r\n---\r\n\r\n### 🚀 “He killed himself” কেন Reflexive Pronoun?\r\n\r\nএখানে:\r\n\r\n- Subject = **He**\r\n- Object = **himself**\r\n- Object টি **He**—এই subject-এর দিকেই নির্দেশ করছে।\r\n- এবং pronoun টি শেষ হয়েছে **self** দিয়ে।\r\n\r\n👉 তাই এটি Reflexive Pronoun।\r\n\r\n---\r\n\r\n### 🎯 Reflexive Pronoun-এর পূর্ণ তালিকা (BCS-এর জন্য Must Memorize)\r\n\r\n| Subject      | Reflexive Pronoun |\r\n| ------------ | ----------------- |\r\n| I            | myself            |\r\n| you          | yourself          |\r\n| he           | himself           |\r\n| she          | herself           |\r\n| it           | itself            |\r\n| we           | ourselves         |\r\n| they         | themselves        |\r\n| you (plural) | yourselves        |\r\n\r\n**Tip:** শেষে শুধু self/selves দেখলেই Reflexive হওয়ার সম্ভাবনা ৯০%।\r\n\r\n---\r\n\r\n### 🧠 Technique Hack: Reflexive Pronoun চেনার সহজ ৩টি নিয়ম\r\n\r\n#### **👉 Rule 1: শেষে self / selves থাকবে।**\r\n\r\nmyself, yourself, himself, herself, itself, ourselves, yourselves, themselves\r\n\r\n#### **👉 Rule 2: Subject = Object (একই হবে)**\r\n\r\nHe → himself\r\nShe → herself\r\nThey → themselves\r\nI → myself\r\n\r\nযদি subject–এর দিকেই ফিরে যায়, তবে Reflexive।\r\n\r\n#### **👉 Rule 3: “নিজেকে/নিজেদের” অনুবাদে আসে**\r\n\r\n**বাংলায় চিন্তা করুন → “নিজেকে”, “নিজেদের”**\r\nএগুলো এলেই Reflexive।\r\n\r\n---\r\n\r\n## 💡 Extra Super Hack (Exam-এ ৩ SEC-এ ধরার কৌশল)\r\n\r\n**যে জায়গায় pronoun আছে সেটি বাদ দিয়ে বাংলা অনুবাদ করুন।**\r\n\r\nযেমন:\r\n\"He killed **_.\" → সে হত্যা করল _**।\r\nএখন “self” দেখে বুঝে নিন → নিজেরেকেই হত্যা করেছে।\r\n\r\n👉 তাহলে Reflexive Pronoun.\r\n\r\n---\r\n\r\n## ⭐ Reflexive Pronoun vs অন্য Pronoun (Confusion Clear)\r\n\r\n| Pronoun Type          | Example    | কেন Reflexive নয়                        |\r\n| --------------------- | ---------- | --------------------------------------- |\r\n| Personal Pronoun      | him        | subject-কে reflect করে না               |\r\n| Demonstrative Pronoun | this, that | self/selves নেই                         |\r\n| Interrogative Pronoun | who, what  | প্রশ্ন করে                              |\r\n| Reflexive Pronoun     | himself    | subject-এর কাজ subject-এর দিকে ফিরে যায় |\r\n\r\n---\r\n\r\n## 📌 BCS & Primary Teacher Recruitment Exam Tips\r\n\r\n### ✔ সর্বদা self/selves খুঁজুন\r\n\r\nএটি Reflexive Pronoun-এর সবচেয়ে বড় clue।\r\n\r\n### ✔ Grammar এ object subject-এ ফিরে যাচ্ছে কিনা দেখুন।\r\n\r\n### ✔ অনুবাদ কৌশল: \"নিজেকে/নিজেদের\" শব্দ বসে কি না দেখুন।\r\n\r\n### ✔ Reflexive Pronoun দিয়ে সাধারণত action নিজের উপর করা হয়\r\n\r\nhurt himself, kill himself, blame herself, enjoy themselves, wash yourself\r\n\r\n---\r\n\r\n# 📝 Practice Questions (Solve & Master)\r\n\r\n1. She prepared **herself** for the exam.\r\n2. I looked at **myself** in the mirror.\r\n3. They helped **themselves**.\r\n4. The cat cleaned **itself**.\r\n\r\n👉 সবগুলোতেই self/selves → Reflexive Pronoun।\r\n\r\n---\r\n\r\n## 🎉 Final Summary (One Line Formula)\r\n\r\n**Subject + Verb + self/selves = Reflexive Pronoun**\r\n\r\n\"He killed himself\" → তাই **himself = Reflexive Pronoun** ✔";
