export const sourceCode = "---\r\ntitle: কোনটি Collective Noun? | BCS & Primary School Teacher English Grammar\r\ndescription: Learn the correct answer to the question 'Which one is a Collective Noun?' with clear Bangla explanation, concept, tricks, and memory hacks for BCS and Primary Teacher exam preparation.\r\ntags: Collective Noun in Bangla, BCS English Grammar, Primary School Teacher English, Noun Types, English Grammar Tricks, Collective Noun Examples\r\n---\r\n\r\n# 📘 কোনটি Collective Noun?\r\n\r\n(ক) Library  \r\n(খ) Boy  \r\n(গ) Month  \r\n(ঘ) Books\r\n\r\n✅ **সঠিক উত্তর: (ক) Library**\r\n\r\n---\r\n\r\n## ✅ কেন Library হলো Collective Noun?\r\n\r\n**Collective Noun** হলো এমন Noun, যা **একদল মানুষ, পশু বা একদল জিনিসকে একই নাম বোঝায়**।  \r\nযেমন— class, team, crowd, herd, fleet, library ইত্যাদি।\r\n\r\n👉 **Library** = অনেক বইয়ের সমষ্টি → তাই Collective Noun  \r\n👉 Boy = ব্যক্তি → Common Noun  \r\n👉 Month = সময়/মাস → Abstract/Common Noun  \r\n👉 Books = বইগুলো → Common Noun (plural)\r\n\r\nযেমন:\r\n\r\n- Team = অনেক খেলোয়াড়\r\n- Army = অনেক সৈনিক\r\n- Flock = অনেক পাখি\r\n- Library = অনেক বইয়ের সমষ্টি\r\n\r\nঅতএব, এখানে শুধু **Library** এমন একটি শব্দ যা অনেক বইয়ের সমষ্টি বোঝায় → **Collective Noun**।\r\n\r\n---\r\n\r\n## 💡 Collective Noun কী? (Easy Bangla Explanation)\r\n\r\nযে Noun **অনেকগুলোর সমষ্টি** বোঝায়, তাকে Collective Noun বলে।  \r\nউদাহরণ:\r\n\r\n- Library = অনেক বই\r\n- Crowd = অনেক লোক\r\n- Fleet = অনেক জাহাজ\r\n- Pack = অনেক নেকড়ে (wolves)\r\n- Army = সৈন্যদের দল\r\n- jury = বিচারকমন্ডলী\r\n- Audience = শ্রোতামন্ডলী\r\n\r\n**Rule:** একদল/একগুচ্ছ/একসংখ্যক → Collective Noun\r\n\r\n---\r\n\r\n## 🧠 Memory Hack (Exam Trick)\r\n\r\n### 🔹 Trick 1: যেগুলো \"একদল/একগুচ্ছ\" বোঝায় → Collective Noun\r\n\r\n- একদল বই = **Library**\r\n- একদল ছাত্র = **Class**\r\n- একদল পাখি = **Flock**\r\n- একগুচ্ছ চাবি = **Bunch**\r\n\r\n### 🔹 Trick 2: নামের শেষে “-ary” থাকলে প্রায়ই Collective Noun\r\n\r\nযেমন—\r\n\r\n- Library\r\n- Glossary\r\n- Dictionary (কিছু ক্ষেত্রে collection বোঝায়)\r\n\r\n### 🔹 Trick 3: Plural শব্দ সাধারণত Collective নয়\r\n\r\n- Books ❌ (এটি plural noun)\r\n- Students ❌\r\n- Cars ❌\r\n\r\nCollective noun → সাধারণত **একবচন**, কিন্তু **দল** বোঝায়।\r\n\r\n---\r\n\r\n## 🌿 More Examples (Exam-Focused)\r\n\r\n| Collective Noun | বোঝায়                    |\r\n| --------------- | ------------------------ |\r\n| Library         | Books (বইয়ের সমষ্টি)     |\r\n| Family          | Members (পরিবারের সদস্য) |\r\n| Team            | Players                  |\r\n| Army            | Soldiers                 |\r\n| Fleet           | Ships                    |\r\n| Crowd           | People                   |\r\n| Class           | Student                  |\r\n\r\n---\r\n\r\n## 🎯 Final Takeaway\r\n\r\n- **Library = Collective Noun**, কারণ এটি অনেক বইয়ের সমষ্টিকে বোঝায়।\r\n- Collective Noun সবসময় **একবচন হলেও অর্থে বহুবচন**।\r\n- Plural শব্দ কখনো Collective Noun নয়।\r\n- Exam-এ Trick: “একদল/একগুচ্ছ/একসংখ্যক = Collective Noun”\r\n\r\n---\r\n\r\n**এই পৃষ্ঠা BCS প্রিলি, প্রাইমারি স্কুল শিক্ষক নিয়োগ ও সকল সরকারি চাকরির পরীক্ষার ইংরেজি অংশের জন্য অত্যন্ত গুরুত্বপূর্ণ।**";
