export const sourceCode = "---\r\ntitle: \"Parts of Speech: 'manned' as a Verb in 'The post must be manned'\"\r\ndescription: \"'The post must be manned' - এই বাক্যে 'manned' শব্দটি Noun না হয়ে Verb কেন? Passive Voice এবং Conversion এর নিয়ম সহজ বাংলায় শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"manned as a verb\",\r\n    \"parts of speech\",\r\n    \"conversion in grammar\",\r\n    \"passive voice with modals\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: ‘The post must be manned immediately.’ Here the underlined word is —\r\n\r\n**Question:** ‘The post must be manned immediately.’ Here the underlined word is —\r\n(a) a noun\r\n(b) a verb\r\n(c) an adverb\r\n(d) an adjective\r\n\r\n**Correct Answer:** (b) a verb\r\n\r\n### কেন 'manned' একটি Verb? (Why is 'manned' a Verb?)\r\n\r\nএই প্রশ্নের উত্তরটি দুটি ধারণার উপর নির্ভরশীল: **Conversion** এবং **Passive Voice**।\r\n\r\n**1. Conversion (Zero Derivation):**\r\n`man` শব্দটি সাধারণত Noun (মানুষ) হিসেবে ব্যবহৃত হলেও, একে Verb হিসেবেও ব্যবহার করা যায়। Verb হিসেবে `to man` এর অর্থ হলো কোনো স্থান বা দায়িত্বে লোকবল সরবরাহ করা বা কর্মী নিয়োগ করা (to staff a place or post)।\r\n\r\n**2. Passive Voice with Modals:**\r\nবাক্যটি Passive Voice-এ লেখা হয়েছে। Modal verb (`must`, `can`, `should` ইত্যাদি) যুক্ত Passive Voice-এর গঠন হলো:\r\n**Structure:** Subject + Modal Verb + `be` + **Past Participle (V3)**\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- **Subject:** `The post`\r\n- **Modal Verb:** `must`\r\n- **Auxiliary:** `be`\r\n- **Past Participle (V3):** `manned`\r\n- **Adverb:** `immediately`\r\n\r\nএখানে `manned` শব্দটি `to man` verb-টির Past Participle (V3) form। এটি Passive Voice-এর গঠন অনুযায়ী `must be`-এর পরে বসেছে। যেহেতু এটি একটি ক্রিয়ার অংশ, তাই এর Parts of Speech হলো **Verb**।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** `can be`, `must be`, `should be`, `will be` - এই ধরনের\r\n  গঠনের পরে যে শব্দটি আসে, তা প্রায় সবসময়ই একটি Verb-এর Past Participle (V3) রূপ\r\n  হয়।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) a noun:** `manned` এখানে কোনো ব্যক্তি, বস্তু বা স্থানের নাম বোঝাচ্ছে না। এটি একটি কাজের অবস্থা বর্ণনা করছে।\r\n- **(c) an adverb:** Adverb সাধারণত verb-কে modify করে (e.g., `immediately` শব্দটি `must be manned`-কে modify করছে)। `manned` নিজে কোনো modifier নয়।\r\n- **(d) an adjective:** যদিও Past Participle অনেক সময় Adjective-এর কাজ করে (e.g., a **manned** spacecraft), কিন্তু এই বাক্যে এটি `be`-এর পরে বসে verb phrase (`must be manned`) গঠন করেছে, তাই এটি verb।\r\n\r\n### Conversion-এর আরও উদাহরণ\r\n\r\n- **Water (N) -> to water (V):** Please **water** the plants.\r\n- **Book (N) -> to book (V):** I will **book** a table.\r\n- **Chair (N) -> to chair (V):** Who will **chair** the meeting?";
