export const sourceCode = "---\r\ntitle: \"Correct Sentence: 'Each of the three boys got a prize'\"\r\ndescription: \"Each of the three boys got a prize - কেন এই বাক্যটি শুদ্ধ? Each, Every, All, A few এর ব্যবহার এবং পার্থক্য সহজ বাংলায় শিখুন। BCS, Primary পরীক্ষার জন্য গুরুত্বপূর্ণ।\"\r\nkeywords:\r\n  [\r\n    \"each of the three boys\",\r\n    \"each vs every\",\r\n    \"all vs each\",\r\n    \"english grammar correction\",\r\n    \"bcs english\",\r\n    \"pronoun usage\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: Choose the correct sentence –\r\n\r\n**Question:** Choose the correct sentence –\r\n(a) A few of the three boys got a prize.\r\n(b) Every of the three boys got a prize.\r\n(c) All of the three boys got a prize.\r\n(d) Each of the three boys got a prize.\r\n\r\n**Correct Answer:** (d) Each of the three boys got a prize.\r\n\r\n### কেন (d) সঠিক উত্তর? (Why is (d) correct?)\r\n\r\nএই প্রশ্নের উত্তরটি `Each`, `Every`, `All`, এবং `A few`-এর ব্যবহারের সুনির্দিষ্ট নিয়মের উপর নির্ভর করে।\r\n\r\n---\r\n\r\n### 1. `Each` vs. `Every`\r\n\r\n- **Each:** দুই বা তার বেশি সংখ্যক ব্যক্তি বা বস্তুর প্রত্যেককে **আলাদাভাবে** বা **পৃথকভাবে** বোঝাতে ব্যবহৃত হয়। `Each`-এর পরে `of the` + plural noun বসতে পারে।\r\n  - _Example:_ **Each of the students** was given a book.\r\n- **Every:** তিন বা তার বেশি সংখ্যক ব্যক্তি বা বস্তুর একটি group-কে সামগ্রিকভাবে বোঝাতে ব্যবহৃত হয়। `Every`-এর পরে **`of the` বসে না**। `Every`-এর পরে সরাসরি singular noun বসে।\r\n  - _Example:_ **Every student** was given a book. (Not: Every of the students)\r\n\r\n**বিশ্লেষণ:** Option (b) `Every of the three boys...` ভুল, কারণ `Every`-এর পরে `of the` বসে না।\r\n\r\n---\r\n\r\n### 2. `Each` vs. `All`\r\n\r\n- **Each:** Group-এর প্রত্যেক সদস্যকে পৃথকভাবে বোঝায় এবং singular হিসেবে গণ্য হয়। (Emphasizes individuality).\r\n- **All:** Group-এর সকল সদস্যকে **একসাথে** বা **সমষ্টিগতভাবে** বোঝায় এবং plural হিসেবে গণ্য হয়।\r\n  - _Example:_ **All of the boys** got prizes.\r\n\r\n**বিশ্লেষণ:** Option (c) `All of the three boys got a prize` ব্যাকরণগতভাবে শুদ্ধ। কিন্তু যখন কোনো পুরস্কার বা কোনো কিছু group-এর প্রত্যেক সদস্যকে পৃথকভাবে দেওয়া হয়, তখন `Each` ব্যবহার করাটা الأكثر স্বাভাবিক এবং অর্থকে আরও সুস্পষ্ট করে। `Each` জোর দেয় যে, তিনজনের **প্রত্যেকেই** একটি করে পুরস্কার পেয়েছে। `All` জোর দেয় যে, সমষ্টিগতভাবে সবাই পুরস্কার পেয়েছে।\r\n\r\n---\r\n\r\n### 3. `A few`\r\n\r\n- **A few:** অনির্দিষ্ট অল্প সংখ্যক (সাধারণত তিন বা তার বেশি) বোঝাতে ব্যবহৃত হয়। এর অর্থ 'কিছু সংখ্যক', 'সবাই নয়'।\r\n  - _Example:_ **A few of the boys** got prizes. (মানে, কিছু ছেলে পুরস্কার পেয়েছে, সবাই পায়নি)।\r\n\r\n**বিশ্লেষণ:** Option (a) `A few of the three boys...` ব্যাকরণগতভাবে শুদ্ধ, কিন্তু এর অর্থ ভিন্ন। এর অর্থ হলো \"তিনজন ছেলের মধ্যে কয়েকজন (সবাই নয়) পুরস্কার পেয়েছে\"। কিন্তু যখন আমরা বোঝাতে চাই যে প্রত্যেকেই পেয়েছে, তখন এটি সঠিক নয়।\r\n\r\n---\r\n\r\n### চূড়ান্ত সিদ্ধান্ত (Final Decision)\r\n\r\n- Option (a) এবং (c) ব্যাকরণগতভাবে শুদ্ধ হলেও, তারা ভিন্ন অর্থ প্রকাশ করে।\r\n- Option (b) ব্যাকরণগতভাবে ভুল।\r\n- Option (d) `Each of the three boys got a prize` বাক্যটি সুস্পষ্টভাবে প্রকাশ করে যে, তিনজন ছেলের **প্রত্যেকেই** একটি করে পুরস্কার পেয়েছে। প্রতিযোগিতামূলক পরীক্ষায়, যেখানে group-এর প্রত্যেক সদস্যকে পৃথকভাবে নির্দেশ করা হয়, সেখানে `Each` প্রায়শই সবচেয়ে সঠিক এবং পছন্দের উত্তর হিসেবে বিবেচিত হয়। এটি individual distribution-কে নির্দেশ করে।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** - `Every` + `of the` = ভুল। - `Each of the...` =\r\n  group-এর প্রত্যেককে আলাদা করে বোঝায়। - `All of the...` = group-এর সবাইকে\r\n  একসাথে বোঝায়। - `A few of the...` = group-এর কয়েকজনকে (সবাইকে নয়) বোঝায়।\r\n</Callout>";
