export const sourceCode = "---\r\ntitle: \"Who's that? - বাক্যে 'that' কোন Part of Speech? | Demonstrative Pronoun\"\r\ndescription: \" 'Who's that?' - এই বাক্যে 'that' শব্দটি কেন একটি Pronoun? Demonstrative Pronoun এবং Demonstrative Adjective-এর মধ্যে পার্থক্য সহজ বাংলায় শিখুন। BCS, Primary পরীক্ষার জন্য গুরুত্বপূর্ণ।\"\r\nkeywords:\r\n  [\r\n    \"demonstrative pronoun\",\r\n    \"that as a pronoun\",\r\n    \"parts of speech mcq\",\r\n    \"english grammar bcs\",\r\n    \"demonstrative adjective vs pronoun\",\r\n    \"who's that\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: ‘Who’s that?’ in this sentence ‘that’ is a/an-\r\n\r\n**Question:** ‘Who’s that?’ in this sentence ‘that’ is a/an-\r\n(a) pronoun\r\n(b) conjunction\r\n(c) adjective\r\n(d) adverb\r\n\r\n**Correct Answer:** (a) pronoun\r\n\r\n### কেন 'that' একটি Pronoun? (Why is 'that' a Pronoun?)\r\n\r\nএই বাক্যে 'that' শব্দটি কোনো ব্যক্তি বা বস্তুর **পরিবর্তে** বসে তাকে নির্দেশ করছে। যে Word কোনো Noun-এর পরিবর্তে বসে, তাকে Pronoun বলে।\r\n\r\n**Key Concept: Demonstrative Pronoun vs. Demonstrative Adjective**\r\n\r\n`This`, `that`, `these`, `those` - এই শব্দগুলো Demonstrative (নির্দেশক) হিসেবে ব্যবহৃত হয়। এরা কখন Pronoun এবং কখন Adjective হবে, তা বোঝা খুব জরুরি।\r\n\r\n1.  **Demonstrative Pronoun:** যখন `this`, `that`, `these`, `those` শব্দগুলো কোনো Noun-এর পরিবর্তে **একা একা** বসে, তখন এরা Pronoun।\r\n\r\n    - **Example:** \"**That** is my car.\" (ঐটি আমার গাড়ি)। এখানে 'That' শব্দটি 'car' (Noun)-এর পরিবর্তে বসেছে।\r\n    - **Our Question:** \"Who's **that**?\" (ঐ ব্যক্তিটি কে?) এখানে 'that' কোনো একজন ব্যক্তি (person) -এর পরিবর্তে বসেছে।\r\n\r\n2.  **Demonstrative Adjective:** যখন `this`, `that`, `these`, `those` শব্দগুলো একটি Noun-এর **ঠিক আগে** বসে সেই Noun-কে modify (বিশেষায়িত) করে, তখন এরা Adjective।\r\n    - **Example:** \"**That car** is mine.\" (ঐ গাড়িটি আমার)। এখানে 'that' শব্দটি 'car' (Noun)-এর আগে বসে গাড়িটিকে নির্দিষ্ট করে দিচ্ছে।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** - যদি `this/that/these/those`-এর ঠিক পরেই একটি **Verb**\r\n  (যেমন: is, are, was) থাকে, তবে শব্দটি **Pronoun**। - যদি\r\n  `this/that/these/those`-এর ঠিক পরেই একটি **Noun** থাকে, তবে শব্দটি\r\n  **Adjective**।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(b) conjunction:** Conjunction দুটি শব্দ বা বাক্যকে যুক্ত করে (e.g., and, but, or)। 'that' এখানে কিছু যুক্ত করছে না।\r\n- **(c) adjective:** Adjective হতে হলে 'that'-এর পরে একটি Noun থাকা আবশ্যক ছিল (e.g., \"Who is **that person**?\")।\r\n- **(d) adverb:** Adverb কোনো verb, adjective বা অন্য adverb-কে modify করে। 'that' এখানে তা করছে না।\r\n\r\n### আরও উদাহরণ (More Examples)\r\n\r\n- **This** is a beautiful gift. (**Pronoun**)\r\n- **This gift** is beautiful. (**Adjective**)\r\n- **These** are fresh apples. (**Pronoun**)\r\n- **These apples** are fresh. (**Adjective**)\r\n\r\nএই সহজ নিয়মটি মনে রাখলে Parts of Speech সনাক্তকরণের প্রশ্নগুলো সহজেই সমাধান করা যায়।";
