export const sourceCode = "---\r\ntitle: \"Participle vs Gerund: 'Hearing the noise, the boy woke up'\"\r\ndescription: \"'Hearing the noise, the boy woke up' - এই বাক্যে 'hearing' শব্দটি Participle কেন? Gerund এবং Participle এর মধ্যে পার্থক্য সহজ বাংলায় শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"present participle\",\r\n    \"participle vs gerund\",\r\n    \"hearing the noise\",\r\n    \"participle phrase\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: Hearing the noise, the boy woke up. Here the word ‘hearing’ is a/an —\r\n\r\n**Question:** Hearing the noise, the boy woke up. Here the word ‘hearing’ is a/an —\r\n(a) noun\r\n(b) participle\r\n(c) gerund\r\n(d) verb\r\n\r\n**Correct Answer:** (b) participle\r\n\r\n### কেন 'hearing' একটি Participle?\r\n\r\nএই প্রশ্নটি আবারও **Gerund** এবং **Participle**-এর মধ্যে পার্থক্য নির্ণয়ের উপর জোর দেয়।\r\n\r\n**Key Concept: Gerund (Noun Function) vs. Participle (Adjective/Adverbial Function)**\r\n\r\n1.  **Gerund:** একটি `verb+ing` রূপ যা বাক্যে **Noun**-এর কাজ করে (subject, object ইত্যাদি)।\r\n2.  **Participle:** একটি `verb+ing` রূপ (Present Participle) যা বাক্যে **Adjective**-এর মতো কাজ করে (কোনো Noun/Pronoun-কে modify করে) অথবা **Adverb**-এর মতো কাজ করে (main verb-এর কারণ, সময় বা অবস্থা বর্ণনা করে)।\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- **Main Clause:** \"the boy woke up.\" (ছেলেটি জেগে উঠেছিল।)\r\n- **Subject:** `the boy`\r\n- **Phrase:** \"Hearing the noise\" (শব্দটি শুনে)\r\n\r\nএখানে `Hearing` শব্দটি `the boy` (Noun)-কে modify করছে। 'কে শুনেছিল?' - উত্তর হলো `the boy`। `Hearing the noise` phrase-টি ব্যাখ্যা করছে 'কেন' বা 'কখন' ছেলেটি জেগে উঠেছিল।\r\n\r\nযেহেতু `hearing` শব্দটি `the boy`-এর একটি কাজ বা অবস্থাকে বর্ণনা করছে এবং main clause-কে modify করছে, তাই এটি একটি **Participle** (specifically, a Present Participle)।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** Participle phrase-কে প্রায়ই `When`, `While`, `Because`,\r\n  `As` ইত্যাদি দিয়ে একটি পূর্ণাঙ্গ clause-এ রূপান্তর করা যায়। - **Hearing the\r\n  noise**, the boy woke up. - = **When the boy heard the noise**, he woke up. -\r\n  এই রূপান্তর সম্ভব হলে, শব্দটি Participle।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) noun / (c) gerund:** `hearing` এখানে বাক্যের subject বা object হিসেবে কাজ করছে না। এটি কোনো কিছুর নাম বোঝাচ্ছে না, বরং একটি কাজকে বর্ণনা করছে যা subject (`the boy`) করেছে।\r\n- **(d) verb:** যদিও `hearing` একটি verb থেকে উদ্ভূত, কিন্তু এটি বাক্যের main verb (finite verb) নয়। বাক্যের main verb হলো `woke up`। `hearing` এখানে একটি non-finite verb যা participle হিসেবে কাজ করছে।\r\n\r\n### আরও উদাহরণ\r\n\r\n- **Seeing** the police, the thief ran away. (Participle)\r\n- **Feeling** tired, I went to bed early. (Participle)\r\n- The girl **singing** on the stage is my sister. (Participle)";
