export const sourceCode = "---\r\ntitle: \"One should be careful about one's duty - 'one's' কেন ব্যবহৃত হয়?\"\r\ndescription: \"One should be careful about ____ duty - শূন্যস্থানে his, her, one's, the কোনটি বসবে? Indefinite Pronoun 'one' এর Possessive Form 'one's' ব্যবহারের নিয়মটি শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"one should do one's duty\",\r\n    \"indefinite pronoun one\",\r\n    \"possessive form of one\",\r\n    \"english grammar rules\",\r\n    \"bcs english preparation\",\r\n    \"pronoun agreement\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: One should be careful about \\_\\_\\_\\_ duty.\r\n\r\n**Question:** One should be careful about \\_\\_\\_\\_ duty.\r\n(a) his\r\n(b) her\r\n(c) one’s\r\n(d) the\r\n\r\n**Correct Answer:** (c) one’s\r\n\r\n### কেন 'one’s' সঠিক উত্তর? (Why is 'one’s' correct?)\r\n\r\nএই প্রশ্নের উত্তরটি **Indefinite Pronoun `one`**-এর ব্যবহারের একটি সুনির্দিষ্ট নিয়মের উপর ভিত্তি করে।\r\n\r\n**Key Concept: Pronoun Agreement with 'One'**\r\n\r\n1.  **Indefinite Pronoun 'One':** যখন `one` শব্দটি কোনো নির্দিষ্ট ব্যক্তিকে না বুঝিয়ে সাধারণভাবে 'যে কেউ' বা 'প্রত্যেকের' (any person in general) অর্থে ব্যবহৃত হয়, তখন এটি একটি Indefinite Pronoun।\r\n\r\n    - _Example:_ **One** should respect **one's** elders.\r\n\r\n2.  **Agreement Rule:** যখন `one` বাক্যের subject হিসেবে ব্যবহৃত হয়, তখন সেই বাক্যের পরবর্তী possessive pronoun (যার অর্থ 'তার') হিসেবে **`one's`** ব্যবহৃত হয়। `his` বা `her` ব্যবহার করা যায় না।\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- \"One should be careful...\" (একজনের সতর্ক থাকা উচিত...)। এখানে `One` কোনো নির্দিষ্ট পুরুষ বা মহিলাকে বোঝাচ্ছে না, বরং সাধারণভাবে 'প্রত্যেকের' কথা বলছে।\r\n- \"...about \\_\\_\\_\\_ duty.\" (...তার দায়িত্ব সম্পর্কে।)\r\n- যেহেতু subject হলো `one`, তাই নিয়ম অনুযায়ী possessive pronoun হবে `one's`।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** যদি কোনো বাক্যের Subject হিসেবে `One` (Indefinite\r\n  Pronoun) থাকে, তাহলে সেই বাক্যে Possessive Form হিসেবে `one's` এবং Reflexive\r\n  Pronoun হিসেবে `oneself` বসবে। - **Subject:** One - **Possessive:** One's -\r\n  **Reflexive:** Oneself\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) his:** যদিও কিছু ক্ষেত্রে `his` কে generic pronoun হিসেবে ব্যবহার করা হয়, কিন্তু `one` এর সাথে `one's` ব্যবহার করাই হলো Standard English-এর নিয়ম। `his` ব্যবহার করলে তা Informal বা American English-এ দেখা যেতে পারে, কিন্তু Formal/British English এবং পরীক্ষার জন্য `one's` ই শুদ্ধ।\r\n- **(b) her:** `her` শুধুমাত্র feminine (নারীবাচক) ক্ষেত্রে ব্যবহৃত হয়। `one` যেহেতু gender-neutral, তাই `her` ভুল।\r\n- **(d) the:** `the` একটি definite article। এখানে একটি possessive pronoun প্রয়োজন যা 'duty'-র মালিকানা বোঝাবে।\r\n\r\n### ব্যতিক্রম (Exception)\r\n\r\nযদি `one` এর সাথে কোনো Noun যুক্ত থাকে (e.g., `one of the boys`, `one of the students`), তখন `one` আর Indefinite Pronoun থাকে না। সেক্ষেত্রে Noun-এর gender অনুযায়ী `his` বা `her` বসে।\r\n\r\n- _Example:_ **One of the boys** should do **his** duty. (এখানে 'boys' এর জন্য 'his' বসেছে)।\r\n\r\nএই পার্থক্যটি মনে রাখা খুবই জরুরি।";
