export const sourceCode = "---\r\ntitle: Personal Pronoun কি? সহজ ব্যাখ্যা ও BCS/প্রাথমিক শিক্ষক নিয়োগ পরীক্ষার প্রস্তুতি\r\ndescription: Personal pronoun কি এবং কিভাবে BCS বা Primary School Teacher নিয়োগ পরীক্ষার জন্য এই ধরনের প্রশ্ন দ্রুত মনে রাখা যায়। সহজ বাংলা ব্যাখ্যা ও উদাহরণ সহ।\r\ntags: BCS, Parts of Speech, Personal Pronoun, প্রাথমিক বিদ্যালয় শিক্ষক, ইংরেজি ব্যাকরণ\r\n---\r\n\r\n# নিচের কোনটি Personal Pronoun?\r\n\r\nপ্রশ্ন: নিচের কোনটি **personal pronoun**?\r\n\r\n(ক) Any\r\n(খ) This\r\n(গ) Who\r\n(ঘ) You ✅\r\n\r\n**সঠিক উত্তর: (ঘ) You**\r\n\r\n---\r\n\r\n## Personal Pronoun কী?\r\n\r\n**Personal pronoun** হলো সেই শব্দ যা **কারো নামের পরিবর্তে ব্যবহার করা হয়**। অর্থাৎ, আমরা যখন কারো নাম বলার পরিবর্তে “আমি”, “তুমি”, “সে”, “তারা” ইত্যাদি শব্দ ব্যবহার করি, তখন এগুলো personal pronoun।\r\n\r\n**বাংলা উদাহরণ:**\r\n\r\n- আমি → I\r\n- তুমি → You\r\n- সে → He / She\r\n- আমরা → We\r\n- তারা → They\r\n\r\n**ইংরেজিতে উদাহরণ:**\r\n\r\n- I am happy. (আমি খুশি)\r\n- You are a teacher. (তুমি একজন শিক্ষক)\r\n- He is playing. (সে খেলছে)\r\n\r\n---\r\n\r\n## কেন (ঘ) You হলো personal pronoun?\r\n\r\n- **You** হলো একজন বা একাধিক ব্যক্তিকে নির্দেশ করে।\r\n- এটি কারো নামের পরিবর্তে ব্যবহার হয়।\r\n- অন্য অপশনগুলো কেন personal pronoun নয়:\r\n\r\n  - **Any** → Quantifier / Determiner, সংখ্যা বা পরিমাণ বোঝায়।\r\n  - **This** → Demonstrative pronoun, কোন বস্তুর বা বিষয় নির্দেশ করে।\r\n  - **Who** → Interrogative pronoun, প্রশ্ন করার জন্য ব্যবহার হয়।\r\n\r\n---\r\n\r\n## Personal Pronoun সহজে মনে রাখার কৌশল (Hack)\r\n\r\n1. **আমি, তুমি, সে, আমরা, তারা মনে রাখুন** → এগুলোই সব personal pronoun।\r\n2. যদি প্রশ্নে “কারো নামের পরিবর্তে” বলা থাকে → personal pronoun চিনুন।\r\n3. **Any, This, Who** কখনই personal pronoun হয় না, এগুলো আলাদা ধরনের pronoun বা determiner।\r\n4. প্র্যাকটিস করুন ছোট ছোট বাক্যে → নিজেকে বলতে বলুন: “I am happy”, “You are smart”।\r\n\r\n**BCS/Primary School Teacher পরীক্ষায় টিপস:**\r\n\r\n- প্রায়ই personal pronoun চাওয়া হয় একাধিক-choice প্রশ্নে।\r\n- প্রশ্নে “প্রথমে ব্যক্তি” বা “নাম না বলে কাউকে নির্দেশ” এমন শব্দ থাকলে → তা personal pronoun।\r\n- প্রতিদিন ৫ মিনিট অনুশীলন করুন personal pronoun-এর flashcards দিয়ে।\r\n\r\n---\r\n\r\n✅ **Quick Memorization Tip:**\r\n\r\n- **আমি, তুমি, সে, আমরা, তারা** → শুধু এই ৫টি (বা তাদের ইংরেজি equivalents) মনে রাখলেই সব personal pronoun প্রশ্নে সফল হবেন।\r\n\r\n---";
