export const sourceCode = "import { Callout } from \"nextra/components\";\r\n\r\n# Ditransitive Verb: Indirect and Direct Object\r\n\r\n**প্রশ্ন:** He gave \\_\\_\\_\\_.\r\n\r\n(a) to the class a tough assignment\r\n(b) a tough assignment for the class\r\n(c) the class a tough assignment\r\n(d) an assignment tough to the class\r\n\r\n**সঠিক উত্তর:** (c) the class a tough assignment\r\n\r\n---\r\n\r\n### **ব্যাখ্যা**\r\n\r\nএই প্রশ্নটি Ditransitive Verb (যে verb দুটি object গ্রহণ করে) এর সঠিক বাক্য গঠন পরীক্ষা করার জন্য ডিজাইন করা হয়েছে। `give` একটি ditransitive verb।\r\n\r\n#### **Key Concepts:**\r\n\r\n1.  **Direct Object (DO):** Verb-কে 'কী' (what) দিয়ে প্রশ্ন করলে যে উত্তর পাওয়া যায়।\r\n    - He gave **what**? -> a tough assignment (এটি Direct Object)।\r\n2.  **Indirect Object (IO):** Verb-কে 'কাকে' (to whom) বা 'কার জন্য' (for whom) দিয়ে প্রশ্ন করলে যে উত্তর পাওয়া যায়।\r\n    - He gave the assignment **to whom**? -> the class (এটি Indirect Object)।\r\n\r\n#### **Ditransitive Verb-এর দুটি সাধারণ গঠন:**\r\n\r\n**Structure 1: `Subject + Verb + Indirect Object + Direct Object`**\r\n\r\n- এই গঠনে কোনো preposition (যেমন `to` বা `for`) বসে না।\r\n- **উদাহরণ:** He gave **me** (IO) **a book** (DO).\r\n\r\n**Structure 2: `Subject + Verb + Direct Object + Preposition + Indirect Object`**\r\n\r\n- এই গঠনে Direct Object-এর পরে `to` বা `for` preposition বসে।\r\n- **উদাহরণ:** He gave **a book** (DO) **to me** (IO).\r\n\r\n#### **অপশনগুলোর বিশ্লেষণ:**\r\n\r\n- **(a) to the class a tough assignment**: ভুল গঠন। Prepositional phrase (`to the class`) সাধারণত Direct Object-এর পরে বসে।\r\n- **(b) a tough assignment for the class**: এটি ব্যাকরণগতভাবে সঠিক (Structure 2)। তবে `give` verb-এর সাথে সাধারণত `to` preposition ব্যবহৃত হয়, `for` নয়। (e.g., gave it **to** the class)।\r\n- **(c) the class a tough assignment**: এটি Structure 1 (`IO + DO`) অনুসরণ করে এবং সম্পূর্ণ সঠিক। এটি সবচেয়ে সাধারণ এবং স্বাভাবিক গঠন।\r\n- **(d) an assignment tough to the class**: ভুল শব্দক্রম। Adjective `tough` Noun `assignment`-এর আগে বসবে (`a tough assignment`)।\r\n\r\n#### **উপসংহার:**\r\n\r\nঅপশন (c) হলো ditransitive verb `give`-এর সবচেয়ে সঠিক এবং প্রচলিত ব্যবহার। এখানে `the class` হলো Indirect Object এবং `a tough assignment` হলো Direct Object।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Memorize:** মনে রাখবেন, ব্যক্তি (Indirect Object) যদি আগে আসে, তাহলে\r\n  preposition লাগে না। বস্তু (Direct Object) যদি আগে আসে, তাহলে ব্যক্তির আগে\r\n  `to` বা `for` preposition লাগে। * He gave **the class** (person) **the\r\n  assignment** (thing). -> No preposition. * He gave **the assignment** (thing)\r\n  **to the class** (person). -> Preposition needed.\r\n</Callout>";
