export const sourceCode = "---\r\ntitle: \"Modal Verb Rule: 'Everyone should speak the truth'\"\r\ndescription: \"'Everyone should ___ the truth.' - কেন 'speak' সঠিক উত্তর? Modal Verb (should, can, must) এর পর Verb-এর Base Form ব্যবহারের নিয়মটি সহজ বাংলায় শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"modal verb rule\",\r\n    \"should speak\",\r\n    \"verb base form\",\r\n    \"modal auxiliary\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: Everyone should \\_\\_\\_ the truth.\r\n\r\n**Question:** Everyone should \\_\\_\\_ the truth.\r\n(a) spoken\r\n(b) to speak\r\n(c) speak\r\n(d) speaking\r\n\r\n**Correct Answer:** (c) speak\r\n\r\n### কেন 'speak' সঠিক উত্তর? (Why is 'speak' correct?)\r\n\r\nএই প্রশ্নের উত্তরটি **Modal Auxiliary Verbs**-এর একটি মৌলিক এবং গুরুত্বপূর্ণ নিয়মের উপর ভিত্তি করে।\r\n\r\n**Key Concept: Modals are followed by the Base Form of the Verb**\r\n\r\n1.  **Modal Verbs কী?** `can`, `could`, `may`, `might`, `shall`, `should`, `will`, `would`, `must` - এই verb-গুলোকে Modal Auxiliary Verb বলা হয়। এরা মূল verb-কে সাহায্য করে এবং বাক্যে বক্তার মনোভাব (ক্ষমতা, সম্ভাবনা, বাধ্যবাধকতা, পরামর্শ ইত্যাদি) প্রকাশ করে।\r\n\r\n2.  **The Rule:** Modal verb-এর পরে সবসময় মূল verb-এর **base form (V1)** বসে। Base form হলো verb-এর মূল রূপ, যার সাথে `s/es`, `ing`, `ed` যুক্ত হয় না এবং যার আগে `to` বসে না।\r\n\r\n**Structure:** Subject + Modal Verb + **Verb (base form)**\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- **Subject:** `Everyone`\r\n- **Modal Verb:** `should` (উচিত)\r\n- **Main Verb:** `___` (বলা)\r\n\r\nনিয়ম অনুযায়ী, modal verb `should`-এর পরে `speak` verb-টির base form বসবে, যা হলো `speak`।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** বাক্যে `can, could, may, might, shall, should, will,\r\n  would, must` দেখলেই, এর পরের verb-টি হবে একদম simple form (V1) — কোনো `s`,\r\n  `ing`, `ed`, বা `to` থাকবে না।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) spoken:** এটি `speak`-এর Past Participle (V3)। Modal-এর পরে V3 বসে না (ব্যতিক্রম: `should have spoken`)।\r\n- **(b) to speak:** এটি `to`-infinitive। Modal-এর পরে `to` বসে না।\r\n- **(d) speaking:** এটি Present Participle বা Gerund (`ing` form)। Modal-এর পরে `ing` form বসে না (ব্যতিক্রম: `should be speaking`)।\r\n\r\n### আরও উদাহরণ\r\n\r\n- You **must go** now. (Not `goes`, `to go`, `going`)\r\n- She **can swim** very well. (Not `swims`, `to swim`, `swimming`)\r\n- They **will come** tomorrow. (Not `comes`, `to come`, `coming`)\r\n\r\nএই নিয়মটি ইংরেজি ভাষার অন্যতম মৌলিক একটি নিয়ম এবং সকল প্রকার পরীক্ষার জন্য অপরিহার্য।";
