export const sourceCode = "---\r\ntitle: \"Distributive Pronoun কাকে বলে? Either, Each, Neither এর ব্যবহার\"\r\ndescription: \"Distributive Pronoun-এর উদাহরণ কোনটি? Either, Each, Neither, Everyone-এর সঠিক ব্যবহার শিখুন। BCS, NTRCA, Primary পরীক্ষার জন্য Distributive Pronoun-এর সহজ ব্যাখ্যা।\"\r\nkeywords:\r\n  [\r\n    \"distributive pronoun\",\r\n    \"either as a pronoun\",\r\n    \"each vs every\",\r\n    \"english grammar mcq\",\r\n    \"bcs english grammar\",\r\n    \"pronoun types\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: Which one of the following words is an example of a distributive pronoun?\r\n\r\n**Question:** Which one of the following words is an example of a distributive pronoun?\r\n(a) such\r\n(b) either\r\n(c) that\r\n(d) any\r\n\r\n**Correct Answer:** (b) either\r\n\r\n### কেন 'either' একটি Distributive Pronoun?\r\n\r\n**Key Concept: Distributive Pronoun**\r\n\r\nDistributive Pronoun এমন Pronoun যা একাধিক ব্যক্তি বা বস্তুর মধ্যে প্রত্যেককে **আলাদাভাবে** বা **পৃথকভাবে** বোঝায়। এরা group-এর সদস্যদের একজন একজন করে নির্দেশ করে।\r\n\r\nCommon Distributive Pronouns:\r\n\r\n- **Each:** দুই বা তার বেশি সংখ্যক ব্যক্তি/বস্তুর প্রত্যেককে আলাদাভাবে বোঝাতে ব্যবহৃত হয়। (e.g., Each of the students was present.)\r\n- **Either:** দুইটি ব্যক্তি/বস্তুর মধ্যে 'যেকোনো একটি' বোঝাতে ব্যবহৃত হয়। (e.g., You can take either of the two pens.)\r\n- **Neither:** দুইটি ব্যক্তি/বস্তুর মধ্যে 'একটিও না' বোঝাতে ব্যবহৃত হয়। (e.g., Neither of the answers is correct.)\r\n- **Everyone, Anyone, No one** ইত্যাদিও পৃথকভাবে বোঝানোর কারণে Distributive Pronoun হিসেবে গণ্য হয়।\r\n\r\n**প্রশ্ন বিশ্লেষণ:**\r\nপ্রশ্নে প্রদত্ত option-গুলোর মধ্যে `either` শব্দটি দুইয়ের মধ্যে একটিকে পৃথকভাবে নির্দেশ করে, যা Distributive Pronoun-এর সংজ্ঞা পূরণ করে।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** Distributive Pronoun sentence-কে 'distribute' বা ভাগ করে\r\n  দেয়। এটি group-কে একসাথে না বুঝিয়ে, এক এক করে বোঝায়। এই pronoun-গুলো সবসময়\r\n  **Singular** হয় এবং এদের পরে **Singular verb** বসে। (e.g., Each **is**...,\r\n  Either **has**...)\r\n</Callout>\r\n\r\n### অন্যান্য Option-গুলো কী?\r\n\r\n- **(a) such:** Such শব্দটি Demonstrative Pronoun (e.g., \"Such was his command\") অথবা Adjective (e.g., \"such a person\") হিসেবে ব্যবহৃত হতে পারে, কিন্তু এটি Distributive নয়।\r\n- **(c) that:** That শব্দটি একটি Demonstrative Pronoun (যখন একা বসে) অথবা Demonstrative Adjective (যখন Noun-এর আগে বসে)। এটি নির্দিষ্ট কিছু নির্দেশ করে, বিতরণ করে না।\r\n- **(d) any:** Any শব্দটি Indefinite Pronoun (অনির্দিষ্ট Pronoun) হিসেবে ব্যবহৃত হয়। এটি দুইয়ের বেশি ব্যক্তি/বস্তুর মধ্যে অনির্দিষ্টভাবে কোনো একটিকে বোঝায় (e.g., \"Any of these books will do\") অথবা Adjective হিসেবেও ব্যবহৃত হয় (e.g., \"any book\")।\r\n\r\n### Distributive Pronoun vs. Distributive Adjective\r\n\r\n`Each`, `either`, `neither` শব্দগুলো Adjective হিসেবেও ব্যবহৃত হতে পারে যদি এদের ঠিক পরেই একটি Noun বসে।\r\n\r\n- **Pronoun:** **Each** of the boys received a prize. ('of the' আছে)\r\n- **Adjective:** **Each boy** received a prize. (Noun 'boy' ঠিক পরেই আছে)\r\n\r\n- **Pronoun:** **Either** of the roads leads to the station.\r\n- **Adjective:** **Either road** leads to the station.\r\n\r\nএই পার্থক্যটি মনে রাখা প্রতিযোগিতামূলক পরীক্ষার জন্য খুবই গুরুত্বপূর্ণ।";
