export const sourceCode = "---\r\ntitle: \"Relative Pronoun 'that' এর ব্যবহার: I have read the book that you lent me\"\r\ndescription: \"I have read the book ____ you lent me - শূন্যস্থানে that, whom, whose, what কোনটি বসবে? Relative Pronoun 'that' এর সঠিক ব্যবহার শিখুন। BCS, Primary পরীক্ষার জন্য গুরুত্বপূর্ণ।\"\r\nkeywords:\r\n  [\r\n    \"relative pronoun that\",\r\n    \"that vs which\",\r\n    \"object relative pronoun\",\r\n    \"english grammar mcq\",\r\n    \"bcs english preparation\",\r\n    \"book that you lent me\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: I have read the book \\_\\_\\_\\_ you lent me.\r\n\r\n**Question:** I have read the book \\_\\_\\_\\_ you lent me.\r\n(a) that\r\n(b) whom\r\n(c) whose\r\n(d) what\r\n\r\n**Correct Answer:** (a) that\r\n\r\n### কেন 'that' সঠিক উত্তর? (Why is 'that' correct?)\r\n\r\nএই প্রশ্নের উত্তরটি **Relative Pronoun**-এর সঠিক ব্যবহারের উপর নির্ভরশীল।\r\n\r\n**Key Concept: Object Relative Pronoun**\r\n\r\n1.  **Relative Pronoun কী?** Relative Pronoun (`who`, `whom`, `whose`, `which`, `that`) দুটি বাক্যকে যুক্ত করে এবং তার পূর্ববর্তী Noun বা Pronoun (Antecedent)-কে নির্দেশ করে।\r\n2.  **Antecedent:** এই বাক্যে Antecedent হলো `the book`।\r\n3.  **Subject vs. Object of the Clause:** আমাদের দেখতে হবে Relative Pronoun-টি ভেতরের clause (`____ you lent me`)-এর subject হিসেবে কাজ করছে নাকি object হিসেবে।\r\n    - Clause: `you lent me ____`\r\n    - Subject: `you` (কে ধার দিয়েছিল? - তুমি)\r\n    - Verb: `lent` (ধার দিয়েছিল)\r\n    - Object: `____` (কী ধার দিয়েছিল? - বইটি)\r\n\r\nযেহেতু শূন্যস্থানটি `lent` verb-এর object-এর জায়গায় বসেছে, তাই আমাদের একটি **Object Relative Pronoun** প্রয়োজন।\r\n\r\n**Pronoun-গুলোর ব্যবহার:**\r\n\r\n- **`that`:** ব্যক্তি (person) এবং বস্তু (thing) উভয়ের ক্ষেত্রে subject বা object হিসেবে ব্যবহৃত হতে পারে।\r\n- **`which`:** শুধুমাত্র বস্তু (thing) বা ইতর প্রাণীর ক্ষেত্রে subject বা object হিসেবে ব্যবহৃত হতে পারে।\r\n- **`who`:** শুধুমাত্র ব্যক্তির ক্ষেত্রে subject হিসেবে ব্যবহৃত হয়।\r\n- **`whom`:** শুধুমাত্র ব্যক্তির ক্ষেত্রে object হিসেবে ব্যবহৃত হয়।\r\n\r\nযেহেতু Antecedent `the book` একটি বস্তু, আমরা `which` বা `that` ব্যবহার করতে পারি। Option-এ `which` নেই, কিন্তু `that` আছে। তাই `that` এখানে সঠিক উত্তর।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** Antecedent যদি বস্তু (`the book`) হয়, তাহলে Relative\r\n  Pronoun হিসেবে `which` বা `that` বসে। যদি ব্যক্তি (`the man`) হয়, তাহলে `who`,\r\n  `whom` বা `that` বসে। Option দেখে সঠিকটি বেছে নিন।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(b) whom:** এটি ব্যক্তির ক্ষেত্রে object হিসেবে ব্যবহৃত হয়। `the book` বস্তু হওয়ায় `whom` ভুল।\r\n- **(c) whose:** এটি অধিকার বা মালিকানা বোঝাতে ব্যবহৃত হয় (যার/যাদের)। যেমন: \"the book **whose** cover is red\"। এখানে মালিকানা বোঝাচ্ছে না।\r\n- **(d) what:** `what` Relative Pronoun হিসেবে ব্যবহৃত হয় যখন এর কোনো Antecedent থাকে না। (e.g., \"I know **what** you mean.\")। যেহেতু এখানে `the book` Antecedent হিসেবে আছে, `what` ব্যবহার করা যাবে না।\r\n\r\n**Note:** Object Relative Pronoun (`that`/`which`) অনেক সময় উহ্য রাখা যায়। অর্থাৎ, \"I have read the book you lent me\" - এই বাক্যটিও শুদ্ধ।";
