export const sourceCode = "---\r\ntitle: \"Modal Verb 'can' for Ability: 'someone who can play the piano'\"\r\ndescription: \"'I am looking for someone who ___ play the piano.' - কেন 'can' সঠিক উত্তর? Modal Verb 'can' এবং 'be able to' এর ব্যবহার ও পার্থক্য সহজ বাংলায় শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"modal verb can\",\r\n    \"can vs be able to\",\r\n    \"expressing ability\",\r\n    \"who can play\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: I am looking for someone who \\_\\_\\_ play the piano.\r\n\r\n**Question:** I am looking for someone who \\_\\_\\_ play the piano.\r\n(a) able to\r\n(b) is able\r\n(c) can be able to\r\n(d) can\r\n\r\n**Correct Answer:** (d) can\r\n\r\n### কেন 'can' সঠিক উত্তর? (Why is 'can' correct?)\r\n\r\nএই প্রশ্নের উত্তরটি **Modal Verb `can`** এবং phrase `be able to`-এর সঠিক ব্যবহারের উপর নির্ভরশীল। উভয়ই ক্ষমতা বা সামর্থ্য (ability) প্রকাশ করতে ব্যবহৃত হয়, কিন্তু তাদের ব্যাকরণগত গঠনে পার্থক্য রয়েছে।\r\n\r\n**Key Concepts:**\r\n\r\n1.  **Modal Verb `can`:**\r\n\r\n    - `can` একটি Modal Auxiliary Verb।\r\n    - এর পরে সবসময় verb-এর মূল রূপ (base form/V1) বসে।\r\n    - **Structure:** Subject + `can` + **Verb (base form)**\r\n    - _Example:_ He **can speak** English.\r\n\r\n2.  **Phrase `be able to`:**\r\n    - এটিও ক্ষমতা প্রকাশ করে এবং `can`-এর সমার্থক।\r\n    - `be` verb-টি subject এবং tense অনুযায়ী পরিবর্তিত হয় (am/is/are/was/were)।\r\n    - এর পরে `to`-infinitive (to + V1) বসে।\r\n    - **Structure:** Subject + `be` verb + `able` + **to-infinitive (to + V1)**\r\n    - _Example:_ He **is able to speak** English.\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- \"I am looking for someone who \\_\\_\\_ play the piano.\"\r\n- Relative Clause: \"who \\_\\_\\_ play the piano\"\r\n- Antecedent of `who`: `someone` (singular)\r\n- Verb after the blank: `play` (base form)\r\n\r\nযেহেতু শূন্যস্থানের পরে verb-এর base form `play` আছে, আমাদের এমন একটি শব্দ দরকার যা base form গ্রহণ করে।\r\n\r\n- **Option (d) `can`:** `who can play the piano`। `can`-এর পরে base form `play` বসেছে। এটি ব্যাকরণগতভাবে সম্পূর্ণ সঠিক।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** শূন্যস্থানের পরে যদি verb-এর base form (V1) থাকে, তাহলে\r\n  `can` বসবে। আর যদি `to` + V1 থাকে, তাহলে `is/am/are able` বসতে পারে।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) able to:** এর আগে একটি `be` verb (is, am, are) প্রয়োজন। `who able to play` অসম্পূর্ণ।\r\n- **(b) is able:** `is able`-এর পরে একটি `to` প্রয়োজন। `who is able play` ভুল। সঠিক হতো: `who is able **to** play`।\r\n- **(c) can be able to:** `can` এবং `be able to` উভয়ই ক্ষমতা বোঝায়। এদের একসাথে ব্যবহার করা বাহুল্য (redundant) এবং ব্যাকরণগতভাবে ভুল। এটি \"I can can do it\" বলার মতো।\r\n\r\nঅতএব, `can` একমাত্র সঠিক এবং উপযুক্ত বিকল্প।";
