export const sourceCode = "---\r\ntitle: What kind of noun is 'Girl'? | Common Noun Identification for BCS & Primary School Teacher\r\ndescription: Learn why the word 'Girl' is a Common Noun with easy Bangla explanation, concept, examples, and memory hacks for BCS and Primary School Teacher exam preparation.\r\ntags: Girl noun type, Common noun Bangla, BCS English Grammar, Primary Teacher Exam English, Noun classification, English Grammar Tricks, Common noun examples Bangla\r\n---\r\n\r\n# ❓ What kind of noun is 'Girl'?\r\n\r\nক) Collective Noun  \r\nখ) **Common Noun** ✅  \r\nগ) Proper Noun  \r\nঘ) Material Noun\r\n\r\n---\r\n\r\n## ✅ সঠিক উত্তর: Common Noun (খ)\r\n\r\n---\r\n\r\n## 💡 কেন 'Girl' হলো Common Noun?\r\n\r\n**Girl** মানে “একজন মেয়ে”—  \r\nএটি **কোনো নির্দিষ্ট মেয়ের নাম নয়**, বরং _সাধারণভাবে যেকোনো মেয়েকে_ বোঝায়।  \r\nযে নাম **সাধারণভাবে কোনো ব্যক্তি/বস্তু/প্রাণীকে** বোঝায় → তাকে **Common Noun** বলা হয়।\r\n\r\n👉 Girl = সাধারণভাবে যেকোনো মেয়ে = **Common Noun**  \r\n👉 কিন্তু **Asha, Rina, Maria** এগুলো নির্দিষ্ট মেয়ের নাম = Proper Noun\r\n\r\n---\r\n\r\n## 🧠 Trick (Memory Hack)\r\n\r\n### 🔹 Trick 1: “General Name = Common Noun”\r\n\r\nযদি শব্দটি\r\n\r\n- কোনো সাধারণ মানুষকে বোঝায় (girl, boy, doctor)\r\n- কোনো সাধারণ প্রাণীকে বোঝায় (cat, cow, bird)\r\n- কোনো সাধারণ জিনিস বোঝায় (pen, book, phone)\r\n\r\n→ তাহলে সেটি **Common Noun**।\r\n\r\n### 🔹 Trick 2: “Name নাহলে Proper Noun নয়”\r\n\r\n➡️ “Girl” কোনো **নাম** নয়, তাই Proper Noun হতে পারে না।\r\n\r\n### 🔹 Trick 3: Collective Noun = দল বোঝায়\r\n\r\n- Library (books)\r\n- Crowd (people)\r\n- Team (players)  \r\n  👉 Girl কোনো দল নয় → Collective নয়।\r\n\r\n### 🔹 Trick 4: Material Noun = পদার্থ বোঝায়\r\n\r\n- Gold\r\n- Water\r\n- Iron  \r\n  👉 Girl কোনো পদার্থ নয় → Material Noun নয়।\r\n\r\n---\r\n\r\n## 🌿 Concept: Noun Types (Easy Bangla)\r\n\r\n| Noun Type       | Definition (Bangla) | Example         | ‘Girl’ কি? |\r\n| --------------- | ------------------- | --------------- | ---------- |\r\n| Common Noun     | সাধারণ নাম          | girl, boy, city | ✔️ হ্যাঁ   |\r\n| Proper Noun     | নির্দিষ্ট নাম       | Asha, Dhaka     | ❌ না      |\r\n| Collective Noun | দল বোঝায়            | team, family    | ❌ না      |\r\n| Material Noun   | পদার্থ              | gold, water     | ❌ না      |\r\n\r\n---\r\n\r\n## 📝 Example Sentences (Understanding)\r\n\r\n- **The girl is dancing.** → সাধারণভাবে বলা → Common Noun\r\n- **A girl came to the class.** → যেকোনো মেয়ে → Common Noun\r\n\r\nকিন্তু:\r\n\r\n- **Rima is dancing.** → নির্দিষ্ট মেয়ে → Proper Noun\r\n\r\n---\r\n\r\n## 🎯 Final Takeaway\r\n\r\n- **Girl = Common Noun**\r\n- কারণ এটি কোনো নির্দিষ্ট ব্যক্তির নাম নয়, বরং যেকোনো মেয়েকে বোঝায়।\r\n- Proper নয় কারণ নাম নয়।\r\n- Collective নয় কারণ দল বোঝায় না।\r\n- Material নয় কারণ পদার্থ নয়।\r\n\r\n---\r\n\r\nএই Grammar concept প্রতিটি সরকারি চাকরির পরীক্ষায় আসে—BCS, Primary School Teacher, NTRCA, Bank Job—সব জায়গায় গুরুত্বপূর্ণ।";
