export const sourceCode = "---\r\ntitle: There is __ milk in the bottle\r\ndescription: Learn the correct answer with explanation to the English grammar question \"There is __ milk in the bottle\". Perfect for Primary School Teacher and BCS English preparation.\r\nkeywords: English grammar, fill in the blanks, English quiz, English practice, there is milk in the bottle, a little vs very little, English MCQ, English grammar question Bangladesh\r\n---\r\n\r\n# Fill in the blanks: There is \\_\\_ milk in the bottle\r\n\r\nক. very little  \r\nখ. small  \r\nগ. very few  \r\nঘ. a little\r\n\r\n### ✅ সঠিক উত্তর: ঘ. a little\r\n\r\n---\r\n\r\n### 🧠 ব্যাখ্যা:\r\n\r\n👉 “Milk” একটি **uncountable noun** (যা গোনা যায় না)। গোনা যায় few,\r\nগোনা যায় না little।\r\n\r\nUncountable noun এর আগে সাধারণত **a little / little** ব্যবহার করা হয়।\r\n\r\n- **a little** মানে — কিছুটা আছে (ইতিবাচক অর্থে)।\r\n- **very little** মানে — খুব সামান্য বা প্রায় কিছুই নেই (নেতিবাচক অর্থে)।\r\n\r\n🔹 উদাহরণ:\r\n\r\n- There is **a little milk** in the bottle. ➤ বোতলে কিছুটা দুধ আছে।\r\n- There is **very little milk** in the bottle. ➤ বোতলে প্রায় কোনো দুধই নেই।\r\n\r\n---\r\n\r\n### ❌ ভুল বিকল্পগুলোর ব্যাখ্যা:\r\n\r\n- **small:** এটি আকার বোঝাতে ব্যবহৃত হয়, পরিমাণ নয়।\r\n- **very few:** এটি countable noun (যেমন books, apples) এর সাথে ব্যবহার হয়।  \r\n  ➤ যেমন: There are very few books on the table.\r\n\r\n---\r\n\r\n### 💡 সহজ টিপস:\r\n\r\n| বিষয়                  | Countable Noun | Uncountable Noun  |\r\n| --------------------- | -------------- | ----------------- |\r\n| পরিমাণ বোঝাতে ব্যবহার | a few / few    | a little / little |\r\n\r\n---\r\n\r\n### 📘 অতিরিক্ত তথ্য:\r\n\r\n- **a little** = কিছুটা আছে (positive meaning)\r\n- **little** = প্রায় কিছুই নেই (negative meaning)\r\n- **very little** = খুব সামান্য, প্রায় নেই\r\n\r\n---\r\n\r\n### 📖 উদাহরণ আরও দেখো:\r\n\r\n1. There is **a little** water in the glass.\r\n2. I have **a few** friends in Dhaka.\r\n3. There is **very little** hope left.\r\n\r\n---\r\n\r\n✅ **মনে রাখবে:**  \r\nযেসব জিনিস গোনা যায় না (যেমন milk, water, sugar, rice), সেগুলোর আগে **a little** বা **little** ব্যবহার করতে হয়।";
