export const sourceCode = "---\r\ntitle: \"Auxiliary vs Principal Verb: Why 'am' Can Be Both\"\r\ndescription: \"Which verb is used as both auxiliary and principal? 'am' (to be verb) কখন Auxiliary (সাহায্যকারী) এবং কখন Principal (মূল) Verb হিসেবে ব্যবহৃত হয়? Modal verb-এর সাথে পার্থক্য শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"auxiliary verb\",\r\n    \"principal verb\",\r\n    \"main verb\",\r\n    \"to be verb\",\r\n    \"am is are\",\r\n    \"modal auxiliary verbs\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: Which one is used both as auxiliary and principal verb?\r\n\r\n**Question:** Which one is used both as auxiliary and principal verb?\r\n(a) can\r\n(b) would\r\n(c) might\r\n(d) am\r\n\r\n**Correct Answer:** (d) am\r\n\r\n### কেন 'am' সঠিক উত্তর? (Why is 'am' correct?)\r\n\r\nএই প্রশ্নের উত্তরটি **Primary Auxiliary Verbs** এবং **Modal Auxiliary Verbs**-এর মধ্যে পার্থক্য বোঝার উপর নির্ভরশীল।\r\n\r\n**Key Concepts:**\r\n\r\n1.  **Principal Verb (Main Verb):** যে verb একটি বাক্যে বা clause-এ একা বসে পূর্ণাঙ্গ অর্থ প্রকাশ করতে পারে, তাকে Principal বা Main Verb বলে।\r\n\r\n    - _Example:_ He **plays** football. I **am** a student.\r\n\r\n2.  **Auxiliary Verb (Helping Verb):** যে verb মূল verb-কে Tense, Mood, বা Voice গঠনে সাহায্য করে, তাকে Auxiliary Verb বলে।\r\n    - _Example:_ He **is playing** football. The work **was done**.\r\n\r\n**'To Be' Verbs (am, is, are, was, were):**\r\n`am` হলো 'to be' verb-এর একটি রূপ। 'To be' verb-গুলো ইংরেজি ভাষায় বিশেষ, কারণ এরা Principal এবং Auxiliary উভয়ভাবেই ব্যবহৃত হতে পারে।\r\n\r\n- **As a Principal Verb:** যখন 'to be' verb বাক্যের একমাত্র ক্রিয়া হিসেবে ব্যবহৃত হয় এবং কোনো অবস্থা, পরিচয় বা অস্তিত্ব বোঝায়।\r\n\r\n  - _Example:_ I **am** a teacher. (এখানে `am` মূল verb, অর্থ 'হই')।\r\n  - _Example:_ They **are** happy. (এখানে `are` মূল verb, অর্থ 'হয়')।\r\n\r\n- **As an Auxiliary Verb:** যখন 'to be' verb অন্য কোনো main verb-এর সাথে বসে Continuous Tense বা Passive Voice গঠন করে।\r\n  - _Example:_ I **am reading** a book. (Present Continuous Tense গঠনে `reading`-কে সাহায্য করছে)।\r\n  - _Example:_ The letter **is written** by him. (Passive Voice গঠনে `written`-কে সাহায্য করছে)।\r\n\r\nযেহেতু `am` উভয় ভূমিকা পালন করতে পারে, তাই এটি সঠিক উত্তর।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** যদি বাক্যে `am/is/are/was/were`-এর পরে আর কোনো action\r\n  verb (যেমন: going, eating, done) না থাকে, তবে `am/is/are` নিজেই Principal\r\n  Verb। যদি পরে কোনো action verb থাকে, তবে এটি Auxiliary।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) can, (b) would, (c) might:** এই তিনটিই হলো **Modal Auxiliary Verbs**। Modal verb-গুলো **শুধুমাত্র Auxiliary** হিসেবে ব্যবহৃত হতে পারে। এরা কখনোই একা বসে বাক্যের main verb হতে পারে না। এদের পরে সবসময় একটি main verb-এর base form (V1) বসে।\r\n  - _Example:_ I **can swim**. (You cannot say \"I can.\")\r\n  - _Example:_ He **would go**. (You cannot say \"He would.\")\r\n  - _Example:_ It **might rain**. (You cannot say \"It might.\")\r\n\r\nএই Modal verb-গুলো main verb-কে সাহায্য করে সম্ভাবনা, অনুমতি, ক্ষমতা ইত্যাদি বোঝাতে, কিন্তু নিজেরা কখনো মূল ক্রিয়া হতে পারে না।";
