export const sourceCode = "---\r\ntitle: \"Lest... should Rule: 'Try hard lest you should fall'\"\r\ndescription: \"'Try hard lest you ___ fall.' - কেন 'should' সঠিক উত্তর? Conjunction 'lest' এর পর Modal Verb 'should' ব্যবহারের নিয়মটি সহজ বাংলায় শিখুন।\"\r\nkeywords:\r\n  [\r\n    \"lest should rule\",\r\n    \"try hard lest you should fall\",\r\n    \"conjunction lest\",\r\n    \"modal verb should\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: Try hard lest you \\_\\_\\_ fall.\r\n\r\n**Question:** Try hard lest you \\_\\_\\_ fall.\r\n(a) may\r\n(b) might\r\n(c) should\r\n(d) would\r\n\r\n**Correct Answer:** (c) should\r\n\r\n### কেন 'should' সঠিক উত্তর? (Why is 'should' correct?)\r\n\r\nএই প্রশ্নের উত্তরটি **Conjunction `lest`**-এর একটি সুনির্দিষ্ট এবং অপরিবর্তনীয় নিয়মের উপর ভিত্তি করে।\r\n\r\n**Key Concept: The `lest ... should` Structure**\r\n\r\n1.  **`Lest` কী?** `Lest` একটি conjunction যার অর্থ 'পাছে ভয় হয় যে' বা 'যাতে না' (for fear that / so that... not)। এটি একটি নেতিবাচক ফলাফল এড়ানোর জন্য সতর্কতা প্রকাশ করে।\r\n\r\n2.  **The Rule:** `Lest`-এর পরে যে clause-টি আসে, সেই clause-এর verb-এর আগে **`should`** বসে। আধুনিক ইংরেজিতে অনেক সময় `should` উহ্য রেখে verb-এর base form-ও ব্যবহার করা হয়, তবে Formal English এবং পরীক্ষার জন্য `should` ব্যবহার করাই নিয়ম।\r\n\r\n**Structure:** ... `lest` + Subject + **`should`** + Verb (base form) ...\r\n\r\n**বাক্য বিশ্লেষণ:**\r\n\r\n- \"Try hard\" (কঠোর চেষ্টা কর)\r\n- \"lest\" (পাছে বা যাতে না)\r\n- \"you \\_\\_\\_ fall.\" (তুমি ব্যর্থ হও।)\r\n\r\nবাক্যটির অর্থ: কঠোর চেষ্টা কর, পাছে তুমি ব্যর্থ হও (বা যাতে তুমি ব্যর্থ না হও)।\r\nনিয়ম অনুযায়ী, `lest`-এর পরে subject `you`-এর পর `should` বসবে।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** বাক্যে `lest` দেখলেই পরের অংশে `should` খুঁজবেন। `Lest`\r\n  এবং `should` একটি অবিচ্ছেদ্য জুটি।\r\n</Callout>\r\n\r\n### ভুল উত্তরগুলো কেন ভুল?\r\n\r\n- **(a) may / (b) might / (d) would:** `lest`-এর সাথে এই modal verb-গুলো বসে না। `lest`-এর নিয়মটি নির্দিষ্ট এবং শুধুমাত্র `should` (অথবা verb-এর base form) এর সাথেই এটি ব্যবহৃত হয়।\r\n\r\n### আরও উদাহরণ\r\n\r\n- Walk carefully **lest** you **should** stumble. (সাবধানে হাঁটো, পাছে তুমি হোঁচট খাও।)\r\n- He ran away **lest** he **should** be seen. (সে পালিয়ে গেল, পাছে তাকে দেখে ফেলে।)\r\n- Study regularly **lest** you **should** fail the exam. (নিয়মিত পড়াশোনা কর, যাতে তুমি পরীক্ষায় ফেল না কর।)\r\n\r\nএই নিয়মটি ইংরেজি ব্যাকরণের একটি ক্লাসিক এবং গুরুত্বপূর্ণ নিয়ম যা প্রায়ই প্রতিযোগিতামূলক পরীক্ষায় আসে।";
