export const sourceCode = "import { Callout } from \"nextra/components\";\r\n\r\n# Modal for Certainty: \"must be\"\r\n\r\n**প্রশ্ন:** ‘He is not present in the class. He must be sick.’ Here ‘must be’ expresses—\r\n\r\n(a) certainty\r\n(b) possibility\r\n(c) obligation\r\n(d) intention\r\n\r\n**সঠিক উত্তর:** (a) certainty\r\n\r\n---\r\n\r\n### **ব্যাখ্যা**\r\n\r\nএই বাক্যে `must be` শব্দটি বক্তার দৃঢ় অনুমান বা যৌক্তিক উপসংহার (logical conclusion) প্রকাশ করছে। যখন কোনো পরিস্থিতি বা প্রমাণের উপর ভিত্তি করে বক্তা প্রায় নিশ্চিত হন, তখন `must` ব্যবহার করা হয়।\r\n\r\n#### **Key Concepts:**\r\n\r\nModal verb `must`-এর প্রধান দুটি ব্যবহার রয়েছে:\r\n\r\n1.  **Obligation (বাধ্যবাধকতা):** যখন কোনো কাজ করা বাধ্যতামূলক বোঝায়।\r\n    - **উদাহরণ:** You **must** follow the rules. (তোমাকে অবশ্যই নিয়ম মানতে হবে।)\r\n2.  **Certainty (নিশ্চয়তা) / Logical Deduction (যৌক্তিক অনুমান):** যখন কোনো প্রমাণের ভিত্তিতে কোনো সিদ্ধান্তে পৌঁছানো হয়।\r\n    - **উদাহরণ:** The ground is wet. It **must** have rained. (মাটি ভেজা। নিশ্চয়ই বৃষ্টি হয়েছে।)\r\n\r\n#### **বিশ্লেষণ:**\r\n\r\n- **Evidence (প্রমাণ):** \"He is not present in the class.\" (সে ক্লাসে উপস্থিত নেই।)\r\n- **Logical Conclusion (যৌক্তিক উপসংহার):** \"He must be sick.\" (সে নিশ্চয়ই অসুস্থ।)\r\n\r\nএখানে, তার ক্লাসে অনুপস্থিতি থেকে বক্তা এই সিদ্ধান্তে পৌঁছেছেন যে সে অসুস্থ। এটি কোনো বাধ্যবাধকতা নয়, বরং একটি দৃঢ় বিশ্বাস বা নিশ্চয়তা।\r\n\r\n#### **Certainty vs. Possibility:**\r\n\r\n- **Certainty (নিশ্চয়তা):** `must`\r\n  - He **must be** tired. (He worked all day.) - বক্তা প্রায় ৯০-৯৫% নিশ্চিত।\r\n- **Possibility (সম্ভাবনা):** `may`, `might`, `could`\r\n  - He **may be** tired. - বক্তা ৫০% বা তার কম নিশ্চিত।\r\n- \\*\\*Impossibility (অসম্ভাবনা):- `can't`\r\n  - He **can't be** tired. (He just woke up.) - বক্তা নিশ্চিত যে এটা সম্ভব নয়।\r\n\r\n<Callout type=\"info\">\r\n  ** মনে রাখবেন:** `must` যখন বাধ্যবাধকতা বোঝায়, তখন এটি একটি কাজ করার নির্দেশ\r\n  দেয়। আর যখন নিশ্চয়তা বোঝায়, তখন এটি একটি পরিস্থিতি সম্পর্কে ধারণা বা অনুমান\r\n  প্রকাশ করে।\r\n</Callout>\r\n\r\nএই প্রশ্নটি `must`-এর দুটি ভিন্ন অর্থের মধ্যে পার্থক্য করার ক্ষমতা যাচাই করে, যা modal verb-এর একটি গুরুত্বপূর্ণ দিক।";
