export const sourceCode = "---\r\ntitle: I water the plants. The word 'water' is used as — verb, adverb, noun, pronoun\r\ndescription: Learn how to identify the correct part of speech of the word 'water' in the sentence 'I water the plants' with easy Bangla explanation, tricks, and concept for BCS and Primary School Teacher exam.\r\nkeywords: water verb or noun, BCS English Grammar, Parts of Speech tricks, Primary School Teacher English, English Grammar in Bangla, I water the plants analysis\r\n---\r\n\r\n# 🪴 I water the plants. The word 'water' is used as—\r\n\r\nA. Verb\r\nB. Adverb  \r\nC. Noun  \r\nD. Pronoun\r\n\r\n✅ **Correct Answer: A. Verb**\r\n\r\n---\r\n\r\n## 💡 Concept: Parts of Speech অনুযায়ী “Water” কখন Noun, কখন Verb?\r\n\r\nইংরেজিতে **\"water\"** শব্দটি **noun** এবং **verb** — দুইভাবেই ব্যবহার হয়।  \r\n👉 কখন noun হবে, আর কখন verb হবে — সেটাই বুঝে নিতে হবে বাক্য থেকে।\r\n\r\n| **Use**  | **Example**         | **বাংলা অর্থ**     | **Parts of Speech**         |\r\n| -------- | ------------------- | ------------------ | --------------------------- |\r\n| **Noun** | I drink water.      | আমি পানি খাই।      | Water = পদার্থ (noun)       |\r\n| **Verb** | I water the plants. | আমি গাছে পানি দিই। | Water = কাজ বোঝাচ্ছে (verb) |\r\n\r\n---\r\n\r\n## 🧠 Trick (Hack) to Memorize — “Action মানেই Verb”\r\n\r\n🔹 যদি **water** শব্দটি **কোন কাজ বোঝায়** (যেমন: গাছে পানি দেওয়া, প্রাণীকে পানি দেওয়া ইত্যাদি) — তাহলে এটা **verb**।  \r\n🔹 আর যদি **water** মানে শুধু **পানি পদার্থ** বোঝায়, তাহলে সেটা **noun**।\r\n\r\n👉 তাই “I water the plants” মানে “আমি গাছে পানি দিই” → এখানে কাজ হচ্ছে → তাই **Verb**।\r\n\r\n---\r\n\r\n## 🌿 Grammar Explanation (BCS/Primary English Preparation)\r\n\r\n**Subject + Verb + Object** এই গঠনটি একটি সাধারণ বাক্যের মূল কাঠামো।  \r\n➡️ এখানে:\r\n\r\n- **Subject:** I\r\n- **Verb:** water\r\n- **Object:** the plants\r\n\r\nঅতএব, **‘water’** এখানে **verb** হিসেবে কাজ করছে।\r\n\r\n---\r\n\r\n## ✍️ Shortcut Technique (Exam Hack)\r\n\r\n🔹 যদি বাক্যে **“do, does, did”** বসানো যায়, তাহলে শব্দটি Verb হতে পারে।  \r\n👉 “I **do** water the plants.” ✔️ — অর্থ ঠিক আছে → Verb  \r\n👉 “I **do** water” (যদি noun হয়) — ❌ অর্থ মিলছে না → noun নয়\r\n\r\n➡️ তাই এই quick test দিয়েই exam এ parts of speech ধরা যায়।\r\n\r\n---\r\n\r\n## 📚 Common Confusing Words (for BCS & Primary Teacher Exam)\r\n\r\n| Word      | Noun Example          | Verb Example           |\r\n| --------- | --------------------- | ---------------------- |\r\n| **Water** | Drink water.          | Water the plants.      |\r\n| **Light** | The light is bright.  | Light the lamp.        |\r\n| **Smoke** | The smoke is harmful. | He smokes a cigarette. |\r\n| **Play**  | It’s a play.          | They play football.    |\r\n\r\n👉 একই শব্দ noun ও verb দুটোই হতে পারে — একে বলে **Conversion or Functional Shift**।\r\n\r\n---\r\n\r\n## 🎯 Final Takeaway\r\n\r\n- **Water** = Noun → যখন পদার্থ বোঝায়।\r\n- **Water** = Verb → যখন কাজ বোঝায়।\r\n- মনে রাখো: **“Action = Verb”**\r\n- BCS ও Primary exam এ এই ধরনের প্রশ্ন বারবার আসে।\r\n\r\n---\r\n\r\n## 🧩 Practice Question\r\n\r\n1. They light the candles. → “Light” কোন parts of speech?\r\n2. I smoke a cigarette. → “Smoke” কোন parts of speech?\r\n3. We play football. → “Play” কোন parts of speech?\r\n\r\n(উত্তর: সবগুলোই Verb, কারণ কাজ বোঝাচ্ছে।)\r\n\r\n---\r\n\r\n✅ **Summary:**\r\n\r\n> “I water the plants.” বাক্যে **‘water’** একটি **Verb**, কারণ এটি কাজ (পানি দেওয়া) বোঝাচ্ছে।\r\n\r\n---\r\n\r\n**Tags:**  \r\nBCS English Grammar, Primary School Teacher English, Parts of Speech in Bangla, Confusing Words English Grammar, Verb Noun Difference, Easy English Tricks Bangla";
