export const sourceCode = "---\r\ntitle: \"They fought with one another – এখানে 'one another' কোন Pronoun?\"\r\ndescription: \"'They fought with one another' বাক্যে 'one another' কোন ধরনের pronoun তা সহজ বাংলায় ব্যাখ্যা, উদাহরণ, স্মরণযোগ্য কৌশল ও BCS/প্রাথমিক শিক্ষক নিয়োগ পরীক্ষার প্রস্তুতির জন্য সম্পূর্ণ গাইড।\"\r\ntags:\r\n  [\r\n    \"Reciprocal Pronoun\",\r\n    \"English Grammar\",\r\n    \"BCS\",\r\n    \"NTRCA\",\r\n    \"Primary Teacher Exam\",\r\n    \"Parts of Speech\",\r\n    \"Pronoun Classification\",\r\n    \"ইংরেজি ব্যাকরণ\",\r\n  ]\r\n---\r\n\r\n# They fought with one another. Here, \"one another\" is a **\\_\\_\\_**.\r\n\r\nপ্রশ্ন: They fought with one another. এখানে “one another” কোন ধরনের pronoun?\r\n\r\n(ক) Personal Pronoun  \r\n(খ) Reflexive Pronoun  \r\n(গ) Reciprocal Pronoun  \r\n(ঘ) Demonstrative Pronoun\r\n\r\n**সঠিক উত্তর: (গ) Reciprocal Pronoun** ✔\r\n\r\n---\r\n\r\n<AudioPlayer\r\n  src=\"/audios/reciprocal-pronoun.wav\"\r\n  title=\"Reciprocal Pronoun Explanation\"\r\n/>\r\n\r\n## Reciprocal Pronoun কী? (সহজ, পরিষ্কার ব্যাখ্যা)\r\n\r\nReciprocal Pronoun এমন pronoun, যা বোঝায় **দুই বা ততোধিক ব্যক্তি পরস্পরের প্রতি একই কাজ করছে**।  \r\nবাংলায় সাধারণত এদের অর্থ হয়—**\"পরস্পর\", \"একজন আরেকজনকে\", \"এক অপরকে\"**।\r\n\r\nReciprocal Pronoun মাত্র দুইটি:\r\n\r\n1. **each other**\r\n2. **one another**\r\n\r\nReciprocal মানে **একজন আরেকজনের প্রতি সমানভাবে কাজ করা**।\r\n\r\nউদাহরণ:\r\n\r\n- They love each other.\r\n- We help one another.\r\n\r\n---\r\n\r\n## “They fought with one another” কেন Reciprocal Pronoun?\r\n\r\nবাক্যটি বিশ্লেষণ করি:\r\n\r\n**Subject:** They (তারা)  \r\n**Phrase:** with one another = পরস্পরের সাথে  \r\n**Verb:** fought (লড়াই করল)\r\n\r\nঅর্থ:  \r\n“তারা পরস্পর/একজন আরেকজনের সাথে লড়াই করল।”\r\n\r\nএটি স্পষ্টভাবে দেখাচ্ছে—  \r\n**কাজটি একদলের মানুষ পরস্পরের প্রতি করছে**।  \r\nএ কারণেই “one another” হলো **Reciprocal Pronoun**।\r\n\r\n---\r\n\r\n# Reciprocal Pronoun-এর মূল ধারণা (যা বুঝলে ভুল হবার সুযোগ নেই)\r\n\r\nReciprocal Pronoun তখনই ব্যবহৃত হয় যখন—\r\n\r\n1. **কমপক্ষে দুইজন বা একাধিক ব্যক্তি থাকবে**\r\n2. তারা পরস্পরের প্রতি সমানভাবে কাজ করবে\r\n3. অর্থে “পরস্পর”, “একজন আরেকজনকে”, “এক অপরকে” বোঝাবে\r\n\r\nএগুলো Reflexive এর মতো নিজেকে বোঝায় না। বরং **দুই বা ততোধিক ব্যক্তির পারস্পরিক সম্পর্ককেই বোঝায়**।\r\n\r\n---\r\n\r\n### কৌশল ১: অর্থে দেখো — যদি “পরস্পর” বসে, তবে Reciprocal Pronoun\r\n\r\n- They respect one another → তারা পরস্পরকে সম্মান করে\r\n- They helped each other → তারা একে অপরকে সাহায্য করেছে\r\n\r\n### কৌশল ৪: One another সাধারণত \"বেশি জন\" বোঝায়\r\n\r\nপ্রথাগত ব্যাকরণ অনুসারে—\r\n\r\n- **Each other → দুইজন**\r\n- **One another → দুইয়ের বেশি**\r\n\r\n# Reciprocal Pronoun বনাম অন্য Pronoun — পরিষ্কার তুলনা\r\n\r\n| Pronoun Type           | Example                     | কেন Reciprocal নয়        |\r\n| ---------------------- | --------------------------- | ------------------------ |\r\n| Personal Pronoun       | him, her, they              | পারস্পরিক নয়             |\r\n| Reflexive Pronoun      | himself, themselves         | self/selves থাকে         |\r\n| Demonstrative Pronoun  | this, that                  | নির্দেশ করে              |\r\n| **Reciprocal Pronoun** | **each other, one another** | পরস্পরের প্রতি কাজ বোঝায় |\r\n\r\nReciprocal Pronoun এর বিশেষত্ব হলো—  \r\n**দুই পক্ষের মধ্যে সমান সম্পর্ক বা কাজ**।\r\n\r\n---\r\n\r\n# আরো উদাহরণ (যাতে ১০০% পরিষ্কার হয়)\r\n\r\n- The students helped one another.\r\n- The players congratulated each other.\r\n- We should support one another.\r\n- They blamed each other for the mistake.\r\n\r\nসবগুলোর মানে: **পরস্পর/এক অপরের প্রতি কাজ**।\r\n\r\n---\r\n\r\n## Practice Questions (নিজে পরীক্ষা করে দেখো)\r\n\r\n১. The boys blamed each other for the loss.  \r\n২. We should help one another.  \r\n৩. The two sisters support each other.  \r\n৪. The neighbors greeted one another happily.\r\n\r\nসবগুলোতেই Reciprocal Pronoun আছে।\r\n\r\n---\r\n\r\n## Final Summary\r\n\r\n**Reciprocal Pronoun = পরস্পর সম্পর্ক বোঝায়**  \r\n**Each other / One another → পারস্পরিক কাজ**  \r\n“They fought with one another” → **one another = Reciprocal Pronoun**";
