export const sourceCode = "import { Callout } from \"nextra/components\";\r\n\r\n# Modal for Preference: \"would rather\"\r\n\r\n**প্রশ্ন:** Mr. Atiq \\_\\_\\_\\_ rather not invest that money in the stock market.\r\n\r\n(a) must\r\n(b) has to\r\n(c) would\r\n(d) could\r\n\r\n**সঠিক উত্তর:** (c) would\r\n\r\n---\r\n\r\n### **ব্যাখ্যা**\r\n\r\nএই বাক্যটিতে `would rather` expression-টি ব্যবহার করে বক্তার পছন্দ বা অগ্রাধিকার (preference) প্রকাশ করা হয়েছে। `would rather` একটি idiomatic phrase যা কোনো একটি কাজের চেয়ে অন্য কাজকে বেশি পছন্দ করা বোঝাতে ব্যবহৃত হয়।\r\n\r\n#### **Key Concepts:**\r\n\r\n1.  **`would rather`**: এর অর্থ হলো 'prefer' বা 'বেশি পছন্দ করা'। এটি একটি modal expression।\r\n2.  **Structure:**\r\n    - **Affirmative:** `Subject + would rather + Verb (base form)`\r\n      - **উদাহরণ:** I **would rather stay** home. (আমি বরং বাড়িতেই থাকব।)\r\n    - **Negative:** `Subject + would rather + not + Verb (base form)`\r\n      - **উদাহরণ:** I **would rather not go** out tonight. (আমি বরং আজ রাতে বাইরে যাব না।)\r\n\r\n#### **বিশ্লেষণ:**\r\n\r\n- বাক্যটিতে `rather not invest` অংশটি রয়েছে। `rather`-এর সাথে `would` যুক্ত হয়ে `would rather` phrase-টি গঠিত হয়, যা পছন্দ প্রকাশ করে।\r\n- \"Mr. Atiq **would rather not invest** that money...\" বাক্যটির অর্থ হলো, \"মি. আতিক বরং ওই টাকা স্টক মার্কেটে বিনিয়োগ না করাকেই বেশি পছন্দ করবেন।\"\r\n- অন্যান্য অপশনগুলো (`must`, `has to`, `could`) `rather`-এর সাথে এই গঠনে ব্যবহৃত হয় না।\r\n\r\n#### **`would rather` vs. `had better`:**\r\n\r\n- **`would rather` (Preference):** দুটি বিকল্পের মধ্যে একটিকে বেছে নেওয়া।\r\n  - I **would rather** drink tea than coffee.\r\n- **`had better` (Advice/Warning):** কোনো কাজ করা ভালো বা উচিত, না করলে খারাপ কিছু হতে পারে এমন ইঙ্গিত দেয়।\r\n  - You **had better** study hard for the exam. (না পড়লে পরীক্ষায় ফেল করতে পারো।)\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Memorize:** `rather` শব্দটি দেখলেই `would`-এর কথা মাথায় আনবেন।\r\n  `would rather` একটি জোড়া শব্দ (pair) হিসেবে ব্যবহৃত হয় পছন্দ বোঝাতে।\r\n</Callout>\r\n\r\nএই প্রশ্নটি `would rather`-এর সঠিক ব্যবহার যাচাই করে, যা ইংরেজিতে পছন্দ বা preference প্রকাশের একটি সাধারণ এবং গুরুত্বপূর্ণ উপায়।";
