export const sourceCode = "import { Callout } from \"nextra/components\";\r\n\r\n# Semi-Modal Verb: \"ought to\"\r\n\r\n**প্রশ্ন:** Children \\_\\_\\_\\_ to respect their parents.\r\n\r\n(a) ought\r\n(b) need\r\n(c) must\r\n(d) present participle\r\n\r\n**সঠিক উত্তর:** (a) ought\r\n\r\n---\r\n\r\n### **ব্যাখ্যা**\r\n\r\nএই বাক্যটিতে শূন্যস্থানের পরে `to` শব্দটি রয়েছে। প্রদত্ত বিকল্পগুলির মধ্যে শুধুমাত্র `ought` হলো এমন একটি Modal Verb যা তার পরে `to` infinitive গ্রহণ করে।\r\n\r\n#### **Key Concepts:**\r\n\r\n1.  **Modal Verbs:** Modal Verbs (যেমন `can`, `must`, `should`, `will`) সাধারণত verb-এর base form (V1) গ্রহণ করে এবং তাদের পরে `to` বসে না।\r\n\r\n    - **উদাহরণ:** You **must respect** your parents. (Correct)\r\n    - **উদাহরণ:** You **must to respect** your parents. (Incorrect)\r\n\r\n2.  **Semi-Modal / Quasi-Modal Verbs:** কিছু verb আছে যারা modal verb-এর মতো আচরণ করে কিন্তু তাদের গঠন কিছুটা ভিন্ন। `ought to`, `used to`, `need to`, `have to` ইত্যাদি হলো Semi-Modal।\r\n    - **`ought to`**: এটি `should`-এর মতো নৈতিক বাধ্যবাধকতা (moral obligation) বা উপদেশ (advice) বোঝাতে ব্যবহৃত হয়। এর গঠন হলো `ought + to + V1`।\r\n\r\n#### **বিশ্লেষণ:**\r\n\r\n- **(a) ought**: `ought`-এর পরে `to` বসে। \"Children **ought to** respect their parents\" বাক্যটি একটি সম্পূর্ণ এবং অর্থপূর্ণ বাক্য যা নৈতিক দায়িত্ব বোঝাচ্ছে।\r\n- **(b) need**: `need` যদি modal হিসেবে ব্যবহৃত হয়, তবে এর পরে `to` বসে না (e.g., You need not go)। যদি main verb হিসেবে ব্যবহৃত হয়, তবে `need to` হয়, কিন্তু এখানে শুধু `need` অপশনে আছে যা `to` এর আগে বসলে বাক্যটি অসম্পূর্ণ থাকে।\r\n- **(c) must**: `must`-এর পরে সরাসরি verb-এর base form বসে, `to` বসে না।\r\n- **(d) present participle**: এটি একটি verb form (e.g., `respecting`), কোনো modal verb নয় যা শূন্যস্থানে বসতে পারে।\r\n\r\n<Callout type=\"info\">\r\n  ** মনে রাখবেন:** শূন্যস্থানের পরে যদি `to` থাকে, তাহলে `ought`, `used`,\r\n  `have`, `has`, `had` ইত্যাদি verb বসার সম্ভাবনা বেশি। `ought to` এবং `should`\r\n  প্রায় একই অর্থ প্রকাশ করলেও, `ought to` বেশি ফরমাল এবং নৈতিকতার ওপর জোর দেয়।\r\n</Callout>\r\n\r\nএই প্রশ্নটি modal এবং semi-modal verb-এর মধ্যে কাঠামোগত পার্থক্য যাচাই করে, যা ইংরেজি গ্রামারের একটি মৌলিক নিয়ম।";
