export const sourceCode = "import { Callout } from \"nextra/components\";\r\n\r\n# Participle Phrase (Reduced Relative Clause)\r\n\r\n**প্রশ্ন:** The bridge \\_\\_\\_\\_ recently for the railroad has been found faulty.\r\n\r\n(a) was constructed\r\n(b) constructed\r\n(c) has constructed\r\n(d) has been constructed\r\n\r\n**সঠিক উত্তর:** (b) constructed\r\n\r\n---\r\n\r\n### **ব্যাখ্যা**\r\n\r\nএই বাক্যটিতে একটি Reduced Relative Clause ব্যবহার করা হয়েছে। শূন্যস্থানে `constructed` শব্দটি বসিয়ে একটি Participle Phrase তৈরি করা হয়েছে যা `The bridge` Noun-টিকে Modify করছে।\r\n\r\n#### **Key Concepts:**\r\n\r\n1.  **Main Clause:** বাক্যের মূল অংশটি হলো \"The bridge ... has been found faulty.\" এখানে `The bridge` হলো Subject এবং `has been found` হলো Verb।\r\n2.  **Relative Clause:** একটি Noun সম্পর্কে অতিরিক্ত তথ্য দেওয়ার জন্য যে Clause ব্যবহৃত হয়।\r\n    - **Full Clause:** The bridge **which was constructed** recently for the railroad has been found faulty.\r\n3.  **Reduced Relative Clause:** Relative Clause-কে সংক্ষিপ্ত করার নিয়ম আছে।\r\n    - **Passive Voice-এ:** Relative pronoun (`which`, `that`, `who`) এবং `be` verb (`is`, `am`, `are`, `was`, `were`) حذف করে শুধুমাত্র Past Participle (V3) ব্যবহার করা হয়।\r\n\r\n#### **বিশ্লেষণ:**\r\n\r\n- **Full Relative Clause:** `which was constructed`\r\n- **Reduction Process:**\r\n  1.  `which` (relative pronoun) বাদ দিন।\r\n  2.  `was` (`be` verb) বাদ দিন।\r\n  3.  শুধু `constructed` (Past Participle) থেকে যায়।\r\n\r\nএই সংক্ষিপ্ত রূপটি (reduced form) হলো `constructed`। তাই বাক্যটি হবে:\r\n\"The bridge **constructed** recently for the railroad has been found faulty.\"\r\n\r\nএখানে `constructed recently for the railroad` অংশটি একটি Participle Phrase যা Adjective-এর মতো কাজ করে `The bridge`-কে বর্ণনা করছে।\r\n\r\n#### **কেন অন্য অপশনগুলো ভুল?**\r\n\r\n- **(a) was constructed**, **(c) has constructed**, **(d) has been constructed**: এই অপশনগুলো বসালে বাক্যে দুটি Finite Verb হয়ে যায় (`was constructed` এবং `has been found`), যা একটি Conjunction ছাড়া একটি Simple Sentence-এ সম্ভব নয়। এটি বাক্যটিকে grammatically incorrect করে তোলে।\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Identify:** যদি একটি Noun-এর পরে শূন্যস্থান থাকে এবং তারপরে বাক্যের\r\n  বাকি অংশে একটি Main Verb (এখানে `has been found`) থাকে, তাহলে শূন্যস্থানে\r\n  প্রায়ই একটি Participle (V3 for passive, V-ing for active) বসে একটি Reduced\r\n  Clause তৈরি হয়।\r\n</Callout>\r\n\r\nএই নিয়মটি ইংরেজি গ্রামারের একটি advanced topic এবং প্রতিযোগিতামূলক পরীক্ষাগুলোতে প্রায়ই এর ব্যবহার দেখা যায়।";
